
/*subscribe*/

.form-subscribe {
    display: block;
    position: relative;
    margin: 0;
    width: 100%;
    flex-shrink: 0;

    input {
        display: block;
        padding: 19px 55px 16px 19px;
        border: 1px solid $color-white;
        text-transform: uppercase;
        color: $color-green;
        background: none;
        border-radius: 40px;
        height: 64px;
        width: 100%;
        font: 400 18px/28px $ff-pp-fragment-sans;

        @include placeholder {
            color: rgba($color-white, .4);
            text-transform: none;
        }

        @include media(">=desktop") {/*1200*/
            padding: 18px 50px 18px 23px;
            height: 56px;
            font-size: 16px;
            line-height: 1.62;
        }

        @include media(">=large-desktop") { /*1800*/
            height: 64px;
            font-size: 18px;
            line-height: 1.56;
            padding-left: 30px;
        }

        &:-webkit-autofill,
        &:-webkit-autofill:hover,
        &:-webkit-autofill:focus,
        &:-webkit-autofill:active {
            color: $color-white;
            -webkit-text-fill-color: $color-white;
            -webkit-box-shadow: 0 0 0 50px $color-dark inset;

        }

        &:active,
        &:focus,
        .no-touch &:hover {
            border-color: $color-white;
        }
    }

    .wpcf7-not-valid-tip {
        margin-top: 5px;
    }

    .form-subscribe-holder {
        position: relative;
    }

    .btn-arrow {
        margin: 0;
        width: 32px;
        height: 32px;
        min-height: 32px;
        position: absolute;
        top: 15px;
        right: 20px;
        padding: 0;

        @include media(">=desktop") {/*1200*/
            width: 28px;
            height: 28px;
            min-height: 28px;
            right: 24px;
            top: 13px;
        }

        @include media(">=medium-desktop") {/*1200*/
            top: 17px;
        }

        @include media(">=large-desktop") { /*1800*/
            width: 32px;
            height: 32px;
            min-height: 32px;
            right: 24px;
            top: 15px;
        }

        span {
            padding: 5px;
            border-color: $color-white;
        }

        .no-touch &:hover {
            span {
                background-color: $color-white;

                path {
                    stroke: $color-dark;
                }
            }
        }
    }
}

/*subscribe end*/