.rotating-nuts-section {
    // overflow: hidden;

    .section-info {
        max-width: 576px;
        position: relative;
        z-index: 7;
        transform: translateZ(1px);

        @include media('>=desktop') {
            padding-top: 7px;
        }

        @include media('>=large-desktop') {
            max-width: 720px;
        }

        p {
            font-size: 16px;
            line-height: 26px;

            @include media('>=large-desktop') {
                font-size: 18px;
                line-height: 29px;
            }
        }

        h2 {
            margin-bottom: 12px;

            @include media('>=desktop') {
                margin-bottom: 18px;
            }
        }
    }

    .media-slider-decor-holder {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transform: translate3d(0,0,1px);

        &.duplicate {
            z-index: 4;
        }

        img {
            object-fit: contain;
            width: auto;
            height: auto;
            max-width: 100%;
            max-height: 100%
        }

        a {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
        }
    }

    .media-slider-item {
        height: auto;
    }

    .buttons-holder {
        position: relative;
        z-index: 10;
    }

    .container {

        @include media('>=large-desktop') {
            overflow: hidden;
        }

        &.media-slider-text {
            margin-top: 39px;
            position: relative;
            z-index: 10;
            transform: translateZ(1px);

            @include media('>=desktop') {
                margin-top: 62px;
            }

            @include media('>=large-desktop') {
                margin-top: 80px;
            }
        }
    }
}

#nut-intro-animation-box {
    @include media('>=desktop') {
        opacity: 1;
        transition: opacity 1.8s ease 1s;
    }
}

.origin-animation-wrap {
    transition: opacity .35s ease .23s;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    img {
        max-width: none;
        object-fit: contain;
    }

    .slick-current & {
        animation: nutAnimFirts .6s ease-out .15s forwards;
    }
}

.sliders-container {
    width: calc(100% + 40px);
    margin: 39px -20px 0;
    position: relative;

    @include media('>=desktop') {
        width: calc(100% + 160px);
        margin: 45px -80px 0;
    }

    @include media('>=medium-desktop') {
        width: calc(100% + 200px);
        margin: 137px -100px 0;
    }

    @include media('>=large-desktop') {
        width: calc(100% + 344px);
        margin: 120px -172px 0;
    }

    &.show-sliders {

        .home-media-slider {
            @include media('>=desktop') {
                opacity: 1;
            }
        }

        .home-media-nav {
            @include media('>=desktop') {
                opacity: 1;
                transform: translate3d(0, -50%, 1px);
            }
        }

        #nut-intro-animation-box {
            @include media('>=desktop') {
                opacity: 0;
            }
        }

        .origin-image-wrap {
            img {
                @include media('>=desktop') {
                    visibility: visible;
                    opacity: 1;
                }
            }

            .origin-image-bg {
                @include media('>=desktop') {
                    opacity: 1;
                }
            }
        }
    }
}

.home-media-slider {
    // position: absolute;
    width: 100%;
    // height: 100%;
    z-index: 5;
    // left: 0;
    // bottom: 0;
    max-width: 400px;
    margin: 0 auto;
    transition: opacity .8s linear;

    @include media('>=tablet') {
        margin: 0;
        max-width: 100%;
    }

    @include media('>=desktop') {
        opacity: 0;
    }

    .media-slider-img-holder {
        position: relative;

        &::after {
            content: '';
            display: block;
            padding-top: 92%;

            @include media('>=tablet') {
                padding-top: 40%;
            }

            @include media('>=desktop') {
                padding-top: 45%;
            }

            @include media('>=large-desktop') {
                padding-top: 39.8%;
            }
        }
    }

    .slick-list, .slick-track,
    .slick-slide, .slick-slide > div,
    .media-slider-item, .media-slider-img-holder {
        height: auto;
    }

    .slick-list {
        overflow: visible;
    }

    .slick-current {
        opacity: 1 !important;
    }
}

.origin-img-holder {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    transform: translate3d(0,0,1px);

    .slick-current & {
        animation: imgHolderAnimation 1.2s;
        animation-timing-function: ease;
    }
}

.changed-slide {
    .slick-current {
        .nut {
            animation: nutAnim 2.3s linear 1.2s infinite;
        }
    }
}

.media-slider-item {

    img {
        position: absolute;
        bottom: 0;
        right: 50%;
        opacity: 0;
        transition: all 1s;
        transform: translate3d(-50%, 0, 1px);
        margin: 0;

        .slick-current & {
            opacity: 1;
        }

        &.nut {
            transform: translate3d(48.3%, 4.9%, 1px);
            // height: 103%;
            height: 95%;
            max-height: none;
            z-index: 1;
            visibility: visible;
            transition: opacity .4s linear, transform 1s linear;

            @include media('>=tablet') {
                height: 99%;
            }
        }
    }

    .media-slider-decor-holder {
        position: absolute;

        .slick-current & {
            animation: leftSideAnimation 1.2s;
            animation-timing-function: ease;
        }
    }

    &.butter {

        .media-slider-decor-holder {

            img:not(.nut) {

                &:nth-of-type(1) {
                    bottom: 0;
                    transform: translate3d(53.4%,-15%, 1px);
                    max-width: 908px;
                    z-index: 3;
                    width: 107.5%;
                    right: 48%;

                    @include media('>=tablet') {
                        width: 47.5vw;
                        right: 50%;
                    }

                    @include media('>=desktop') {
                        right: 52%;
                    }

                    @include media('>=large-desktop') {
                        right: 50%;
                    }

                    .slick-current & {
                        animation: butterImg1 2.3s linear 1.2s infinite;
                    }
                }

                &:nth-of-type(2) {
