.home-media-nav-holder {
    position: absolute;
    top: 0;
    left: 50%;
    width: 100%;
    max-width: 400px;
    z-index: 5;
    transform: translateX(-50%);

    @include media('>=tablet') {
        left: 0;
        max-width: 100%;
        transform: translateX(0);
    }

    &::after {
        content: '';
        display: block;
        padding-top: 92%;

        @include media('>=tablet') {
            padding-top: 40%;
        }

        @include media('>=desktop') {
            padding-top: 45%;
        }

        @include media('>=large-desktop') {
            padding-top: 39.8%;
        }
    }
}

.home-media-nav {
    padding: 0 30px;
    position: absolute;
    z-index: 10;
    width: 100%;
    left: 0;
    top: 50%;
    transform: translate3d(0,-50%, 1px);

    @include media('>=tablet') {
        padding: 0;
    }

    @include media('>=desktop') {
        opacity: 0;
        transform: translate3d(190px, -50%, 1px);
        transition: opacity .8s ease, transform 1.2s ease, .2s;
    }

    .slick-list {
        overflow: visible;

        @include media('>=desktop') {
            transform: scale(.97);
        }

        @include media('>=medium-desktop') {
            transform: scale(.97);
        }

        @include media('>=large-desktop') {
            transform: scale(.94);
        }
    }

    .slick-track {
        flex-direction: row;
    }

    .slick-current {

        & + .slick-slide {
            p {
                @include media('<tablet') {
                    transform: scale(.5) translateX(-50%);
                }
            }
        }
    }

    .slick-slide {
        height: inherit;
        margin: 0 20px;

        @include media('>=mobile-landscape') {
            margin: 0 45px;
        }

        @include media('>=tablet') {
            margin: 0 5px;
        }

        @include media('>=desktop') {
            margin: 0 35px;
        }

        @include media('>=medium-desktop') {
            margin: 0 40px;
        }

        p:not(.active-title) {

            @include media('>=desktop') {
                transform: translateX(-50%);
                position: relative;
                z-index: 4;
            }
        }

        &.slick-current {

            p:not(.active-title) {

                @include media('>=desktop') {
                    transform: translateX(0);
                }
            }

            & + div {
                p:not(.active-title) {

                    @include media('>=desktop') {
                        transform: translateX(90%);
                    }
                }

                & + div {
                    p:not(.active-title) {

                        @include media('>=desktop') {
                            transform: translateX(50%);
                        }
                    }

                    & + div {
                        p:not(.active-title) {

                            @include media('>=desktop') {
                                transform: translateX(50%);
                            }
                        }
                    }
                }
            }
        }

        &.prev-current {
            p:not(.active-title) {

                @include media('>=desktop') {
                    transform: translateX(-90%);
                }
            }
        }

        & > div {
            height: 100%;
        }
    }
}

.media-nav-item {
    height: 100%;
    position: relative;
    min-width: 140px;

    @include media('>=desktop') {
        min-height: 160px;
    }

    @include media('>=medium-desktop') {
        min-height: 180px;
    }

    @include media('>=large-desktop') {
        min-height: 240px;
    }

    p {
        color: rgba($color-dark, .4);
        font-weight: 800;
        font-size: 60px;
        line-height: 60px;
        height: inherit;
        transform: scale(.5) translateX(50%);
        transition: all 1s ease;
        cursor: pointer;
        text-align: center;
        font-family: $ff-pp-fragment-serif;

        @include media('>=tablet') {
            transform: scale(.5) translateX(0);
            text-align: center;
        }

        @include media('>=desktop') {
            font-size: 32px;
            line-height: 32px;
            transform: scale(1) translateX(0);
            transition: all .5s linear;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        @include media('>=large-desktop') {
            font-size: 48px;
            line-height: 48px;
        }

        &.active-title {
            display: none;
            @include media('>=desktop') {
                font-size: 160px;
                line-height: 160px;
                opacity: 0;
                display: inline-block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                visibility: hidden;
            }

            @include media('>=medium-desktop') {
                font-size: 180px;
                line-height: 180px;
            }

            @include media('>=large-desktop') {
                font-size: 240px;
                line-height: 240px;
            }

            .slick-current & {
                @include media('>=desktop') {
                    opacity: 1;
                    color: $color-dark;
                    visibility: visible;
                    transform: translate(-50%, -50%);
                }
            }
        }

        .slick-current & {
            color: $color-dark;
            text-align: center;
            transform: scale(1) translateX(0);

            @include media('>=tablet') {
                transform: scale(1) translateX(0);
            }

            @include media('>=desktop') {
                transform: scale(1) translateX(0);
                color: rgba($color-dark, 0);
            }

            // @include media('>=medium-desktop') {
            //     transform: scale(1) translateX(0);
            // }

            // @include media('>=large-desktop') {
            //     transform: scale(1) translateX(0);
            // }
        }
    }
}

.origin-image-wrap {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 400px;

    @include media('>=tablet') {
        left: 0;
        max-width: 100%;
        transform: translateX(0);
    }

    &::after {
        content: '';
        display: block;
        padding-top: 92%;

        @include media('>=tablet') {
            padding-top: 40%;
        }

        @include media('>=desktop') {
            padding-top: 45%;
        }

        @include media('>=large-desktop') {
            padding-top: 39.8%;
        }
    }

    img, video, canvas {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 50%;
        left: 50%;
        object-fit: contain;
        z-index: 2;
        transform: translate3d(-50%, -50%, 1px);

        &.origin-image-bg {
            height: calc(100% - 52px);
            z-index: 1;
            transition: opacity 1s ease;

            @include media('>=desktop') {
                opacity: 0;
            }
        }
    }

    canvas {
        transform: translate3d(-49.7%,-40%,1px) scale(2.17) rotate(0deg);
        visibility: hidden;

        @include media('>=desktop') {
            visibility: visible;
            transform: translate3d(-49.4%,-40%,1px) scale(2.16) rotate(0deg);
        }

        @include media('>=large-desktop') {
            transform: translate3d(-49.7%,-40%,1px) scale(2.17) rotate(0deg);
        }
    }
}