
.main {
  flex-shrink: 0;
  flex-grow: 1;
  // overflow: hidden;
  padding-top: 116px;

  @include media(">=desktop"){/*1200*/
    padding-top: 124px;
  }

  @include media(">=large-desktop"){/*1800*/
    padding-top: 126px;
  }
  
}

.wrapper {
  position: relative;
  overflow: clip;
  width: 100%;
  @include flex(column);
  flex-shrink: 0;
  flex-grow: 1;
  min-height: 100vh;
  height: 100%;
  overflow-x: hidden;
}

.header,
.footer {
  flex-shrink: 0;
}

.container {
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  flex-shrink: 0;

  @include media('>=desktop') { /*1200*/
      padding-left: 80px;
      padding-right: 80px;
  }

  @include media('>=medium-desktop') { /*1440*/
      padding-left: 100px;
      padding-right: 100px;
  }

  @include media('>=large-desktop') { /*1800*/
      padding-left: 172px;
      padding-right: 172px;
      max-width: 1920px;
  }
}

.indent-top {
  margin-top: $indent-section-mobile;

  @include media('>=desktop') { /*1200*/
    margin-top: $indent-section-desktop;
  }

  @include media('>=medium-desktop') { /*1440*/
    margin-top: $indent-section-medium-desktop;
  }

  @include media('>=large-desktop') { /*1800*/
    margin-top: $indent-section-large-desktop;
  }
}

.indent-bottom {
  margin-bottom: $indent-section-mobile;

  @include media('>=desktop') { /*1200*/
      margin-bottom: $indent-section-desktop;
  }

  @include media('>=medium-desktop') { /*1440*/
    margin-bottom: $indent-section-medium-desktop;
  }

  @include media('>=large-desktop') { /*1800*/
      margin-bottom: $indent-section-large-desktop;
  }
}

.indent-top-medium {
  margin-top: $indent-section-mobile/2;

  @include media('>=desktop') { /*1200*/
    margin-top: $indent-section-mobile;
  }

  @include media('>=medium-desktop') { /*1440*/
    margin-top: $indent-section-desktop;
  }

  @include media('>=large-desktop') { /*1800*/
    margin-top: $indent-section-medium-desktop;
  }
}

.indent-bottom-medium {
  margin-bottom: $indent-section-mobile/2;

  @include media('>=desktop') { /*1200*/
      margin-bottom: $indent-section-mobile;
  }

  @include media('>=medium-desktop') { /*1440*/
    margin-bottom: $indent-section-desktop;
  }

  @include media('>=large-desktop') { /*1800*/
      margin-bottom: $indent-section-medium-desktop;
  }
}

.section-info {
  max-width: 732px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;

  &.medium{
    @include media('>=desktop') { /*1200*/
      max-width: 876px;
    }
  }

  &.large {
    @include media('>=desktop') { /*1200*/
      max-width: 1114px;
    }
  }

  h2, h3, h4 {
    margin-bottom: 19px;
  }

  p {
		margin-bottom: 10px;
	}

	> *:last-child:not(.buttons-holder) {
		margin-bottom: 0;
	}
}

.round-card-list {
  margin-top: 30px;
  margin-bottom: 30px;

  @include media('>=mobile-landscape') {
    display: flex;
    flex-direction: row;
    column-gap: 30px;
    flex-wrap: wrap;
  }

  @include media('>=tablet') {
    justify-content: space-around;
  }

  @include media('>=desktop') {
    margin-top: 70px;
  }

  @include media('>=large-desktop') {
    margin-top: 97px;
  }
}

.round-card-slider {
  margin-top: 50px;
  width: calc(100% + 20px);
  margin-right: -20px;

  @include media('>=desktop') {
    width: calc(100% + 80px);
    margin-right: -80px;
  }

  @include media('>=medium-desktop') {
    width: calc(100% + 100px);
    margin-right: -100px;
  }

  @include media('>=large-desktop') {
    margin-top: 76px;
    width: calc(100% + 172px);
    margin-right: -172px;
  }

  .slick-list {
    overflow: visible;
  }

  .slick-slide {
    margin: 0 10px;

    &:first-child {
      @include media('>=desktop') {
        margin-left: 10px;
      }
    }

    @include media('>=desktop') {
      margin: 0 24px;
    }
  }

  .round-card-media {
    @include media('>=large-desktop') {
      margin-bottom: 46px;
    }
  }

  .round-card-info {
    @include media('>=tablet') {
      padding: 0 10px;
    }

    h4 {
      @include media('>=tablet') {
        margin-bottom: 15px;
      }
    }
  }
}

.green-divider {
  background-color: $color-green;
  padding: 24px 0;
  position: relative;

  &.sale {
    padding-left: 36px;
    padding-right: 36px;

    p {
      font-size: 14px;
      line-height: 24px;
      font-weight: 300;

      @include media(">=tablet"){/*768*/
        font-size: 18px;
        line-height: 28px;
      }
    }

    b, strong {
      font-weight: 400;
    }

  }

  p {
    color: $color-white;
    text-align: center;
    letter-spacing: .4px;

    @include media('>=large-desktop') {
      font-size: 24px;
      line-height: 34px;
    }
  }

  .close-btn {
    position: absolute;
    top: 15px;
    right: 15px;
    padding: 0;
    background: none;
    border: 0;
    margin: 0;
    width: 16px;
    height: 16px;
    cursor: pointer;

    @include media(">=tablet"){/*768*/
      top: 30px;
      right: 32px;
    }

    svg {
      width: 16px;
      height: 16px;

      path {
        stroke: $color-white;
        transition: all $transition-duration linear;
      }
    }

    .no-touch &:hover {
      path {
        opacity: 1;
      }
    }
  }
}

.has-dark-overlay {
  &::after {
      content: '';
      top: 0;
      left: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba($color-dark, 0.4);
      z-index: 0;
      // transform: translateZ(1px);
  }

  & + .container {
      h1, .h1, .h2, p, span {
          color: $color-white;
      }
  }

  h1, .h1, .h2, .h3, h3, .h4, h4, .h5, h5, p, span {
    color: $color-white;
  }
}

.has-white-overlay {
  &::after {
      content: '';
      top: 0;
      left: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba($color-white, .4);
      z-index: 0;
      // transform: translateZ(1px);
  }
}

.animation {

  .hero-info, .section-info:not(.static) {

    h1, h2, h3, p, .buttons-holder, .dark-bullet {
      transition: transform 1s ease, opacity .4s linear;
      transform: translateY(150px);
      opacity: 0;
    }

    p {
      transform: translateY(170px);
    }

    .dark-bullet {
      transform: translateY(180px);
    }

    .buttons-holder {
      transform: translateY(190px);
    }
  }

  .more-items-heading {
    h2, h3, p, .buttons-holder, .dark-bullet {
      transition: transform 1s ease, opacity .4s linear;
      transform: translateY(100px);
      opacity: 0;
    }
  }

  .left-side {

    .nav-list {
      opacity: 0;
      transform: translateY(180px);
      transition: transform 1s ease, opacity .4s linear;
    }

    & > .buttons-holder {
      opacity: 0;
      transform: translateY(190px);
      transition: transform 1s ease, opacity .4s linear;
    }
  }

  .news-grid-slider {

    .news-card {
      transition: transform 1s ease, opacity .4s linear;
      transform: translateY(100px);
      opacity: 0;

      &:nth-child(1) {
        transform: translateY(140px);
      }

      &:nth-child(3) {
        transform: translateY(120px);
      }
    }
  }

  &.animated {

    .hero-info, .section-info, .more-items-heading {

      h1, h2, h3, p, .buttons-holder, .dark-bullet {
        transform: translateY(0);
        opacity: 1;
      }
    }

    .left-side {

      & > .buttons-holder, .nav-list {
        transform: translateY(0);
        opacity: 1;
      }
    }

    .news-grid-slider {
      .news-card {
        transform: translateY(0);
        opacity: 1;
      }
    }
  }
}