.header {
    z-index: 50;
    width: 100%;
    position: absolute;
    // transform: translateZ(1px);

    .show-menu & {
        @include media("<desktop") {
            position: fixed;
        }
    }

    .container {
        @include media(">=tablet") {/*768*/
            padding-left: 24px;
            padding-right: 24px;
        }

        @include media(">=medium-desktop") {/*1440*/
            padding-left: 40px;
            padding-right: 40px;
        }

        @include media(">=large-desktop") {/*1800*/
            padding-left: 48px;
            padding-right: 48px;
        }
				@include media('>=1921px') {
						max-width: 100%;
				}
    }

    .cart-link {
        position: fixed;
        top: 10px;
        left: 18px;
        z-index: 2;
        height: 18px;
        line-height: 1;
        cursor: pointer;

        @include media(">=desktop") {/*1200*/
            top: auto;
            left: auto;
            position: relative;
            height: auto;
            line-height: inherit;
        }

        span:not(.icon) {
            @include media("<desktop") {
                display: none;
            }
        }

        .icon {
            position: relative;
            @include media("<desktop") {
                margin: 0;
            }

            > span {
                position: absolute;
                top: 4px;
                left: 10px;
                width: 2px;
                height: 2px;
                opacity: 0;
                background: $color-green;
                border-radius: 50%;
                transition: opacity $transition-duration linear;
                display: block;

                @include media(">=desktop") {
                    top: 6.5px;
                    left: 7px;
                }

            }

            &.no-empty {
                span {
                    opacity: 1;
                }
            }
        }

        svg {
            @include media(">=desktop") {
                height: 14px;
                width: 14px;
            }
        }
    }

    .back {
        font-size: 16px;
        line-height: 1.62;
        letter-spacing: 1px;
        text-transform: uppercase;
        opacity: 0;
        pointer-events: none;
        margin: 0 0 19px;
        border: 0;
        background: none;
        padding: 0;
        transition: opacity $transition-duration linear;
        color: $color-dark;

        &.active {
            opacity: 1;
            pointer-events: all;
            cursor: pointer;
        }

        @include media(">=desktop") {/*1200*/
            display: none;
        }

        svg {
            margin: 0 12px -1px 0;
        }
    }

    .search-icon {
        @include media(">=desktop") {/*1200*/
            width: 15px;
            height: 15px;
        }
    }

    .cart-icon {
        @include media(">=desktop") {/*1200*/
            width: 15px;
            height: 15px;
        }
    }

    .lock-icon {
        @include media(">=desktop") {/*1200*/
            width: 12px;
            height: 14px;
        }
    }

    .contact-icon {
        @include media(">=desktop") {/*1200*/
            display: none;
        }
    }
}

.header-holder {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    // padding: 79px 0 50px;
    padding: 79px 0 55px;
    position: fixed;
    top: 47px;
    width: 100%;
    opacity: 0;
    transition: height $transition-duration linear, opacity $transition-duration linear, transform .6s ease;
    height: 0;
    pointer-events: none;

    @include media(">=desktop") {/*1200*/
        height: auto;
        overflow: visible;
        padding: 0;
        // position: relative;
        top: auto;
        pointer-events: all;
        opacity: 1;
    }

    .scrolled & {
        @include media('>=desktop') {
            transform: translateY(-101%);
        }
    }

    &:before {
        @include media("<desktop") {
            position: fixed;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            content: "";
            background: $color-light-gray;
        }
    }

    .show-menu & {
        @include media("<desktop") {
            pointer-events: all;
            height: calc(100vh - 47px);
            opacity: 1;
        }
    }
}

.header-logo-row {
    height: 47px;
    display: flex;
    background: $color-dark;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: none;
    z-index: 2;

    @include media(">=desktop") { /*1200*/
        position: absolute;
        top: 9px;
        left: 50%;
        right: auto;
        transform: translateX(-50%);
        height: auto;
        background: none;
        z-index: auto;
    }

    &:before {
        position: absolute;
        top: 0;
        left: -9999px;
        right: -9999px;
        height: 46px;
        background: $color-dark;
        content: "";

        @include media(">=desktop"){/*1200*/
            height: 49px;
        }
    }

    .btn-menu {
        position: absolute;
        top: 50%;
        right: 22px;
        transform: translateY(-50%);
        z-index: 2;
        transition: all $transition-duration linear;

        @include media(">=desktop") { /*1200*/
            display: none;
        }

        .no-touch &:hover {
            cursor: pointer;
        }

        .show-menu & {
            transform: translateY(-50%);

            &:before {
                transform: rotate(-45deg) translate(0, 11px);
            }

            &:after {
                transform: rotate(45deg) translate(4px, -7px);
            }

        }
    }

    .logo {
        width: 185px;
        text-align: center;
        padding: 11px 0 0;

        &:before {
            top: 0;
            width: 360px;
            height: 116px;
            background-image: url("../images/logo-bg-black.svg");

            @include media(">=desktop") { /*1200*/
                top: 2px;
            }

            @media screen and (min-width: 1200px) and (max-width: 1439px) {
                width: 300px;
            }
        }

    }
}

.header-top {
    order: 1;

    @include media(">=desktop") {
        order: 0;
        padding: 18px 0 15px;
        position: relative;
        background: $color-dark;
        z-index: 2;
    }

    .container {
        @include media(">=desktop") {
            display: flex;
        }
    }
}

.header-add-links {
    font-weight: 400;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: $color-dark;
    align-items: center;
    font-size: 16px;
    line-height: 1.62;
    margin: 4px 0 0;

    @include media(">=desktop") { /*1200*/
        display: flex;
        flex-wrap: wrap;
        color: $color-white;
        margin: 0 0 0 auto;
        font-size: 11px;
        line-height: 1.82;
    }

    @include media(">=large-desktop"){/*1800*/
        font-size: 12px;
        line-height: 1.83;
    }

    li + li {
        margin: 12px 0 0;

        @include media(">=desktop") { /*1200*/
            padding: 0 0 0 12px;
            margin: 0 0 0 12px;
            position: relative;
        }

        @include media(">=large-desktop"){/*1800*/
            padding: 0 0 0 16px;
            margin: 0 0 0 16px;
        }

        &:before {
            @include media(">=desktop") { /*1200*/
                height: 100%;
                width: 1px;
                background: rgba($color-white, .1);
                content: "";
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }

    li {
        &.current-menu-item {
            a {
                &:before {
                    transform: translateX(0);
                }
            }
        }
    }

    a {
        display: inline-flex;
        color: currentColor;
        position: relative;
        align-items: center;
        overflow: hidden;

        &:before {
            position: absolute;
            top: 96%;
            height: 1px;
            left: 0;
            width: 100%;
            background: currentColor;
            content: "";
            transition: transform $transition-duration/1.5 ease;
            // transform-origin: bottom left;
            // transform: scaleX(0);
            transform: translateX(-100%);
        }

        .no-touch &:hover {
            &:before {
                @include media(">=tablet-landscape") {/*1024*/
                    transform: translateX(0);
                }
            }
        }
    }

    .icon {
        width: 20px;
        margin: 2px 10px -4px 0;
        text-align: center;

        @include media(">=desktop") {/*1200*/
            max-width: 12px;
            width: auto;
            text-align: left;
            margin: 2px 11px -2px 0;
        }

        path {
            stroke: currentColor;
        }
    }
}

.header-bottom {
    position: relative;

    &.animation {
        @include media('>=desktop') {
            transition: transform 1s ease;
            transform: translateY(-65px);
        }

        &.animated {
            @include media('>=desktop') {
                transform: translateY(0);
            }
        }
    }

    & > .container {
        transition: background-color .5s ease;
    }

    .has-layer & {
        & > .container {
            @include media('>=desktop') {
                background-color: $color-light-gray;
                padding-bottom: 20px;
            }

            @include media('>=large-desktop') {/*1800*/
                padding-bottom: 19px;
            }
        }

        .header-nav {
            & > ul {
                & > li {
                    & > .header-nav-link-view {
                        @include media('>=desktop') {
                            color: currentColor;
                        }
                    }
                }
            }
        }
    }
}

.header-nav {

    @include media(">=desktop") {/*1200*/
        padding: 24px 0 0;
        display: flex;
    }

    @media screen and (min-width: 1200px) and (max-width: 1439px) {
        padding: 24px 0 0;
    }

    @include media(">=large-desktop") {/*1800*/
        padding: 22px 0 0;
    }

    > ul {
        overflow: hidden;
        @include media(">=desktop") {/*1200*/
            width: 46%;
            display: flex;
            flex-wrap: wrap;
            overflow: visible;
        }

        &:nth-child(2) {
            @include media(">=desktop") {/*1200*/
                margin: 0 0 0 auto;
                justify-content: flex-end;
            }

            > li {

                @include media(">=desktop") {/*1200*/
                    margin: 0 0 0 20px;
                }

                @media screen and (min-width: 1200px) and (max-width: 1439px) {
                    margin: 0 0 0 13px;
                }

                @include media(">=medium-desktop"){/*1440*/
                    margin: 0 0 0 24px;
                }

                @include media(">=large-desktop"){/*1800*/
                    margin: 0 0 0 30px;
                }
            }
        }

        > li {
            transition: opacity $transition-duration linear, height $transition-duration linear;

            &.hide {
                opacity: 0;
                height: 0;
                visibility: hidden;
                margin: 0;
            }

            & > .header-nav-link-view {
                transition: color .6s ease;
                // .show-drop-page & {
                //     @include media(">=desktop") {/*1200*/
                //         color: $color-dark !important;
                //     }
                // }

                .white & {
                    @include media(">=desktop") {/*1200*/
                        color: $color-white;
                    }
                }
            }
        }
    }

    li {
        margin: 0 0 20px;

        @include media(">=desktop") {/*1200*/
            margin: 0 20px 0 0;
        }

        @media screen and (min-width: 1200px) and (max-width: 1439px) {
            margin: 0 13px 0 0;
        }

        @include media(">=medium-desktop"){/*1440*/
            margin: 0 24px 0 0;
        }

        @include media(">=large-desktop"){/*1800*/
            margin: 0 32px 0 0;
        }

        &.active,
        &.show-drop,
        &.current-menu-item {
            > .header-nav-link-view {
                &:before {
                    transform-origin: bottom left;
                    transform: scaleX(1);
                }
            }
        }

        .header-nav-link-view {
            color: $color-dark;
            text-decoration: none;
            position: relative;
            font-weight: 400;
            font-size: 24px;
            line-height: 1.33;
            text-transform: uppercase;

            @include media(">=desktop") {/*1200*/
                font-size: 14px;
                line-height: 1.71;
                letter-spacing: 1px;
            }

            @media screen and (min-width: 1200px) and (max-width: 1439px) {
                font-size: 13px;
            }

            @include media(">=large-desktop"){/*1800*/
                font-size: 16px;
                line-height: 1.62;
            }

            // .white & {
            //     @include media(">=desktop") {/*1200*/
            //         color: $color-white;
            //     }
            // }

            &:is(a) {
                &:before {
                    position: absolute;
                    top: 100%;
                    height: 1px;
                    left: 0;
                    width: 100%;
                    background: currentColor;
                    content: "";
                    transition: transform $transition-duration/1.5 ease;
                    transform-origin: bottom left;
                    transform: scaleX(0);
                }

                .no-touch &:hover {
                    &:before {
                        transform: scaleX(1);
                    }
                }
            }

            &:is(span) {
                display: none;
                margin: 0 0 18px;
                padding: 0 0 16px;
                border-bottom: 1px solid rgba($color-creame, .4);

                @include media(">=desktop") { /*1200*/
                    display: inline-block;
                }
            }
        }

        .header-nav-second-list {
            padding: 2px 0 0;

            @include media(">=desktop"){/*1200*/
                padding: 0;
            }

            li {
                margin: 0 0 16px;

                @include media(">=desktop") {/*1200*/
                    margin: 0 0 10px;
                }
            }

            .header-nav-link-view {
                letter-spacing: 0;
                text-transform: none;
                color: #7E8284;
            }
        }
    }

    a {
        position: relative;
        z-index: 2;
    }
}

.header-drop-menu {
    overflow: hidden;
    height: 0;
    opacity: 0;
    transition: opacity $transition-duration linear;

    @include media(">=desktop") {/*1200*/
        height: 0;
        position: absolute;
        top: 0;
        opacity: 1;
        left: 0;
        right: 0;
        z-index: -1;
        transition: height $transition-duration linear;
    }

    .show-drop & {
        height: auto;
        opacity: 1;
        @include media(">=desktop") {/*1200*/
            height: 100vh;
        }
    }

    > .container {
        position: relative;
        @include media(">=desktop") { /*1200*/
            padding-top: 166px;
            padding-bottom: 79px;
        }

        @include media("<desktop") {
            padding-left: 0;
            padding-right: 0;
        }

        &:before {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            content: "";
            background: $color-light-gray;
            z-index: -1;
        }
    }

    .sub-menu {
        @include media(">=desktop") { /*1200*/
            display: flex;
            max-height: calc(100vh - 246px);
            overflow-y: auto;
            align-items: flex-start;
        }
    }

    .drop-text-col {
        display: none;

        @include media(">=desktop") { /*1200*/
            flex: 0 0 39.5%;
            padding: 2px 6% 0 0;
            display: block;
        }

        @include media(">=medium-desktop") { /*1440*/
            flex: 0 0 43.5%;
            padding: 2px 13% 0 0;
        }

        h3, .h3 {
            margin: 0 0 21px;
        }

        p {
            margin: 0 0 38px;
        }
    }

    .drop-nav-col {
        font-size: 24px;
        line-height: 1.33;
        font-weight: 400;

        @include media(">=desktop") { /*1200*/
            font-size: 16px;
            width: 23.5%;
            line-height: 1.62;
            padding: 0 5% 0 0;
        }

        > ul {
            margin-top: 10px;

            @include media(">=desktop") { /*1200*/
                margin-top: 0;
            }
        }

        + .drop-nav-col {
            margin-top: 14px;

            @include media(">=desktop") { /*1200*/
                margin-top: 0;
            }
        }

        > ul:not(.header-nav-second-list) {
            display: inline-block;
            li {
                margin: 0 0 20px;
                @include media(">=desktop") {/*1200*/
                    margin: 0 0 18px;
                    padding: 0 0 16px;
                    border-bottom: 1px solid rgba($color-creame, .4);
                    min-width: 236px;
                }
            }
        }

        .link {
            @include media("<desktop") {
                color: #7E8284;
                margin: 0 0 24px;
            }
        }
    }

    .btn {
        font-size: 12px;
        line-height: 1.83;
        height: 44px;

        span {
            padding: 11px 26px;
        }
    }

    .news-card {
        display: none;

        @include media(">=desktop") {/*1200*/
            margin: 0 0 0 auto;
            max-width: 494px;
            width: 40%;
            display: block;
        }

        h5, .h5 {
            margin: 0 0 8px;
            font-weight: 400;
            font-family: $ff-pp-fragment-sans;
        }

        p {
            margin: 0;
        }
				img {
					@include media('>=desktop') {
						width: 100%;
					}
				}
        .news-card-text {
            @include media(">=large-desktop") {
                padding: 26px 31px 22px;
            }

            & + a {
                position: absolute;
            }
        }
    }

    .news-card-picture {
        @include media('>=desktop') {
            overflow: hidden;
            position: relative;
        }

        img {

            @include media('>=desktop') {
                top: 50%;
                left: 50%;
                width: 100%;
                height: 100%;
                object-fit: cover;
                position: absolute;
                transform: translate3d(-50%, -50%, 1px);
            }
        }

        &::after {
            @include media('>=desktop') {
                content: '';
                display: block;
                padding-top: 48.5%;
            }
        }
    }

    &.has-news {

        .drop-text-col {
            @include media('>=desktop') {
                flex: 0 0 33.5%;
                padding: 2px 1% 0 0;
            }

            @include media('>=medium-desktop') {
                flex: 0 0 28.5%;
                padding: 2px 0 0 0;
            }
        }

        .news-card {
            @include media('>=desktop') {
                width: 30%;
            }

            @include media('>=medium-desktop') {
                width: 31%;
            }
        }
    }
}

.show-drop-page {
    .bg-drop {
        opacity: 1;
        pointer-events: all;
        visibility: visible;
    }

    .white {

        .header-nav {
            > ul {
                > li {
                    & > .header-nav-link-view {
                        @include media(">=desktop") {/*1200*/
                            color: $color-dark;
                        }
                    }
                }
            }
        }
    }
}

.bg-drop {
    visibility: hidden;
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    // z-index: 0;
    z-index: 20;
    background: rgba($color-dark, .5);
    opacity: 0;
    transition: all $transition-duration linear $transition-duration/2;
}

.touch {

    .scrolled {

        .header-holder {
            @include media('>=desktop') {
                transform: translateY(0);
            }
        }
    }
}