.modal-window {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateX(-100%);
    transition: opacity .3s linear .3s, transform .3s ease;

    &.show {
        opacity: 1;
        transform: translateX(0);
    }

    &.one-time {
        p {
            color: $color-dark;
            font-size: 16px;
            line-height: 24px;

            @include media('>=desktop') {
                font-size: 18px;
                line-height: 28px;
            }
        }

        .modal-text-card {
            @include media('>=large-desktop') {
                padding-bottom: 46px;
                margin-bottom: 52px;
            }

            &:last-child {
                padding-bottom: 0;
                margin-bottom: 0;
                border-bottom: none;
            }
        }

        .modal-img-holder {
            @include media('<tablet') {
                display: none;
            }
        }
    }

    &.nutrition {

        h5, .h5 {
            font-family: $ff-pp-fragment-sans;
            font-weight: 400;
            margin-bottom: 4px;
        }

        .modal-outer {
            max-width: 504px;
            width: calc(100% - 40px);
            overflow: hidden;
            padding: 20px 10px 20px 0;
            background-color: $color-beige;

            @include media('>=tablet') {
                padding: 45px 10px 20px 0;
            }
        }

        .modal-text {
            padding: 0 30px 0 20px;

            @include media('>=tablet') {
                padding: 5px 30px;
                width: 100%;
            }

            @include media('>=desktop') {
                padding: 7px 28px 25px 48px;
            }

            p {
                font-size: 16px;
                line-height: 26px;
            }

            .modal-text-card {
                p {
                    color: $color-dark;
                }
            }
        }

        .modal-text-card {
            padding-bottom: 14px;
            margin-bottom: 11px;

            &:first-child {
                padding-right: 0;
            }
        }

        .close-modal {
            @include media('<tablet') {
                top: 15px;
                right: 15px;
            }

            &::after, &::before {
                background-color: $color-dark;
            }
        }

        .modal-inner-wrap {
            @include media('<tablet') {
                max-height: calc(100vh - 150px);
            }

            @include media('>=tablet') {
                // overflow: auto;
                padding-right: 10px;
            }

            .jcf-scrollbar-vertical {
                right: 15px;

                @include media('>=tablet') {
                    right: 9px;
                }
            }

            .jcf-scrollable-wrapper {
                margin-top: 25px;

                @include media('>=tablet') {
                    margin-top: 0;
                }
            }
        }

        .modal-inner {
            @include media('<tablet') {
                max-height: calc(100vh - 150px);
            }
        }
    }

    &.cart {
        align-items: flex-start;
        justify-content: flex-end;
        z-index: 100;
        visibility: visible;
        pointer-events: none;
        transform: translate3d(0,0,1px);
        transition: opacity 1s ease 1s;
        // transition: opacity .1s ease;

        &.show {
            opacity: 1;
            // visibility: visible;
            pointer-events: all;
            transition: opacity .1s ease;

            .modal-outer {
                transform: translate3d(0,0,1px);
            }
        }

        .modal-inner {
            flex-direction: column;
            overflow: hidden;
            height: 100% !important;
        }

        .modal-inner-wrap {
            max-height: calc(100vh - 50px);
            height: 100vh;

            @include media('>=tablet') {
                padding: 41px 24px 41px 35px;
                // overflow-y: auto;
            }

            @include media('>=desktop') {
                padding: 41px 24px 13px 35px;
            }

            .jcf-scrollable-wrapper {
                margin-top: 15px;
                width: 100% !important;

                @include media('>=tablet') {
                    margin-top: 0;
                }
            }
        }

        &.ios-mob {

            .modal-inner-wrap {
                @include media('<tablet') {
                    max-height: calc(100vh - 150px);
                }
            }
        }

        .modal-outer {
            background-color: $color-beige;
            height: 100vh;
            padding: 25px 35px 25px 25px;
            max-width: 810px;
            overflow: hidden;
            transition: transform 1s cubic-bezier(0.16, 1.08, 0.38, 0.98);
            transform: translate3d(100%,0,1px);
        }

        .close-modal {
            @include media('<tablet') {
                top: 15px;
                right: 15px;
            }

            &::after, &::before {
                background-color: $color-dark;
            }
        }
    }

    .buttons-holder {
        margin-top: 12px;
    }

    .note {
        font-size: 12px;
        line-height: 22px;
        font-weight: 400;
        display: block;
    }

    .woocommerce-mini-cart__buttons {
        column-gap: 20px;
        margin-top: 20px;
        row-gap: 20px;

        @include media('>=tablet') {
            column-gap: 0;
            margin-left: 0;
        }

        @include media('>=desktop') {
            margin: 30px 0 0 0;
        }

        .button {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            .no-touch & {
                &:hover {
                    background-color: $color-green;
                }
            }

            @include media('>=mobile-landscape') {
                width: calc(50% - 20px);
            }

            @include media('>=tablet') {
                width: 100%
            }
        }
    }
}

[data-modal="reports"], [data-modal="one-time"] {
    .modal-inner-wrap {

        .jcf-scrollbar-vertical {
            top: auto;
            right: 15px;
            height: calc(100% - 40px) !important;

            @include media('<tablet') {
                width: 2px;
                right: 20px !important;
            }

            @include media('>=tablet') {
                height: calc(100% - 50px) !important;
            }

            @include media('>=desktop') {
                right: 30px;
            }

            .jcf-scrollbar-slider {
                height: 100% !important;
            }

            .jcf-scrollbar-handle {
                @include media('<tablet') {
                    width: 2px;
                }
            }
        }
    }

    .modal-text {

        @include media('<tablet') {
            padding: 20px 35px 20px 20px;
        }

        @include media('>=tablet') {
            height: auto;
        }
    }

    .modal-img-holder {
        @include media('>=tablet') {
            height: auto;
        }
    }

    .jcf-scrollable {
        @include media('>=tablet') {
            align-items: flex-start;
        }
    }
}

[data-modal="reports"] {

    .modal-text-card {
        margin-top: 20px;
        margin-bottom: 0;

        @include media('>=large-desktop') {
            margin-top: 37px;
        }

        &:first-child {
            margin-top: 0;
        }
    }
}

.modal-divider {
    display: block;
    height: 1px;
    background-color: rgba($color-dark, .1);
    margin: 10px 0;

    @include media('>=desktop') {
        margin: 16px 0 16px;
    }

    &.double {
        height: 2px;
    }
}

.close-modal {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 25px;
    right: 25px;
    background-color: rgba(0,0,0,0);
    border: none;
    cursor: pointer;
    z-index: 10;

    @include media('>=tablet') {
        top: 20px;
        right: 20px;
    }

    &::after, &::before {
        content: '';
        width: 20px;
        height: 1px;
        background-color: $color-dark;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%, -50%, 1px) rotate(-45deg);

        @include media('>=tablet') {
            background-color: $color-white;
        }
    }

    &::before {
        content: '';
        transform: translate3d(-50%, -50%, 1px) rotate(45deg);
    }
}

.modal-overlay {
    width: 100%;
    height: 100%;
    background-color: rgba($color-dark, .5);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.modal-outer {
    position: relative;
    z-index: 3;
    width: 100%;
    max-width: 1040px;
    padding: 0 15px;

    @include media('>=desktop') {
        padding: 0;
    }
    .modal-img-holder {
        @include media('<tablet') {
            display: none;
        }
    }
}

.modal-inner-wrap {
    max-height: calc(100vh - 100px);
    // overflow: auto;

    @include media('>=tablet') {
        // overflow: hidden;
    }

    .jcf-scrollable-wrapper {
        margin-top: 15px;

        @include media('>=tablet') {
            margin-top: 0;
        }
    }

    .jcf-scrollbar-vertical {
        right: -11px;
        z-index: 20;
        opacity: .3;

        @include media('>=tablet') {
            right: -30px;
        }
    }

    .jcf-scrollbar-horizontal {
        display: none;
    }
}

.modal-inner {
    position: relative;
    overflow: hidden;
    max-height: calc(100vh - 100px);
    width: 100% !important;
    background-color: $color-beige;

    @include media('>=tablet') {
        display: flex;
        flex-direction: row;
    }
}

.modal-text {
    background-color: $color-beige;
    padding: 20px;

    @include media('>=tablet') {
        padding: 30px;
        width: 51.5%;
        height: min-content;
    }

    @include media('>=large-desktop') {
        padding: 57px 48px 50px;
    }

    h4 {
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;

        @include media('>=tablet') {
            margin-bottom: 5px;
        }
    }

    p {
        color: rgba($color-dark, .4);

        @include media('>=large-desktop') {
            font-size: 24px;
            line-height: 34px;
        }
    }
}

.modal-img-holder {
    height: 40vh;
    position: relative;
    z-index: 1;

    @include media('>=tablet') {
        height: inherit;
        width: 48.5%;
        min-height: 300px;
    }

    @include media('>=desktop') {
        min-height: 450px;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
    }
}

.modal-text-card {
    padding-bottom: 10px;
    border-bottom: 1px solid rgba($color-dark, .1);
    margin-bottom: 20px;

    @include media('>=tablet') {
        padding-bottom: 15px;
    }

    @include media('>=desktop') {
        padding-bottom: 20px;
    }

    @include media('>=large-desktop') {
        padding-bottom: 30px;
        margin-bottom: 37px;
    }

    &:first-child {
        // padding-right: 30px;

        @include media('>=tablet') {
            padding-right: 5px;
        }
    }

    a:not(.btn) {
        &:hover {
            text-decoration: underline;
        }
    }
}

.calories-info {
    padding-top: 18px;

    .modal-text-card {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border-bottom-width: 2px;
        padding-bottom: 2px !important;
        margin-bottom: 8px !important;

        & > .h5 {
            margin-left: 15px;
            flex-shrink: 0;
        }
    }

    .note {
        text-align: right;
        font-size: 14px;
        line-height: 24px;
    }
}

.calories-info-card {
    font-size: 16px;
    line-height: 26px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 10px;
    letter-spacing: .3px;

    &:last-child {
        margin-bottom: 0;
    }

    &.half {
        @include media('>=mobile-landscape') {
            width: 35%;
        }

        &:last-child {
            margin-bottom: 10px;
        }
    }

    &.has-top-margin {
        margin-top: 16px;
    }

    & > span {
        flex-shrink: 0;
        margin-left: 15px;
    }

    .bold {
        font-weight: 500;
    }
}

.calories-card-outer {
    display: flex;
    flex-direction: column;

    @include media('>=mobile-landscape') {
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
    }
}

.text-bottom {
    margin-top: 15px;
    font-weight: 400;
    font-size: 12px;
    line-height: 22px;
    color: rgba($color-dark, .4);
}

.cart-modal-title {
    display: flex;
    align-items: center;
    position: relative;
    flex-wrap: wrap;

    h4 {
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;
    }

    button {
        color: $color-dark;
        background-color: $color-creame;
        border-radius: 50%;
        width: 24px;
        height: 24px;
        border: none;
        outline: none;
        font-size: 17px;
        position: relative;
        display: block;
        padding: 0;

        &::after {
            content: '';
            position: absolute;
            left: 50%;
            top: 100%;
            border: 12px solid transparent;
            border-bottom: 12px solid $color-dark;
            transform: translate3d(-50%, -50%, 1px);
            opacity: 0;
        }

        &.show-arrow {
            &::after {
                opacity: 1;
            }
        }
    }
}

.discount-tooltip-outer {
    // position: relative;
    margin-left: 17px;
    flex-shrink: 0;
}

.discount-tooltip {
    background-color: $color-dark;
    padding: 15px;
    border-radius: 4px;
    position: absolute;
    max-width: 100%;
    z-index: 111;
    opacity: 0;
    width: 100vw;
    top: calc(100% + 9px);
    left: 0;
    visibility: hidden;

    @include media('>=mobile-landscape') {
        max-width: 345px;
        position: fixed;
        transform: translateY(-30vh);
    }

    &.show {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
    }

    @include media('>=tablet') {
        padding: 16px 25px;
    }

    h6 {
        color: $color-white;
        font-size: 18px;
        line-height: 28px;
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;
    }

    p {
        color: $color-white;
        font-size: 16px;
        line-height: 26px;
    }
}

.woocommerce-mini-cart__total {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 18px;
    line-height: 1.56;

    @include media('>=desktop') {
        font-size: 20px;
        line-height: 1.4;
    }

    @include media('>=large-desktop') {
        font-size: 24px;
        line-height: 1.33;
    }

    &.discount-total {
        font-size: 12px;
        line-height: 22px;
        justify-content: flex-end;
        padding-left: 10px;

        .amount {
            color: rgba($color-dark, .4);
            padding-left: 10px;
        }
    }
}

.minicart-wrapper {
    // padding-bottom: 20px;

    // @include media('>=desktop') {
    //     padding-bottom: 10px;
    // }

    .shipping-title {
        margin-top: 30px;
        font-weight: 400;

        @include media('>=desktop') {
            margin-top: 60px;
        }
    }

    .shipping-description {
        margin-top: 10px;
        font-size: 16px;
        line-height: 26px;
    }

    .shipping-disclaimer {
        font-size: 16px;
        line-height: 26px;
        margin-top: 10px;

        li {
            padding-left: 15px;
            margin-bottom: 10px;

            &:last-child {
                margin-bottom: 0;
            }

            &:before {
                content: "";
                float: left;
                margin: 8px 0 0 -15px;
                border-radius: 50%;
                width: 6px;
                height: 6px;
                background: $color-dark;
            }
        }
    }

    .empty-title {
        font-weight: 400;
        font-size: 18px;
        line-height: 28px;
        margin-top: 32px;
        margin-bottom: 6px;

        & + span {
            font-weight: 300;
            font-size: 16px;
            line-height: 26px;
        }
    }

    &.empty {
        height: calc(100% - 28px);

        @include media('>=tablet') {
            height: inherit;
        }

        .widget_shopping_cart_content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 100%;
        }
    }
}