.order-section {
    background-color: $color-light-gray;
    overflow: hidden;

    .section-info {
        padding: 48px 0;
        position: relative;
        text-align: left;
        max-width: 1000px;

        @include media('>=tablet-landscape') {
            width: 50%;
            flex-shrink: 0;
            margin-left: 0;

            &:first-child {
                padding-right: 20px;
            }
        }

        @include media('>=desktop') {
            padding: 104px 0 101px;

            &:first-child {
                padding-right: 80px;
            }
        }

        @include media('>=medium-desktop') {
            padding: 104px 0 96px;

            &:first-child {
                padding-right: 100px;
            }
        }

        @include media('>=large-desktop') {
            padding-top: 132px;
            padding-bottom: 123px;

            &:first-child {
                padding-right: 172px;
            }
        }

        h3 {
            margin-bottom: 24px;

            @include media('<desktop') {
                font-size: 48px;
                line-height: 56px;
            }

            @include media('>=desktop') {
                margin-bottom: 20px;
            }

            @include media('>=large-desktop') {
                margin-bottom: 18px;
            }

            span {
                font-weight: 400;
                display: block;
            }
        }

        p {
            margin-left: 0;
            margin-right: 0;

            @include media('>=tablet-landscape') {
                max-width: 480px;
            }

            @include media('>=desktop') {
                padding-right: 78px;
            }

            @include media('>=medium-desktop') {
                margin-bottom: 12px;
            }

            @include media('>=large-desktop') {
                padding-right: 8px;
            }
        }

        .buttons-holder {
            margin-top: 13px;
        }

        .small {

            span {

                @include media('>=large-desktop') {
                    padding-top: 12px;
                    padding-bottom: 10px;
                }
            }
        }
    }

    &.animation {
        .section-info:not(.small-orders), .small-orders .section-info-inner {
            opacity: 0;
            transform: translate3d(0, 100px, 1px);
            transition: transform .85s ease, opacity .4s ease;
        }

        .small-orders .section-info-inner {
            transition: transform .85s ease .3s, opacity .4s ease .3s;
        }

        &.animated {
            .section-info:not(.small-orders), .small-orders .section-info-inner {
                opacity: 1;
                transform: translate3d(0, 0, 1px);
            }
        }
    }
}

.section-info {

    &.small-orders {

        &::after {
            content: '';
            width: 100vw;
            height: 100%;
            top: 0;
            left: 50%;
            background-color: $color-gray;
            transform: translate3d(-50%, 0, 1px);
            z-index: 1;
            position: absolute;

            @include media('>=tablet-landscape') {
                width: 50vw;
                transform: translate3d(0, 0, 1px);
                left: 0;
            }
        }

        h3, p, .buttons-holder {
            position: relative;
            z-index: 2;
            // transform: translateZ(1px);
        }
    }

    .section-info-inner {
        position: relative;
        z-index: 3;

        @include media('>=tablet-landscape') {
            margin-left: 20px;
        }

        @include media('>=desktop') {
            margin-left: 80px;
        }

        @include media('>=medium-desktop') {
            margin-left: 66px;
        }

        @include media('>=large-desktop') {
            margin-left: 172px;
        }
    }
}

.order-inner {
    @include media('>=tablet-landscape') {
        display: flex;
        flex-direction: row;
    }
}

.phone-info {
    margin-top: 41px;

    @include media('>=tablet-landscape') {
        margin-top: 0;
        position: absolute;
        bottom: 10px;
        left: 0;
    }

    @include media('>=desktop') {
        bottom: 31px;
    }

    @include media('>=large-desktop') {
        bottom: 30px;
    }

    span {
        font-weight: 300;
        display: block;

        @include media('>=tablet-landscape') {
            display: inline;
        }
    }

    a {
        font-weight: 400;

        .no-touch & {

            &:hover {
                text-decoration: underline;
            }
        }
    }
}