#datepicker {
    border: 1px solid #E1E8E8;
    background-color: $color-white;

    .ui-datepicker-prev, .ui-datepicker-next {
        padding: 5px;
        cursor: pointer;
        flex-shrink: 0;

        @include media('>=desktop') {
            padding: 9px;
        }

        span {
            display: none;
        }

        &::after {
            content: '';
            width: 18px;
            height: 15px;
            display: block;
            background-image: url('../images/datepicker-arrow.svg');
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;

            @include media('>=desktop') {
                width: 20px;
                height: 21px;
            }
        }
    }

    .ui-datepicker-title {
        @include media('>=desktop') {
            font-size: 20px;
            line-height: 28px;
        }

        @include media('>=large-desktop') {
            font-size: 24px;
            line-height: 34px;
        }
    }

    .ui-datepicker-next {
        order: 3;

        &::after {
            transform: rotate(-180deg);
        }
    }

    .ui-datepicker-header {
        padding: 15px 10px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        background-color: $color-light-gray;

        @include media('>=desktop') {
            padding: 36px 53px;
        }
    }

    .ui-datepicker-calendar {
        border: none;
        text-align: center;

        @include media('>=desktop') {
            width: 87%;
            margin: 31px auto;
        }

        th {
            background-color: $color-white;
            text-align: center;
        }

        td, th {
            height: auto;
            padding: 10px 5px;
            background-color: $color-white;
            position: relative;

            @include media('>=desktop') {
                padding: 19px 5px;
                font-size: 16px;
                line-height: 26px;
                width: calc(100%/7);
            }
        }

        tr {
            &:nth-child(odd) {
                td {
                    background-color: $color-white;
                }
            }
        }
    }

    .ui-datepicker-week-end {
        color: $color-creame;
    }

    td {
        &:not(.ui-state-disabled) {

            &::after {
                content: '';
                width: 30px;
                height: 30px;
                border-radius: 50%;
                display: block;
                background-color: $color-green;
                top: 50%;
                left: 50%;
                position: absolute;
                transform: translate3d(-50%, -53%, 1px);
                z-index: 0;

                @include media('>=desktop') {
                    width: 40px;
                    height: 40px;
                }
            }

            a, span {
                color: $color-white;
                transform: translate3d(-50%, -50%, 1px);
                z-index: 1;
                position: absolute;
                top: 50%;
                left: 50%;
                pointer-events: none;
            }
        }
    }
}