.almond-industry {

    .section-info {
        max-width: 772px;

        p {
            max-width: 766px;
        }
    }

    .news-holder {
        .news-card {
            &:not(.full) {
                @include media('>=tablet') {
                    width: calc(50% - 16px);
                }

                .h4, h4 {
                    @include media('>=desktop') {
                        margin-bottom: 23px;
                    }
                }
            }
        }
    }

    .news-card {
        position: relative;
        margin-bottom: 40px;
        background-color: $color-white;
        overflow: hidden;

        @include media('>=tablet') {
            margin-bottom: 0;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    .news-card-picture {
        width: 100%;
        position: relative;

        @include media('>=tablet') {
            height: 330px;
            max-height: 330px;
        }

        @include media('>=desktop') {
            height: 400px;
            max-height: 400px;
        }

        @include media('>=medium-desktop') {
            height: 500px;
            max-height: 500px;
        }

        @include media('>=large-desktop') {
            height: 600px;
            max-height: 600px;
        }
    }

    .news-card-text {
        padding: 15px;
        position: relative;

        @include media('>=tablet') {
            height: 100%;
        }

        @include media('>=desktop') {
            padding: 30px;
        }

        @include media('>=large-desktop') {
            padding: 42px 40px 38px;
        }
    }

    .video-play-btn {
        background: none;
        outline: none;
        border: none;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%, -50%, 1px);
        z-index: 2;
        pointer-events: none;
    }
}

.news-holder {
    margin-top: 40px;

    @include media('>=tablet') {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        column-gap: 32px;
        row-gap: 32px;
    }

    @include media('>=desktop') {
        margin-top: 75px;
    }

    @include media('>=large-desktop') {
        margin-top: 97px;
    }

    .news-card {
        overflow: hidden;
        margin-bottom: 40px;

        @include media('>=tablet') {
            margin-bottom: 0;
        }
    }

    .news-card-text {
        @include media('>=tablet') {
            height: 100%;
        }
    }

    .news-card-picture {
        &::after {
            content: '';
            width: 100%;
            display: block;
            padding-top: 70%;

            @include media('>=tablet') {
                display: none;
            }
        }

        img, video {
            position: absolute;
            top: 0;
            left: 0;
        }
    }
}