
/*bulleted-list-section*/

.bulleted-list-section-row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    .sub-text {
        margin: 0 0 15px;

        @include media(">=tablet") {/*768*/
            margin: 0 0 25px;
        }
    }

    .section-info {
        margin: 0 0 30px;
        text-align: left;

        @include media(">=tablet") {/*768*/
            margin: 0 20px 0 0;
            max-width: 775px;
            flex: 0 0 50%;
        }

        h2, .h2 {
            margin: 0 0 20px;
    
            @include media(">=tablet") {/*768*/
                margin: 0 0 28px;
            }
        }
    }

    &.reverse {
        @include media(">=tablet") {/*768*/
            flex-direction: row-reverse;
        }

        .section-info {
            @include media(">=tablet") {/*768*/
                margin: 0 0 0 20px;
            }
        }

        .bulleted-list-col {
            @include media(">=tablet") {/*768*/
                margin: 0 auto 0 0;
            }
        }
    }

    &.big-ico-list {
        .section-info {
            @include media(">=tablet"){/*768*/
                max-width: 635px;
                flex: 0 0 calc(50% - 20px);
            }
        }

        .bulleted-list-col {
            padding: 20px;
            max-width: 772px;

            @include media(">=tablet"){/*768*/
                padding: 40px;
                flex: 0 0 50%;
            }

            @include media(">=desktop"){/*1200*/
                padding: 80px;
            }
        }
    }

}

.bulleted-list-col {
    background: rgba($color-creame, .2);
    padding: 20px;
    
    @include media(">=tablet") {/*768*/
        max-width: 504px;
        flex: 0 0 45%;
        margin: 0 0 0 auto;
    }

    @include media(">=desktop") {/*1200*/
        padding: 44px 38px 44px 40px;
    }

    ul {
        margin: 0;
        font-weight: 400;
    }

    .dark-bullet {
        li:before {
            top: 3px;
        }
    }

    .icon-list {
        display: flex;
        flex-wrap: wrap;
        overflow: hidden;
        margin: -20px 0;
        justify-content: space-between;

        @include media(">=mobile-landscape"){/*568*/
            margin: -40px 0;
        }

        li {
            position: relative;
            width: 100%;
            border-top: 1px solid rgba($color-dark, .1);
            margin: -1px 0 0;
            padding: 30px 0;
            @include title-h5;
            font-family: $ff-pp-fragment-sans;
            font-weight: 300;

            @include media(">=mobile-landscape"){/*568*/
                width: 50%;
                padding: 40px 0;
            }
        
            @include media(">=large-desktop") {/*1800*/
                line-height: 1.42;
            }

            &:nth-child(even) {
                @include media(">=mobile-landscape"){/*568*/
                    padding-left: 4%;
                }
            }

            > span {
                display: block;
            }
        }

        .icon {
            margin: 0 0 16px;
            border-radius: 50%;
            width: 72px;
            height: 72px;
            background: $color-dark;
            display: flex;
            padding: 10px;
            
            @include media(">=mobile-landscape"){/*568*/
                margin: 0 0 26px;
            }

            @include media('>=medium-desktop') {
                width: 102px;
                height: 102px;
            }

            img {
                margin: auto;

                @include media('>=medium-desktop') {
                    transform: scale(1.416);
                }
            }
        }

        .text {
            padding: 0 10px 0 0;
        }
    }
}



/*bulleted-list-section end*/