.article-template {
	padding-top: 50px;

	@include media('>=desktop') {
		padding-top: 73px;
		position: relative;
	}

	.container {
		position: relative;
		z-index: 20;
	}

	.content {

		ul, ol {
			margin-left: auto;
			margin-right: auto;
		}

		ol {
			margin-top: -4px;
		}

		p {
			overflow: hidden;
		}

		figure {
			text-align: center;

			img {
				@include media('>=tablet') {
					height: auto;
					width: auto;
					max-width: 100%;
					margin-left: auto;
					margin-right: auto;
				}
			}
		}
	}

	.article-tags {
		font-size: 14px;
		line-height: 24px;
		font-weight: 400;
		color: #000;
		text-transform: uppercase;
		padding-top: 24px;
		border-top: 1px solid rgba($color-dark, .1);
		max-width: 772px;
		margin: 50px auto;

		&:last-child {
			margin-bottom: 0;
		}

		@include media('>=desktop') {
			font-size: 16px;
			line-height: 26px;
			margin: 80px auto;
		}

		span, a {
			display: inline-block;
			margin-right: 11px;
			letter-spacing: 1px;
		}
	}

	.article-tags-title {
		color: rgba($color-dark, .4);
	}
}

.article-slider-wrap {
	margin: 50px auto;
	max-width: 1040px;

	@include media('>=desktop') {
		margin: 80px auto;
	}
}

.article-slider {

	.slick-dots {
		padding-top: 0;
		position: absolute;
		bottom: 35px;
		left: 50%;
		transform: translateX(-50%);
	}

	.slick-arrow {
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
		width: 40px;
		height: 40px;
		background-color: rgba(0, 0, 0, 0);
		border-radius: 50%;
		border: 1px solid $color-white;
		outline: none;
		cursor: pointer;
		z-index: 10;
		transition: opacity .3s ease;
		padding-top: 4px;
		display: flex;
		align-items: center;
		justify-content: center;

		@include media('>=desktop') {
			width: 64px;
			height: 64px;
			right: 40px;
		}

		&.slick-prev {
			left: 10px;

			@include media('>=desktop') {
				left: 40px;
			}
		}

		&.slick-next {
			svg {
				transform: rotate(-180deg);
			}
		}

		&.slick-disabled {
			opacity: .5;
		}

		svg {
			position: relative;
			top: -1px;
			max-width: 80%;
			max-height: 80%;
			object-fit: contain;
		}
	}
}

.article-slide {
	position: relative;

	&::after {
		content: '';
		display: block;
		padding-top: 70%;

		@include media('>=tablet') {
			padding-top: 55.5%;
		}
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		position: absolute;
		top: 0;
		left: 0;
	}
}

.social-networks {
	background-color: $color-dark;
	max-width: 400px;
	margin: 30px auto 0;

	@include media('>=tablet') {
		margin: 50px auto 0;
	}

	@include media('>=desktop') {
		margin: 0;
		left: 0;
		position: absolute;
		top: 0;
	}

	.socials-list {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		padding: 9px;
		justify-content: center;

		@include media('>=desktop') {
			flex-direction: column;
		}
	}

	li {
		width: 62px;
		height: 62px;
		position: relative;

		a {
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			transition: background-color .4s ease;

			svg {
				path {
					transition: fill .4s ease;
				}
			}

			.no-touch &:hover {
				background-color: $color-creame;

				svg {
					path {
						fill: $color-dark;
					}
				}
			}
		}

		img, svg {
			max-width: 20px;
			max-height: 20px;
			object-fit: contain;
		}
	}

	.copy-link {
		svg {
			transform: scale(1.1);
		}
	}

	&.position-sticky {
		@include media(">=desktop") {
			position: fixed;
		}
	}

	&.position-absolute {
		@include media(">=desktop") {
			top: auto !important;
			bottom: 0;
		}
	}

	&.responsive {
		margin-bottom: 30px;

		@include media('>=desktop') {
			display: none;
		}
	}
}