
/*search-result*/

.search-result-section {

    .pagination {
        @include media(">=desktop") {
            margin-top: 61px;
        }

        .wp-pagenavi {
            a, span {
                &:after {
                    background: $color-dark;
                }
            }
        }
    }

}

.search-result-content {
    @include media(">=desktop") {
        margin: 0 auto;
        max-width: 1040px;
        padding: 17px 0 0;
    }

}

.search-result-form {
    margin: 0 0 40px;
    position: relative;

    @include media(">=desktop"){
        margin: 0 0 82px;
    }

    input[type="text"] {
        padding: 18px 36px;
        width: 100%;
			//background: url("../images/search-ico.svg") no-repeat 10px 50%;

        @include media(">=tablet") {
            padding: 18px 56px;
            //background-position: 28px 50%;
        }
    }

    button {
        border: 0;
        background: none;
        position: absolute;
        cursor: pointer;
        top: 0;
        right: 0;
        bottom: 0;
        display: flex;
        width: 35px;
        margin: 0;
        padding: 0;

        @include media(">=tablet") {
            width: 47px;
        }

        svg {
            width: 16px;
            height: 16px;
            margin: auto 0;

            @include media(">=desktop") {
                width: 20px;
                height: 20px;
            }
        }
        &[type="submit"]{
            right: auto;
            left: 0;
            border-radius: 35px;
            justify-content: flex-end;
            padding-right: 5px;
            opacity: .4;
					@include media('>=tablet') {
						padding-right: 0;
					}
            @include media('>=medium-desktop') {
                border-radius: 40px;
            }
            .no-touch &{
                transition: opacity .4s ease-in-out;
							&:hover{
								opacity: 1;
							}
            }
            svg{
                width: 20px;
                height: 20px;
            }
        }
    }
}

.search-result-amount {
    font-size: 18px;
    line-height: 1.56;
    margin: 0 0 20px;

	@include media('>=desktop') { /*1200*/
		font-size: 20px;
        line-height: 1.4;
        margin: 0 0 50px;
	}

	@include media('>=large-desktop') { /*1800*/
		font-size: 24px;
        line-height: 1.33;
	}

    b, strong {
        font-weight: 600;
    }
}

.search-result-row {
    color: #62676B;
    border-bottom: 1px solid rgba($color-dark, .1);
    padding: 0 0 40px;

    @include media(">=desktop") {/*1200*/
        padding: 0 0 62px;
    }

    & + & {
        padding-top: 40px;

        @include media(">=desktop") {/*1200*/
            padding-top: 66px;
        }
    }

    b, strong {
        font-weight: 700;
    }

    h4, .h4 {
        margin: 0 0 20px;
        color: $color-dark;

        a {
            color: currentColor;
            text-decoration: none;
            transition: all $transition-duration linear;

            .no-touch &:hover {
                text-decoration: underline;
                color: $color-green;
            }
        }
    }
}

/*search-result end*/