
/*tab*/

.icon-list-tabs {
    padding-top: 60px;

    @include media(">=desktop"){/*1200*/
        padding-top: 132px;
    }

    .icon-list-row {
        .text {
            @include media(">=tablet"){/*768*/
                flex: 0 0 47.5%;
            }
        }

        .icon {
            img {
                @include media('<tablet') {
                    max-width: 60px;
                    max-height: 60px;
                }
            }
        }
    }

    .big-table {

        th, td {
            text-align: left;

            @include media('>=medium-desktop') {
                font-size: 16px;
            }
        }
    }
}

.tabset-btn-text {
    cursor: pointer;

    h3, .h3 {
        margin: 0 0 21px;
    }


}

.tabset {
    overflow-x: auto;
    margin: 0 0 30px;
    position: relative;
    z-index: 2;

    @include media(">=desktop") { /*1200*/
        margin: 0 0 58px;
    }

    &.text-col-view {
        margin: 0 0 40px;

        @include media(">=desktop") { /*1200*/
            margin: 0 0 80px;
        }   
    }

    .holder {
        display: inline-flex;
        position: relative;

        &:before {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            content: "";
            border: 1px solid rgba($color-dark, .1);
            border-radius: 40px;
            pointer-events: none;
        }
    }

    button {
        background: none;
        min-height: 10px;
        z-index: 2;
        position: relative;

        span {
            padding: 12px 32px;
            color: $color-dark;
            font-size: 14px;
            line-height: 24px;
            font-weight: 400;
            letter-spacing: 1px;
        }

        &.active,
        .no-touch &.active:hover {
            background-color: $color-dark;

            span {
                color: $color-white;
            }
        }

        .no-touch &:hover {
            background-color: rgba(0,0,0,0);

            span {
                text-decoration: underline;
                color: $color-dark;
            }

            &:before {
                transform: scaleX(0);
            }
        }
    }

    &.text-col-view {
        @include media("<tablet"){/*768*/
            width: calc(100% + 40px);
            margin: 0 -20px 40px;
            padding: 0 0 0 20px; 
        }

        .holder {
            justify-content: space-between;
            width: 100%;

            &:before {
                display: none;
            }

        }

        .tabset-btn-text {
            transition: all $transition-duration linear;
            max-width: 500px;
            flex: 0 0 60vw;
            padding: 0 20px 0 0;

            @include media(">=tablet"){/*768*/
                max-width: 730px;
                flex: 0 0 47.5%;
                padding: 0;
            }

            &:not(.active) {
                opacity: .2;
            }
        }
    }
}

.tabs {
    position: relative;
    overflow: hidden;
    z-index: 2;

    .tab {
        transition: all $transition-duration linear;
        opacity: 0;
        height: 0;
        visibility: hidden;
        overflow: hidden;

        &.active {
            height: 100%;
            visibility: visible;
            opacity: 1;
        }
    }
}

/*tab end*/