.faq-inner {
    max-width: 1308px;
    margin: 0 auto;

    .right-side {
        display: block;
        background-color: rgba($color-creame, .2);
        padding: 15px;
        margin-top: 45px;

        @include media('>=tablet') {
            padding: 30px;
            width: 75.5%;
            margin-top: 0;
        }

        @include media('>=medium-desktop') {
            padding: 50px;
        }

        @include media('>=large-desktop') {
            padding: 79px 80px;
        }

        &::before {
            display: none;
        }

        .btn {

            span {
                align-items: center;

                @include media('>=large-desktop') {
                    padding: 12px 43px;
                }
            }

            svg {
                flex-shrink: 0;
                margin-right: 9px;
            }
        }
    }

    .left-side {
        justify-content: flex-start;
    }

    .accordion {
        &:not(:last-child) {
            margin-bottom: 40px;
        }

        .hidden {
            a {
                text-decoration: underline;

                .no-touch &:hover {
                    text-decoration: none;
                }
            }
        }
    }
}

.faq-categories {
    margin-top: 40px;

    h5 {
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;

        @include media('>=large-desktop') {
            line-height: 34px;
        }
    }
}

.category-form-search {
    input {
        width: 100%;
        padding-right: 50px;

        @include media('>=medium-desktop') {
            padding-right: 50px;
        }

        &:focus {

            &+button {
                opacity: 1;
            }
        }
    }

    .feildset {
        position: relative;
    }

    button {
        position: absolute;
        top: 50%;
        right: 23px;
        transform: translate3d(0, -50%, 1px);
        background-color: rgba(0,0,0,0);
        border: none;
        outline: none;
        display: flex;
        opacity: .4;
        transition: opacity .3s ease;
        cursor: pointer;
    }
}

.categories-list {
    margin-top: 23px;
    font-weight: 400;
}

.category-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
    transition: all .3s;

    &:last-child {
        margin-bottom: 0;
    }

    span {
        &:first-child {
            margin-right: 25px;
            letter-spacing: .3px;
            width: 100%;
        }

        &:last-child {
            opacity: .4;
            width: 50%;
            text-align: right;
        }
    }

    .no-touch & {
        &:hover {
            cursor: pointer;
        }
    }

    &.active {
        font-weight: 600;
    }
}

.accordion {

    li {
        padding-top: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid rgba($color-dark, .1);

        @include media('>=desktop') {
            padding-top: 30px;
            padding-bottom: 30px;
        }

        @include media('>=large-desktop') {
            padding-top: 43px;
            padding-bottom: 37px;
        }

        &:first-child {
            padding-top: 0;
        }

        &.active {

            .item-opener {
                svg {
                    transform: rotate(0);
                }
            }
        }
    }

    .hidden {
        display: none;
        padding-top: 16px;
        padding-right: 30px;
    }

    h5 {
        display: flex;
        position: relative;
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;
        padding-right: 30px;
        cursor: pointer;

        @include media('>=large-desktop') {
            line-height: 34px;
        }
    }

    .item-opener {
        background-color: rgba(0,0,0,0);
        outline: none;
        border: none;
        position: absolute;
        top: 5px;
        right: -6px;
        z-index: 1;
        padding: 5x;

        @include media('>=desktop') {
            top: 2px;
            right: -12px;
            z-index: 1;
            padding: 10px;
        }

        .no-touch &:hover {
            cursor: pointer;
        }

        svg {
            transition: transform .3s ease;
            transform: rotate(-180deg);
        }
    }

    p {
        font-weight: 300;
    }
}