.review-section {
    min-height: 100vh;
    position: relative;
    overflow: hidden;
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include media('<desktop') {
        flex-direction: column-reverse;
        padding-bottom: 100px;
        background-color: rgba($color-creame, .2);
    }

    @include media('>=desktop') {
        padding: 80px 0;
    }

    @include media('>=medium-desktop') {
        padding: 160px 0;
    }

    @include media('>=large-desktop') {
        padding: 200px 0;
        min-height: 1418px;
    }

    .slick-dots {
        justify-content: flex-start;

        &.white {
            li {
                button {
                    color: rgba($color-white, .4);
                }

                &.slick-active{
                    button {
                        color: $color-white;
                    }
                }
            }
        }
    }

    .slide-bg {
        background-position: 80% center;
        background-repeat: no-repeat;
        background-size: cover;

        img, picture {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include media('>=tablet-landscape') {
            background-position: center;
        }
    }

    .container {
        position: relative;
        z-index: 2;
    }

    & + .three-img-section {
        .one-picture {
            @include media('<tablet') {
                padding-top: 50px;
            }
        }
    }
}

.review-inner {

    &.right-side-content {

        .review-slider {
            @include media('>=desktop') {
                margin-left: auto;
            }
        }

        .slick-dots {
            @include media('>=desktop') {
                justify-content: flex-end;
            }
        }
    }
}

.review-slider {
    max-width: 928px;

    @include media('>=desktop') {
        width: 55%;
        max-width: 728px;
    }
}

.review-slide {

    svg {
        margin-bottom: 43px;
        width: 80px;
        display: inline-block;
        height: auto;

        @include media('>=large-desktop') {
            margin-bottom: 39px;
        }
    }

    p {
        font-family: $ff-pp-fragment-serif;
        font-weight: 400;
        font-size: 28px;
        line-height: 36px;

        @include media('>=tablet') {
            letter-spacing: .2px;
        }

        @include media('>=desktop') {
            font-size: 30px;
            line-height: 38px;
        }

        @include media('>=medium-desktop') {
            font-size: 35px;
            line-height: 43px;
        }

        @include media('>=large-desktop') {
            font-size: 40px;
            line-height: 48px;
        }
    }

    h3 {
        margin-bottom: 75px;
        font-weight: 800;

        @include media('>=large-desktop') {
            margin-bottom: 90px;
        }
    }
}

.slider-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    @include media('<desktop') {
        position: static;
        height: 450px;
        margin-top: -500px;
    }

    @include media('<tablet-landscape') {
        height: 400px;
        margin-top: -450px;
    }

    @include media('<tablet') {
        height: 300px;
        margin-top: -350px;
    }

    .slick-list, .slick-track, .slick-slide, .slick-slide > div, .slide-bg {
        height: 100%;
    }
}

.author {
    color: $color-dark;
    margin-top: 24px;
    font-size: 16px;
}

.author-position {
    color: rgba($color-dark, .4);
}

.review-slider-nav {
    margin-top: 20px;

    @include media('>=desktop') {
        margin-top: 37px;
    }

    @include media('<desktop') {
        margin-top: 470px;
        padding-top: 40px;
    }

    @include media('<tablet-landscape') {
        margin-top: 420px;
    }

    @include media('<tablet') {
        margin-top: 320px;
    }

    &.white {

        .slick-dots {

            li {
                button {
                    color: rgba($color-white, .4);
                }

                &::after {
                    background-color: rgba($color-white, .1);
                }

                &.slick-active{
                    button {
                        color: $color-white;
                    }
                }
            }
        }
    }

    .slick-dots {
        @include media('<tablet') {
            padding-top: 0;
        }
    }
}