
/*authorization-section*/

.authorization-section, .reset-password-section {
    .woocommerce {
        @include media(">=desktop"){/*1200*/
            display: flex;
            flex-wrap: wrap;
        }

        .woocommerce-notices-wrapper {
            width: 100%;
        }

        .woocommerce-form,
        .lost_reset_password {
            border: 0;
            margin: 0;
            border-radius: 0;
            padding: 20px;
            background: rgba($color-creame, .2);

            @include media(">=mobile-landscape"){/*768*/
                padding: 40px;
                margin: 0 auto;
                max-width: 80%;
            }
    
            @include media(">=desktop"){/*1200*/
                width: 50%;
                margin: 0 0 0 auto;
                max-width: 772px;
            }

            @include media(">=large-desktop"){/*1800*/
                padding: 80px;
            }

            &.woocommerce-ResetPassword {
                margin: 0 auto;
            }
            
            .form-row-button {
                margin-top: 0;
                margin-bottom: 0;
            }

            a {
                text-decoration: underline;

                .no-touch &:hover {
                    text-decoration: none;
                }
            }

            .lost_password a {
                text-decoration: none;

                .no-touch &:hover {
                    text-decoration: underline;
                }
            }

            p:not([class]){
                margin: 0 0 10px; 
            }
        }

        .lost_reset_password {
            .form-row-first {
                width: 100%;
                margin: 0;
            }
        }

        button.button {
            width: 100%;
            margin: 20px 0 0;

            @include media(">=desktop"){/*1200*/
                margin: 40px 0 0;
            }
        }

        // when login page has registration for
        #customer_login {
            display: flex;
            flex-direction: column;
            row-gap: 40px;

            @include media('>=tablet-landscape') {
                flex-direction: row;
                column-gap: 80px;
            }

            @include media('>=desktop') {
                column-gap: 100px;
            }

            @include media('>=large-desktop') {
                column-gap: 100px;
            }

            &::before, &::after {
                display: none;
            }

            .woocommerce-form {
                margin-top: 25px;

                @include media('>=tablet-landscape') {
                    margin-left: 0;
                    margin-right: 0;
                    max-width: 100%;
                }

                @include media('>=desktop') {
                    width: 100%;
                    margin-top: 42px;
                }
            }

            .section-info {
                width: 100%;

                h4, h2 {
                    width: 100%;

                    @include media('<desktop') {
                        text-align: center;
                    }
                }
            }

            h4, h2 {
                width: 100%;

                @include media('<desktop') {
                    text-align: center;
                }
            }

            .col-1, .col-2 {
                width: 100%;
            }
        }
    }

    .section-info {
        margin-bottom: 20px;

        @include media(">=desktop"){/*1200*/
            width: 40%;
            margin: 0;
            text-align: left;
            padding: 7px 0 0;
        }

        .sub-text {
            @include media(">=desktop"){/*1200*/
                max-width: 500px;
            }
        }
    }

    .lost_password {
        text-align: center;
        padding: 16px 0 0;
        font-weight: 400;
    }

    .woocommerce-ResetPassword {
        text-align: center;
        margin-top: 20px;
        padding: 20px;
        background-color: rgba($color-creame, .2);

        @include media('>=tablet') {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding: 80px;
            max-width: 772px;
            margin: 20px auto 0;
        }

        p {
            margin: 0 0 10px;

            @include media('>=tablet') {
                margin: 0 0 24px;
            }
        }

        .woocommerce-form-row {
            width: 100% !important;

            button {
                width: 100%;
            }

            label {
                text-align: left;
            }
        }

        .woocommerce-Button {
            width: 100%;
        }
    }
}

/*authorization-section end*/