
/*know-more*/

.know-more-section {
    position: relative;
    overflow: hidden;

    .section-info {
        min-height: 300px;
        padding: 50px 0;
        position: relative;

        @include media('>=desktop') {
            padding: 85px 0;
            max-width: 1052px;
        }

        @include media('>=medium-desktop') {
            padding: 130px 0;
        }

        @include media('>=large-desktop') {
            padding: 182px 0 176px;
        }

    }

    h3, .h3 {
        margin: 0 0 22px;
    }

    .buttons {
        margin-top: 40px;
    }

    .btn span {

        @include media(">=tablet") {/*768*/
            min-width: 239px;
        }
    }
}

.know-more-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

/*know-more end*/