$fonts-path: '../fonts/';
@font-face {
    font-family: 'PP_FragmentSans';
    src: url('#{$fonts-path}ppfragment-sansbold-webfont.woff2') format('woff2'),
         url('#{$fonts-path}ppfragment-sansbold-webfont.woff') format('woff');
    font-weight: 700;
    font-style: normal;
	font-display: swap;

}

@font-face {
    font-family: 'PP_FragmentSans';
    src: url('#{$fonts-path}ppfragment-sanslight-webfont.woff') format('woff'),
        url('#{$fonts-path}ppfragment-sanslight-webfont.woff2') format('woff2'),
        url('#{$fonts-path}ppfragment-sanslight-webfont.eot') format('eot'),
        url('#{$fonts-path}ppfragment-sanslight-webfont.ttf') format('ttf');
    font-weight: 300;
    font-style: normal;
	font-display: swap;

}

