
/*quality-box*/

.quality-box {
    border: 1px solid $color-gray;
    letter-spacing: 1px;
    font-size: 14px;
    line-height: 22px;
    font-weight: 400;

    @include media(">=desktop"){/*1200*/
        font-size: 16px;
        line-height: 26px;
    }
		& + .quality-box {
			@include media('<desktop') {
				margin-top: 8px;
			}

            @include media('>=desktop') {
                margin-top: 24px;
            }
		}
    + & {
        margin-top: 24px;
    }

    &.active {
        .open-close-btn {
            &:before {
                margin: 0;
                transform: translateY(-50%) rotate(225deg);
            }
        }

        .jcf-scrollable-wrapper .jcf-scrollbar-horizontal {
            opacity: 1;
        }
    }


    .jcf-scrollable-wrapper {
        height: auto !important;
        width: 100% !important;

        @include media('>=desktop') {
            position: static !important;
            overflow: visible;
            pointer-events: none;
        }

        .jcf-scrollbar-vertical {
            display: none;
        }

        .jcf-scrollbar-horizontal {
            height: 5px;
            display: flex;
            width: 100% !important;
            opacity: 0;
            transition: opacity .3s ease;

            @include media('>=desktop') {
                display: none;
            }

            .jcf-scrollbar-dec, .jcf-scrollbar-inc {
                height: 5px;
                width: 5px;
            }

            .jcf-scrollbar-slider {
                height: 5px;
                width: 100% !important;
            }

            .jcf-scrollbar-handle {
                height: 5px;
            }
        }

        .jcf-scrollable {
            height: auto !important;
            width: 100% !important;

            @include media('>=desktop') {
                overflow: visible !important;
            }
        }
    }
}

.quality-title {
    background: $color-gray;
    position: relative;
    overflow: hidden;
    padding: 15px 20px;
    display: flex;
    align-items: center;
    min-height: 92px;

    @include media(">=desktop"){/*768*/
        padding: 40px 46px;
    }

    > span {
        max-width: 400px;
        text-transform: uppercase;
        padding: 0 30px 0 0;
    }

    .open-close-btn {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        // bottom: 0;
        // right: 0;
        border: 0;
        background: none;

        &:before {
            position: absolute;
            top: 50%;
            right: 20px;
            width: 14px;
            height: 14px;
            margin: -5px 0 0;
            transform: translateY(-50%) rotate(45deg);
            content: "";
            border: 1px solid $color-dark;
            border-width: 0 1px 1px 0;
            transition: all $transition-duration linear;
        }

        @include media(">=desktop") {/*1200*/
            display: none;
        }
    }
}

.quality-description {
    display: flex;
    // max-height: 0;
    transition: all $transition-duration linear;
    overflow: hidden;
    opacity: 0;
    flex-direction: column;

    .active & {
        opacity: 1;
        overflow-x: auto;
        max-height: 900px !important;

        @include media(">=desktop") {
            max-height: max-content;
        }
    }

    @include media(">=desktop"){/*1200*/
        position: relative;
        opacity: 1;
        z-index: 2;
        overflow: visible;
        max-height: 100%;
    }

    .col-picture {
        padding: 24px 20px 24px 20px;
        flex: 0 0 80px;
        text-align: center;

        @include media(">=tablet"){/*768*/
            flex: 0 0 100px;
        }

        @include media(">=desktop"){/*1200*/
            padding: 16px 50px 16px 26px;
            flex: 0 0 200px;
        }

        @include media(">=large-desktop"){/*1800*/
            padding: 16px 90px 16px 46px;
            flex: 0 0 16.8%;
        }
    }

    .col-text {
        padding: 24px 0;
        font-size: 14px;
        line-height: 22px;
        font-weight: 300;
        display: flex;
        justify-content: space-between;
        letter-spacing: 0;
        align-items: flex-start;

        @include media(">=desktop"){/*1200*/
            // padding: 30px 20px 30px 0;
            padding: 18px 20px 30px 0;
            flex: 0 0 20%;
            flex-wrap: wrap;
            margin: 0 0 -24px;
            align-self: center;
        }

        @include media(">=large-desktop"){/*1800*/
            padding: 18px 50px 30px 0;
        }

        .col-text-title {
            display: block;
            font-weight: 400;
            margin: 0 0 8px;

            @include media('>=desktop') {
                font-size: 16px;
                line-height: 26px;
            }
        }

        .col {
            width: 210px;
            padding: 0 20px 0 0;

            @include media(">=desktop"){/*1200*/
                flex: 0 0 20%;
                margin: 0 0 24px;
            }

            @include media('>=large-desktop') {
                padding: 0 54px 0 0;
            }

            p {
                font-weight: 300;

                @include media('>=desktop') {
                    font-size: 16px;
                    line-height: 26px;
                }
            }
        }
    }

    .col-nuts {
        display: flex;
        justify-content: space-between;
        // align-self: flex-start;
        padding: 24px 15px 24px 0;
        position: relative;

        &::before {
            @include media('>=desktop') {
                content: '';
                width: 100%;
                height: 1px;
                display: block;
                top: 102px;
                left: 0;
                position: absolute;
                background-color: rgba($color-dark, .1);
            }
        }

        @include media(">=desktop") {/*1200*/
            padding: 0 0 23px;
            flex: 0 0 100%;
            justify-content: flex-start;
        }

        .col {
            height: auto;
            text-align: center;
            display: flex;
            padding: 0 5px;
            flex-direction: column;
            flex: 0 0 60px;
            overflow: hidden;
            height: 75px;

            @include media(">=tablet"){/*768*/
                height: 90px;
            }

            @include media(">=desktop") {/*1200*/
                overflow: visible;
                flex: 0 0 11.1%;
                height: auto;
                max-height: 304px;
            }

            .size {
                display: block;
                width: 100%;
                margin: 0 0 8px;

                @include media(">=desktop") {/*1200*/
                    padding: 39px 0;
                    height: 104px;
                    margin: 0 0 22px;
                }
            }

            .view {
                display: flex;
                align-items: flex-end;
                justify-content: center;
                flex: 1;
                margin: 0 auto;
                height: calc(100% - 30px);

                @include media(">=desktop") {/*1200*/
                    height: auto;
                }
            }

            img {
                margin-top: auto;
                max-height: 100%;
            }

            &:nth-child(2) {
                .view {
                    width: 48%;

                    @include media(">=tablet") {/*768*/
                        width: 65%;
                    }

                    @include media(">=desktop") {/*1200*/
                        width: 100%;
                    }
                }
            }

            &:nth-child(3) {
                .view {
                    width: 44%;
                    @include media(">=tablet") {/*768*/
                        width: 60%;
                    }

                    @include media(">=desktop") {/*1200*/
                        width: 100%;
                    }
                }
            }

            &:nth-child(4) {
                .view {
                    width: 40%;
                    @include media(">=tablet") {/*768*/
                        width: 55%;
                    }

                    @include media(">=desktop") {/*1200*/
                        width: 100%;
                    }
                }
            }

            &:nth-child(5) {
                .view {
                    width: 36%;
                    @include media(">=tablet") {/*768*/
                        width: 50%;
                    }

                    @include media(">=desktop") {/*1200*/
                        width: 100%;
                    }
                }
            }

            &:nth-child(6) {
                .view {
                    width: 32%;
                    @include media(">=tablet") {/*768*/
                        width: 45%;
                    }

                    @include media(">=desktop") {/*1200*/
                        width: 100%;
                    }
                }
            }

            &:nth-child(7) {
                .view {
                    width: 28%;

                    @include media(">=tablet") {/*768*/
                        width: 40%;
                    }

                    @include media(">=desktop") {/*1200*/
                        width: 100%;
                    }
                }
            }

            &:nth-child(8) {
                .view {
                    width: 28%;

                    @include media(">=tablet") {/*768*/
                        width: 40%;
                    }

                    @include media(">=desktop") {/*1200*/
                        width: 100%;
                    }
                }
            }

            &:nth-child(9) {
                .view {
                    width: 28%;

                    @include media(">=tablet") {/*768*/
                        width: 40%;
                    }

                    @include media(">=desktop") {/*1200*/
                        width: 100%;
                    }
                }
            }

        }
    }
}

.col-top {
    display: flex;

    @include media('>=desktop') {
        padding-bottom: 15px;
        position: relative;
    }

    &::after {

        @include media('>=desktop') {
            content: '';
            width: 100%;
            height: 1px;
            position: absolute;
            bottom: 0;
            left: 0;
            background-color: rgba($color-dark, .1);
        }
    }

    .col-text, .col-picture {
        position: relative;

        &::after {
            content: '';
            width: 100%;
            height: 1px;
            position: absolute;
            bottom: 0;
            left: 0;
            background-color: rgba($color-dark, .1);

            @include media('>=desktop') {
                display: none;
            }
        }
    }


    .col-text {
        @include media('>=desktop') {
            flex: 0 0 calc(100% - 200px);
        }
    }
}
/*quality-box end*/