
/*more-items*/
.more-items-section {
    overflow: hidden;

    .slick-list {
        overflow: visible !important;
    }

    .buttons.only-mobile {
        margin-top: 40px;
        margin-bottom: 80px;
    }
}

.retail {

    .more-product-item {

        .icon {
            padding: 0;

            &::before {
                display: none;
            }

            img {
                object-fit: cover;
            }
        }

        .no-touch &:hover {

            .icon {

                img {
                    transform: scale(1.1);
                }
            }
        }
    }

    .picture {
        overflow: hidden;
    }
}

.more-product-slider {
    .slick-slide {
        padding: 0 16px 0 0;

        @include media(">=desktop"){/*1200*/
            padding: 0 32px 0 0;
        }

        .more-product-item {
            @include media(">=large-desktop"){/*1800*/
                // width: 370px !important;
            }
        }
    }

    .slick-dots {
        @include media(">=tablet"){/*768*/
            display: none !important;
        }
    }
}

.more-items-heading {
    margin: 0 0 36px;

    @include media(">=tablet") {/*768*/
        display: flex;
        align-items: flex-end;
        margin: 0 0 30px;
    }

    @include media(">=large-desktop") {/*1800*/
        margin: 0 0 44px;
    }
    
    .buttons {
        display: none;

        @include media(">=tablet") {/*768*/
            display: block;
            margin: 0 0 16px auto;
        }
        
        @include media(">=large-desktop") {/*1800*/
            margin-bottom: 0;
        }
    }

    .btn span{
        padding-top: 13px;
        padding-bottom: 13px;
        font-size: 12px;
        line-height: 22px;

        @include media(">=desktop") {/*1200*/
            padding-left: 36px;
            padding-right: 36px;
        }

        @include media(">=large-desktop") {/*1800*/
            padding-left: 30px;
            padding-right: 30px;
            padding-top: 11px;
            padding-bottom: 11px;
            font-size: 14px;
            line-height: 24px;
        }
        
    }

    h2, .h2 {
        @include media(">=tablet") {/*768*/
            padding: 0 20px 0 0;
            max-width: 710px;
            flex: auto;
        }  

        @include media(">=large-desktop"){/*1800*/
            max-width: 740px;
        }
    }
}

.more-items-content {
    position: relative;
    padding: 46px 0 37px;

    @include media(">=desktop"){/*1200*/
        padding: 64px 0;
    }

    @include media(">=medium-desktop"){/*1440*/
        padding: 80px 0;
    }

    @include media(">=large-desktop"){/*1800*/
        padding: 125px 0;
    }

}

.more-items-bg {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    width: 100vw;
    z-index: -1;
    transform: translateX(-50%);

    img,
    .video-bg {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .mask {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba($color-dark, 0.1);
    }
}

.more-product-item {
    background: $color-white;
    position: relative;

    .no-touch &:hover {

        .icon {
            &::before {
                transform: translate(-50%,-50%) scale(1.4);
            }

            img {
                transform: rotate(-30deg);

                // &.has-shadow {
                //     transform: scale(1.1);
                // }
            }
        }
    }

    > a {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .picture {
        background: $color-beige;
        height: 240px;
        display: flex;
        
        @include media(">=tablet"){/*768*/
            height: 340px;
        }

        @include media(">=desktop"){/*1200*/
            height: 390px;
        }


        > img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .icon {
        position: relative;
        display: flex;
        width: 100%;
        height: 100%;
        padding: 20px;

        @include media(">=tablet"){/*768*/
            padding: 30px;
        }

        @include media(">=desktop"){/*1200*/
            padding: 49px;
        }

        &:before {
            content: "";
            border-radius: 50%;
            background: $color-creame;
            aspect-ratio: 1/1;
            position: absolute;
            top: 50%;
            left: 50%;
            width: 59%;
            max-width: 156px;
            transform: translate(-50%, -50%);
            transition: transform .4s ease;

            @include media(">=tablet"){/*768*/
                max-width: 205px;
            }
        }

        img {
            position: relative;
            margin: auto;
            width: 100%;
            height: 100%;
            object-fit: contain;
            max-width: 100%;
            max-height: 100%;
            transition: transform .4s ease,
        }
    }

    .name {
        font-family: $ff-pp-fragment-sans;
        margin: 0;
        text-align: center;
        padding: 15px 10px;
        font-weight: 400;
        min-height: 87px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include media(">=desktop"){/*1200*/
            padding: 31px 20px 27px;
            min-height: 114px;
        }

        @include media(">=large-desktop"){/*1800*/
            min-height: 122px;
            font-size: 22px;
        }
    }
}

/*more-items end*/