@import "variables";
@import "../libs/include-media";
@import "mixins";


body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: $color-beige;
	min-width: 320px;
	color: $color-dark;
	font: 300 18px/1.56 $ff-pp-fragment-sans;

	@include media('>=desktop') { /*1200*/
		font-size: 16px;
		line-height: 1.62;
	}

	@include media('>=large-desktop') { /*1800*/
		font-size: 18px;
		line-height: 1.56;
	}

	&.show-menu {
		@include media("<desktop") {
			overflow: hidden;
		}
	}
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	// common styles
}

h1, .h1 {
	@include title-h1;
}

h2, .h2 {
	@include title-h2;
}

h3, .h3 {
	@include title-h3;
}

h4, .h4 {
	@include title-h4;
}

h5, .h5 {
	@include title-h5;
}

h6, .h6 {
	@include title-h6;
}

b,
strong {
	font-weight: 400;
}

p {
	margin: 0;

	.content & {
		margin: 0 0 24px;
	}
}

img {
	vertical-align: top;
	max-width: 100%;
	height: auto;
	border: none;
}

a {
	text-decoration: none;
	color: inherit;
	outline: none;
}

.content {
	a:not([class]) {
		text-decoration: underline;
		color: $color-green;

		.no-touch &:hover {
			text-decoration: none;
		}
	}

	ul:not(.slick-dots) {
		padding-top: 13px;

		li {
			padding-left: 15px;
			margin-bottom: 10px;

			&:last-child {
				margin-bottom: 0;
			}

			&:before {
				content: "";
				float: left;
				margin: 8px 0 0 -15px;
				border-radius: 50%;
				width: 6px;
				height: 6px;
				background: $color-green;
			}
		}
	}

	ol {
		counter-reset: counterOrdered;

		li {
			position: relative;
			padding: 0 0 0 40px;
			margin-bottom: 12px;

			@include media('>=large-desktop') { /*1800*/
				padding-left: 36px;
			}

			&:before {
				position: absolute;
				content: counter(counterOrdered, decimal-leading-zero) ".";
				counter-increment: counterOrdered;
				top: 1px;
				left: 0;
				height: 28px;
				min-width: 28px;
				color: $color-green;
				font-weight: 400;
			}
		}
	}

	ul:not(.slick-dots),
	ol {
		max-width: 772px;

		ul {
			margin: 0;
			padding-left: 20px;
			padding-top: 10px;
		}

		ol {
			padding-top: 10px;
		}
	}

	.sub-text {

		@include media('>=desktop') {
			margin-bottom: 23px;
		}
	}

	p, h2, h3, h4, h5, h6 {
		max-width: 772px;
		margin-left: auto;
		margin-right: auto;

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	p {
		margin-bottom: 17px;
		margin-top: 17px;

		@include media('>=desktop') {
			margin-bottom: 21px;
			margin-top: 21px;
		}
	}

	h2, h3, h4, h5, h6 {
		margin-top: 50px;
		margin-bottom: 25px;

		@include media('>=desktop') {
			margin-top: 80px;
			margin-bottom: 32px;
		}
	}

	img, figure {
		margin: 50px 0;
		max-height: 800px;
		width: 100%;
		display: block;
		max-width: 100%;

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}

		@include media('>=desktop') {
			margin: 80px 0;
		}
	}

	blockquote {
		max-width: 1040px;
		margin: 50px auto;

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}

		@include media('>=desktop') {
			margin: 80px auto;
			letter-spacing: -1.9px;
		}

		cite {
			margin-top: 15px;
			font-size: 14px;
			line-height: 24px;

			@include media('>=desktop') {
				margin-top: 22px;
				font-size: 16px;
				line-height: 26px;
			}

			span {
				color: $color-dark;
			}
		}
	}

	& > span {
		max-width: 772px;
		margin: 17px auto;
		display: block;

		@include media('>=desktop') {
			margin: 21px auto;
		}
	}

	.wp-block-image {

		figure {
			width: auto;
		}
	}

	.wp-video {
		margin: 0 auto;
	}
}


// forms

input[type="text"],
input[type="password"],
input[type="search"],
input[type="email"],
input[type="tel"],
input[type="number"] {
	display: inline-block;
	margin: 0;
	border: 1px solid rgba($color-dark, .4);
	padding: 5px 20px;
	font: 400 16px/24px $ff-pp-fragment-sans;
	height: 54px;
	vertical-align: middle;
	background: rgba(0, 0, 0, 0);
	outline: none;
	transition: border-color $transition-duration/2 ease;
	border-radius: 35px;
	-webkit-appearance: none;
	-webkit-border-radius: 35px;

	@include placeholder {
		color: rgba($color-dark, .4);
	}

	@include media('>=medium-desktop') {
		height: 64px;
		border-radius: 40px;
		-webkit-border-radius: 40px;
		padding: 5px 29px;
		line-height: 28px;
		font-size: 18px;
	}

	&:focus {
		border-color: $color-dark;
	}

	.no-touch &:hover {
		border-color: $color-dark;
	}

	&.wpcf7-not-valid {
		border-color: $color-red;
	}
}

textarea {
	border: 1px solid rgba($color-dark, .4);
	padding: 20px;
	border-radius: 35px;
	-webkit-border-radius: 35px;
	line-height: 24px;
	font-size: 16px;
	background: rgba(0, 0, 0, 0);
	transition: border-color $transition-duration/2 ease;
	min-height: 100px;
	max-width: 100%;
	width: 100%;
	resize: none;
	font-family: $ff-pp-fragment-sans;

	@include media('>=tablet') {
		min-height: 150px;
	}

	@include media('>=desktop') {
		min-height: 200px;
	}

	@include media('>=medium-desktop') {
		border-radius: 40px;
		-webkit-border-radius: 40px;
		padding: 29px;
		line-height: 28px;
		font-size: 18px;
		min-height: 250px;
	}

	@include media('>=large-desktop') {
		min-height: 320px;
	}

	&:focus {
		border-color: $color-dark;
	}

	.no-touch &:hover {
		border-color: $color-dark;
	}

	&.wpcf7-not-valid {
		border-color: $color-red;
	}
}

select {
	display: inline-block;
	margin: 0;
	border: 1px solid $color-dark;
	padding: 5px 20px;
	line-height: 30px;
	font-size: 16px;
	height: 42px;
	vertical-align: middle;
	background: $color-white;
	outline: none;

	&:focus {
		border-color: $color-white;
	}
}

.buttons {
	&.only-mobile {
		display: block;

		@include media(">=tablet") { /*768*/
			display: none;
		}
	}
}

.btn,
.woocommerce .product-detail.product .button,
.woocommerce button.button,
.minicart-wrapper .button,
.woocommerce-MyAccount-navigation a,
.woocommerce a.button,
.woocommerce a.button.alt {
	display: inline-block;
	margin: 0;
	border: 0;
	text-transform: uppercase;
	font-size: 14px;
	line-height: 24px;
	font-family: $ff-pp-fragment-sans;
	color: $color-white;
	vertical-align: middle;
	text-decoration: none;
	background: $color-dark;
	cursor: pointer;
	-webkit-appearance: none;
	-webkit-border-radius: 40px;
	font-weight: 400;
	letter-spacing: 1px;
	min-height: 54px;
	border-radius: 40px;
	transition: color $transition-duration/2 linear, background-color $transition-duration/2 linear;
	position: relative;
	overflow: hidden;
	padding: 0;
	z-index: 1;

	@include media('>=desktop') { /*1200*/
		min-height: 50px;
	}

	@include media(">=medium-desktop") { /*1440*/
		min-height: 56px;
	}

	@include media('>=large-desktop') { /*1800*/
		min-height: 64px;
		font-size: 16px;
		line-height: 26px;
	}

	span {
		display: flex;
		margin: auto;
		text-align: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		z-index: 2;
		border-radius: 40px;
		position: relative;
		border: 1px solid transparent;
		padding: 15px 20px 13px;
		transition: background-color $transition-duration/2 linear;

		@include media('>=desktop') { /*1200*/
			padding: 13px 20px 11px;
		}

		@include media(">=medium-desktop") { /*1440*/
			padding: 16px 20px 14px;
		}

		@include media('>=large-desktop') { /*1800*/
			padding: 19px 30px 17px;
		}
	}

	&:disabled,
	&:disabled[disabled] {
		opacity: .4;
		color: $color-white;
		padding: 0;

		.no-touch &:hover {
			color: $color-white;
			opacity: .4;
			background-color: $color-dark;
		}

		// &:before {
		// 	display: none;
		// }
	}

	.no-touch &:hover {
		color: $color-white;
		border-color: $color-green;
		background-color: $color-green;
	}

	&:focus {
		border-color: $color-green;
	}

	&.small {
		min-height: 48px;
		font-size: 12px;
		line-height: 22px;

		@include media('>=desktop') { /*1200*/
			min-height: 44px;
		}

		@include media('>=large-desktop') { /*1800*/
			min-height: 48px;
			font-size: 14px;
			line-height: 24px;
		}

		span {
			// padding: 14px 24px 12px;
			padding: 14px 22px 12px;

			@include media('>=desktop') { /*1200*/
				padding: 12px 24px 10px;
			}

			@include media('>=large-desktop') { /*1800*/
				padding: 13px 30px 11px;
			}
		}
	}

	&.has-border {
		color: $color-dark;
		background: transparent;

		.no-touch &:hover {
			color: $color-white;
			background-color: $color-dark;
		}

		span {
			border-color: $color-dark;
		}

		&:before {
			background: $color-dark;
		}
	}

	&.btn-arrow {
		display: flex;
		padding: 10px;
		border-radius: 50px;
		min-height: 50px;
		width: 50px;
		height: 50px;

		span {
			border-width: 2px;

			@include media(">=desktop") { /*1200*/
				border-width: 1px;
			}

			@include media(">=large-desktop") { /*1800*/
				border-width: 2px;
			}
		}

		svg {
			margin: auto;

			@include media(">=desktop") { /*1200*/
				width: 13px;
				height: 10px;
				margin: 3px 0 0 3px;
			}

			@include media(">=large-desktop") { /*1800*/
				padding: 0;
				width: 14px;
				height: 12px;
			}

			path {
				transition: all $transition-duration/2 linear;
			}
		}
	}

	&.white {
		background-color: $color-white;
		color: $color-dark;

		.no-touch &:hover {
			background-color: $color-dark;
		}
	}

	&.without-bg {
		background-color: rgba(0, 0, 0, 0);
		color: $color-dark;
		margin-top: 15px;
	}

	&.standart-size-padding {
		padding: 0 50px;

		@include media('>=desktop') {
			padding: 0 75px;
		}

		@include media('>=large-desktop') {
			padding: 0 90px;
		}

		span {
			padding-left: 0;
			padding-right: 0;

			@include media('>=desktop') {
				padding-left: 0;
				padding-right: 0;
			}

			@include media('>=large-desktop') {
				padding-left: 0;
				padding-right: 0;
			}
		}

		&.has-border {
			border: 1px solid $color-dark;

			span {
				border: none;
			}
		}

		.green-section & {
			padding: 0 20px;
			min-width: 239px;
		}
	}
}

.buttons-holder, .woocommerce-mini-cart__buttons {
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	margin: -16px 0 0;
	transition: opacity .3s ease;

	@include media('>=tablet') {
		align-items: center;
		margin: -20px 0 -20px -20px;
	}

	&.centered {
		justify-content: center;
	}

	&.hidden {
		opacity: 0;
	}

	.btn, .button {
		margin: 16px 0 0 0;
		width: 100%;
		z-index: 1;

		@include media('>=tablet') {
			margin: 20px 0 20px 20px;
			width: auto;
		}
	}
}

.woocommerce.no-touch .product-detail.product .button:hover,
.no-touch .woocommerce button.button:hover {
	background: $color-green;
	color: $color-white;
}

.woocommerce a.button.alt,
.woocommerce button.button.alt {
	margin: 40px auto 0;
	display: block;
	background: $color-dark;

	.no-touch &:hover,
	&:hover {
		background: $color-green;
	}

	// &:before {
	// 	background-color: $color-dark;
	// }

	// &.checkout-button {
	// 	.no-touch &:hover,
	// 	&:hover {
	// 		background: $color-white;
	// 	}
	// }

	@include media(">=tablet") { /*768*/
		width: 80%;
		max-width: 300px;
	}
}

.woocommerce form {
	.form-row-first,
	.form-row-last {
		@include media("<mobile-landscape") {
			width: 100%;
		}
	}

	.form-row {
		padding: 0;
		margin: 0 0 10px;

		@include media(">=tablet") { /*768*/
			margin: 0 0 24px;
		}
	}

	.password-input input[type=password] {
		padding-right: 50px;
	}

	.show-password-input {
		background: url("../images/eye-opened.svg") no-repeat 0 50%;
		width: 46px;
		bottom: 0;
		top: 0;
		right: 0;
		height: 54px;

		@include media(">=medium-desktop") { /*1440*/
			height: 64px;
		}

		&.display-password {
			background-image: url("../images/eye-closed.svg");
		}

		&:after {
			display: none;
		}
	}

	#wc-stripe-cc-form {

		& + fieldset {
			border: none;

			.form-row {

				label {
					@include media('<medium-desktop') {
						padding-left: 25px;
					}
				}
			}
		}
	}
}


/*inputs*/
.wpcf7-list-item {
	position: relative;
	display: block;
}

input[type=checkbox],
input[type=radio] {
	position: absolute;
	pointer-events: none;
	top: auto;
	left: 0;
	opacity: 0;

	&:disabled {
		& ~ label,
		& ~ span {
			opacity: .3;
		}
	}

	&:not(:disabled) {
		& ~ label,
		& ~ span {
			cursor: pointer;

			.no-touch &:hover {
				opacity: 1;
			}
		}

		&:checked {
			& ~ label,
			& ~ span {
				opacity: 1;

				&:before {
					border-color: $color-dark;
				}
			}

		}

		.no-touch &:hover {
			& ~ label,
			& ~ span {
				&:before {
					border-color: $color-dark;
				}
			}
		}
	}

	&:checked {
		& ~ label,
		& ~ span {
			&::before {
				background: $color-dark;
			}

			&:after {
				opacity: 1;
			}
		}
	}

	&:focus-visible {
		& ~ label,
		& ~ span {
			&:before {
				outline-color: $color-dark;
			}
		}
	}

	& ~ label,
	& ~ span {
		transition: color $transition-duration linear;
		position: relative;
		display: inline-block;
		vertical-align: top;
		padding: 0 0 0 30px;
		margin: 0 !important;
		-webkit-tap-highlight-color: transparent;

		&:before {
			content: '';
			width: 20px;
			height: 20px;
			border: 1px solid rgba($color-dark, .4);
			outline: 2px dashed transparent;
			position: absolute;
			top: 2px;
			left: 0;
			transition: border-color $transition-duration linear, background $transition-duration linear, outline-color $transition-duration linear;
		}

		&:after {
			content: '';
			position: absolute;
			transition: opacity $transition-duration linear;
			opacity: 0;
			width: 9px;
			height: 6px;
			border: 1px solid $color-white;
			border-width: 0 0 1.5px 1.5px;
			transform: rotate(-45deg);
			top: 7px;
			left: 6px;
		}

		a {
			text-decoration: underline;

			.no-touch &:hover {
				text-decoration: none;
			}
		}
	}

	&#wc-stripe-new-payment-method {

		& ~ label,
		& ~ span {
			&::before {
				top: 0;
			}

			&::after {
				top: 5px;
			}
		}
	}
}

input[type=radio] {
	&:checked {
		& ~ label,
		& ~ span {
			cursor: default;
		}
	}

	& ~ label,
	& ~ span {
		&:before {
			border-radius: 50%;
		}
	}
}

label {
	display: inline-block;
	margin: 0;
	vertical-align: middle;
}

.woocommerce form .form-row label {
	line-height: inherit;
	padding-left: 20px;

	@include media('>=medium-desktop') {
		padding-left: 30px;
	}
}

blockquote {
	background: none;
	padding: 20px 0;
	border: 1px solid rgba($color-dark, .1);
	position: relative;
	border-width: 1px 0;
	text-align: center;
	margin: 0;

	@include media('>=desktop') { /*1200*/
		padding: 30px 0;
	}

	@include media('>=large-desktop') { /*1800*/
		padding: 48px 0;
	}

	&.h4 {
		font-weight: 600;
	}

	p,
	q {
		@include title-h4;
		font-weight: 600;
		display: block;
		margin: 0 0 12px;

		@include media('>=desktop') { /*1200*/
			margin: 0 0 18px;
		}

		@include media('>=large-desktop') { /*1800*/
			margin-bottom: 24px;
		}
	}

	q {
		&:before,
		&:after {
			display: none;
		}
	}

	cite {
		font-style: normal;
		display: block;
		color: rgba($color-dark, .4);
		letter-spacing: normal;
		font-weight: 400;

		@include media('>=large-desktop') { /*1800*/
			font-size: 16px;
			line-height: 1.62;
		}

		strong,
		b {
			font-weight: 300;
			color: $color-dark
		}
	}

	& > :last-child {
		margin-bottom: 0;
	}
}

table {
	margin: 0;
	border: 1px solid $color-gray;
	width: 100%;

	// &.dark-head {
	// 	th {
	// 		color: $color-white;
	// 		background: $color-dark;

	// 		@include media(">=desktop") { /*1200*/
	// 			height: 105px;
	// 		}
	// 	}
	// }

	th,
	td {
		border: 0;
		padding: 10px;

		@include media(">=desktop") { /*1200*/
			padding: 20px;
		}

		@include media(">=large-desktop") { /*1800*/
			padding: 20px 39px;
		}
	}

	th {
		text-transform: uppercase;
		letter-spacing: 1px;
		font-size: 12px;
		line-height: 1.71;
		font-weight: 400;
		background: $color-gray;
		text-align: left;

		@include media(">=desktop") { /*1200*/
			font-size: 14px;
			height: 104px;
		}
	}

	td {
		font-size: 14px;
		line-height: 1.62;
		font-weight: 300;
		background: $color-light-gray;

		@include media(">=desktop") { /*1200*/
			font-size: 16px;
			height: 107px;
		}
	}

	tr:nth-child(odd) {
		td {
			background: $color-white;
		}
	}

	.table-adaptive & {
		thead,
		th {
			@include media('<tablet') {
				display: none;
			}
		}

		tbody tr {
			@include media('<tablet') {
				display: block;
			}
		}

		td {
			position: relative;
			transition: all $transition-duration linear;

			@include media('<tablet') {
				border: none;
				display: block;
				position: relative;
				width: 100%;
			}

			&:before {
				@include media('<tablet') {
					content: attr(data-label);
					display: block;
					text-transform: uppercase;
				}
			}
		}

		.active td {
			color: $color-white;
			background: $color-green;
		}

		.active + .active td {
			box-shadow: 0px -1px 0px 0px #699632;
			// border-top: 1px solid ;
		}
	}
}

.wpcf7-not-valid-tip {
	color: $color-red;
	font-size: 14px;
