.logo {
	position: relative;
	height: 90px;
	display: block;
	margin: 0 auto;

	a {
		display: inline-block;
	}

	&:before {
		content: "";
		width: 320px;
		height: 140px;
		position: absolute;
		top: -11px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;
		background: url("../images/logo-bg.svg") no-repeat 50% 0;
	}

	img {
		position: relative;
		z-index: 2;
		height: 100%;
		width: auto;
	}
}

.btn-menu {
	min-width: 24px;
	min-height: 24px;
	width: 24px;
	height: 24px;
	border: 0;
	outline: none;
	background: none;

	&:before,
	&:after {
		position: absolute;
		top: 4px;
		left: 0;
		width: 100%;
		background: $color-white;
		height: 2px;
		content: "";
		transition: all $transition-duration linear;
	}

	&:after {
		top: 14px;
	}
}

.link {
	position: relative;
	font-weight: 400;

	&:before {
		position: absolute;
		top: 100%;
		height: 1px;
		left: 0;
		width: 100%;
		background: currentColor;
		content: "";
		transition: transform $transition-duration/1.5 ease;
		transform-origin: bottom left;
		transform: scaleX(0);
	}

	.no-touch &:hover {
		&:before {
			transform: scaleX(1);
		}
	}
}

