				}
			}
		}
	}

	.select2-selection--single {
		position: relative;
		min-width: 150px;
		height: 54px;
		z-index: 20;
		font: 400 16px/24px $ff-pp-fragment-sans;
		background: none;
		border: 1px solid rgba($color-dark, .4);
		border-radius: 35px;
