			height: 64px;
			border-radius: 40px;
		}

		.no-touch &:hover {
			border-color: $color-dark;
		}

		.select2-selection__rendered {
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;
			display: flex;
			align-items: center;
			color: $color-dark;
			padding: 12px 50px 12px 20px;
			height: inherit;

			@include media('>=medium-desktop') {
				padding-left: 29px;
			}
		}

		.select2-selection__arrow {
			position: absolute;
			text-align: center;
			width: 50px;
			bottom: 0;
			right: 0;
			top: 0;
			height: auto;

			b {
				border: 0;
				margin: 0;
				position: absolute;
				top: 50%;
				left: 3px;
				width: 19px;
				height: 12px;
				background: url("#{$images-path}/select-arrow.svg") 50% 50% no-repeat;
				background-size: contain;
				transition: transform .3s;
				transform: translateY(-50%) rotate(180deg);
			}
		}

		.select2-selection__placeholder {
			color: rgba($color-dark, .4);
		}
	}

	.select2-results__option--highlighted[aria-selected],
	.select2-results__option--highlighted[data-selected] {
		background: none;
		color: $color-dark;
	}
}

