				}

				@include media('>=desktop') {
					margin-top: 33px;
					padding-bottom: 27px;
				}

				img {
					width: 58px;
					height: 58px;
					position: relative;
					object-fit: contain;
					vertical-align: top;
					float: left;
					z-index: 1;

					@include media('<tablet') {
						margin-left: 0;
						margin-top: 15px;
					}

					@include media('>=tablet') {
						width: 171px;
						height: auto;
						// height: 158px;
					}
				}

				& > a {
					display: block;
					font-size: 18px;
					line-height: 1.56;
					font-weight: 400;

					@include media('>=desktop') {
						font-size: 20px;
						line-height: 1.4;
					}

					@include media('>=large-desktop') {
						font-size: 24px;
						line-height: 1.33;
					}

					&:not(.remove) {
						position: relative;
					}
				}

				.remove {
					display: flex;
					justify-content: center;
					flex-direction: column;
					margin-right: 12px;
					color: $color-dark !important;

					&:hover {
						color: $color-dark !important;
						background: rgba(0, 0, 0, 0);
					}

					@include media('>=mobile-landscape') {
						height: auto;
						flex-shrink: 0;
					}

					& + a {
						flex-shrink: 0;
						@include media('<tablet') {
							max-width: 58px;
						}

						@include media('>=tablet') {
							display: flex;
							align-items: center;
						}

						@include media('>=desktop') {
							display: block;
						}
					}
				}

				.woocommerce-Price-amount {
					display: flex;
					justify-content: flex-end;
					flex-direction: column;
					margin-left: auto;
					padding-left: 20px;
					flex-shrink: 0;
					font-size: 18px;
					line-height: 1.56;

					@include media('>=desktop') {
						font-size: 20px;
						line-height: 1.4;
						padding-bottom: 13px;
					}

					@include media('>=large-desktop') {
						font-size: 24px;
						line-height: 1.33;
					}
				}

				.item-wrapper {
					margin-left: 11px;
					padding-top: 12px;
					@include media('>=desktop') {
							flex-grow: 1;
					}
					@include media('>=large-desktop') {
						margin-left: 25px;
					}

					& > a, & > span {
						display: block;
					}

					& > a {
						font-size: 20px;
						color: #000;
						margin-bottom: 5px;
						font-weight: 400;

						@include media('>=desktop') {
							font-size: 26px;
						}

						@include media('>=large-desktop') {
							font-size: 33px;
							line-height: 40px;
						}
					}
				}

				.item-weight {
					color: rgba($color-dark, .4);
				}

				.quantity {
					margin-top: 20px;
					display: inline-block;

					@include media('>=large-desktop') {
						margin-top: 25px;
					}

					input[type="number"] {
						text-align: center;

						@include media('>=large-desktop') {
							font-size: 24px;
							line-height: 32px;
						}
					}

					.qty {
						width: 3.1em;
					}
				}
			}
		}

		ul.product_list_widget {
			margin-bottom: 20px;

			@include media('>=desktop') {
				margin-bottom: 38px;
			}

			& + .discount-total {
				@include media('>=desktop') {
					margin-top: -26px;
				}
			}
		}
	}

	.cart_item {

		.quantity {
			.qty {
				width: 100px;
			}
		}
	}

	.woocommerce-order-details__title {
		margin-bottom: 10px;
	}

	.woocommerce-order-details {

		.order-again {
			margin-top: 15px;

			a {
				display: inline-flex;
				align-items: center;
				justify-content: center;
				padding: 0 20px;
			}
		}
	}

	.woocommerce-order {
		ul {

			&.order_details {
				li {
					margin-right: 0;
				}
			}
		}
	}

	.woocommerce-checkout #payment ul.payment_methods li {
		margin: 0 0 0.5em;
	}

	form .form-row .select2-container {

		& + .jcf-select-text {
			opacity: 0;

			& + .jcf-select-opener {
				opacity: 0;
			}
		}
	}

	.form-row {

		.wc-stripe-elements-field {
			height: 54px;
			border-radius: 40px;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			border-color: rgba($color-dark, .4);
			padding-left: 20px;
			padding-right: 20px;
			transition: border-color $transition-duration/2 ease;

			@include media('>=desktop') {
				height: 64px;
			}

			@include media('>=medium-desktop') {
				padding-left: 29px;
				padding-right: 29px;
			}

			& > div {
				width: 100%;
			}

			&:focus {
				border-color: $color-dark;
			}
		}
	}

	.woocommerce-input-wrapper {
		// overflow: visible;

		input {
			line-height: 3.6 !important;
		}

		.jcf-select {

			& > input {

				& + .jcf-select-text {
					opacity: 0;

					& + .jcf-select-opener {
						opacity: 0;
					}
				}
			}
		}
	}

	.payment_method_stripe {

		.form-row {
			label {
				display: block !important;
			}
		}
	}

	#stripe-payment-data {

		li {
			label {
				display: block !important;
				line-height: 26px;
			}

			input {
				display: block;
			}
		}
	}
}

.woocommerce-customer-details {
	background: rgba($color-creame, .2);
	padding: 20px;

	@include media(">=desktop") { /*1200*/
		padding: 40px;
	}

	h2, .h2 {
		margin: 0 0 10px;

		@include media(">=desktop") { /*1200*/
			margin: 0 0 20px;
		}
	}

	h3, .h3, h4, .h4 {
		margin: 0 0 10px;
	}
}

.woocommerce-cart-form {
	.product-remove {
		width: 50px;
	}

	.product-thumbnail {
		max-width: 50px;

		@include media('>=desktop') {
			min-width: 90px !important;
		}

		img {
			width: auto;
		}
	}

	.quantity {
		display: inline;
	}

	.coupon {

		input {
			height: 54px;

			@include media('>=desktop') { /*1200*/
				height: 50px;
			}

			@include media(">=medium-desktop") { /*1440*/
				height: 56px;
			}

			@include media('>=large-desktop') { /*1800*/
				height: 64px;
			}
		}

		.button {
			.no-touch &:hover {
				background-color: $color-green;
			}
		}

		& + .button {
			.no-touch &:hover {
				background-color: $color-green;
			}
		}
	}

	h1 {
		margin-bottom: 20px;
		text-align: center;
		font-size: 32px;
		line-height: 1.4;

		@include media('>=desktop') {
			margin-bottom: 30px;
			font-size: 64px;
			line-height: 1.12;
		}

		@include media('>=large-desktop') {
			font-size: 80px;
			line-height: 1.1;
		}
	}
}

.quantity {
	position: relative;

	.screen-reader-text {
		display: none;
	}

	input[type=number]::-webkit-inner-spin-button,
	input[type=number]::-webkit-outer-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	input[type=number] {
		-moz-appearance: textfield;
		padding: 0 15px;
		border: 0;
		width: 76px;
		height: auto;
	}

	.quantity-button {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		width: 15px;
		text-align: center;
		cursor: pointer;
		border: 0;
		background: none;
		padding: 0;
		margin: 0;
		display: flex;
		justify-content: center;
		align-items: center;

		&.quantity-up {
			left: auto;
			right: 0;
		}

		.minuse {
			width: 12px;
			height: 12px;
			background: url("../images/minus2.svg") no-repeat 50% 50%;
		}

		.plus {
			width: 12px;
			height: 12px;
			background: url("../images/plus.svg") no-repeat 50% 50%;
		}
	}
}

.shipping-calculator-button,
.woocommerce a.button.edit {
	display: inline-block;
	text-transform: uppercase;
	font-family: $ff-pp-fragment-sans;
	white-space: nowrap;
	color: $color-dark;
	background: transparent;
	font-weight: 400;
	letter-spacing: 1px;
	border-radius: 40px;
	transition: color $transition-duration/2 linear, background $transition-duration/2 linear;
	position: relative;
	overflow: hidden;
	min-height: 48px;
	font-size: 12px;
	line-height: 1.83;

	@include media('>=desktop') { /*1200*/
		min-height: 44px;
	}

	@include media('>=large-desktop') { /*1800*/
		min-height: 48px;
		font-size: 14px;
		line-height: 1.71;
	}

	span {
		display: flex;
		margin: auto;
		text-align: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		z-index: 2;
		border-radius: 40px;
		position: relative;
		border: 1px solid $color-dark;
		transition: background $transition-duration linear;
		padding: 13px 24px;

		@include media('>=desktop') { /*1200*/
			padding: 11px 24px;
		}

		@include media('>=large-desktop') { /*1800*/
			padding: 12px 30px;
		}
	}

	.no-touch &:hover {
		color: $color-white;
		border-color: $color-green;
		background-color: $color-dark;

		// &:before {
		// 	transform: scaleX(1);
		// 	transform-origin: bottom left;
		// }
	}

	&:focus {
		border-color: $color-green;
	}

	&:before {
		position: absolute;
		top: -1px;
		left: -1px;
		bottom: -1px;
		right: -1px;
		background: $color-dark;
		content: "";
		transition: transform $transition-duration/1.5 ease;
		transform: scaleX(0);
		transform-origin: bottom left;
		z-index: 0;
	}
}

#add_payment_method .cart-collaterals .shipping-calculator-button,
.woocommerce-cart .cart-collaterals .shipping-calculator-button,
.woocommerce-checkout .cart-collaterals .shipping-calculator-button {
	position: relative;

	&:hover {
		&:after {
			color: $color-white;
		}
	}

	&:after {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 15px;
		color: $color-dark;
		transition: all $transition-duration linear;
	}

	span {
		padding-left: 50px;
	}
}

.woocommerce span.onsale {
	top: 10px;
	left: 10px;
	line-height: 1;
	font-size: 12px;
	line-height: 16px;
	display: flex;
	align-items: center;
	justify-content: center;
	text-transform: uppercase;
	min-width: 60px;
	min-height: 60px;
	padding: 10px;
	background: $color-green;
	font-weight: 400;

	@include media(">=mobile-landscape") { /*568*/
		min-width: 80px;
		font-size: 14px;
		min-height: 80px;
	}
}

.clipboard-success-img {
	position: absolute;
	padding: 5px 15px;
	background: black;
	border-radius: 5px;
	color: white;
	z-index: 2;
	pointer-events: none;
	top: -100%;
	left: 50%;
	transform: translate3d(-50%, 50%, 1px);
	white-space: nowrap;
	font-size: 14px;
	display: none;

	@include media('>=desktop') {
		top: 50%;
		transform: translateY(-50%);
		left: calc(100% + 20px);
	}
}

.grecaptcha-badge {
	transform: translateX(100%) !important;
}