.benefits-section {
    &.cream-bg {
        padding-top: $indent-section-mobile/2;
        padding-bottom: $indent-section-mobile/2;

        @include media('>=desktop') { /*1200*/
            padding-top: $indent-section-mobile;
            padding-bottom: $indent-section-mobile;
        }

        @include media('>=medium-desktop') { /*1440*/
            padding-top: $indent-section-desktop;
            padding-bottom: $indent-section-desktop;
        }

        @include media('>=large-desktop') { /*1800*/
            padding-top: $indent-section-medium-desktop;
            padding-bottom: $indent-section-medium-desktop - 30px;
        }
    }

    .section-info {
        p {
            margin-left: auto;
            margin-right: auto;
            max-width: 772px;
        }
    }

    &.animation {

        .benefits-item {
            transform: translateY(150px);
            opacity: 0;
            transition: transform 1s ease, opacity .4s linear;

            &:nth-child(2) {
                transition-delay: .15s;
            }

            &:nth-child(3) {
                transition-delay: .30s;
            }

            &:nth-child(4) {
                transition-delay: .45s;
            }
        }

        &.animated {

            .benefits-item {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }
}

.benefits-item {
    text-align: center;
    max-width: 346px;
    margin: 0 auto 25px;

    &:last-child {
        margin-bottom: 0;
    }

    @include media('>=tablet-landscape') {
        width: 50%;
        padding: 0 5px;
        margin: 0;
    }

    h5 {
        margin-bottom: 16px;
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;
        @include media('<large-desktop') {
            font-size: 22px;
        }
    }
}

.benefits-icon-holder {
    width: 58px;
    height: 58px;
    margin: 0 auto 18px;
    display: flex;
    align-items: center;
    justify-content: center;

    svg, img {
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
    }
}

.benefits-list {
    margin-top: 30px;

    @include media('>=tablet-landscape') {
        display: flex;
        flex-direction: row;
        justify-content: center;
        column-gap: 20px;
    }

    @include media('>=desktop') {
        margin-top: 60px;
        column-gap: 30px;
    }

    @include media('>=medium-desktop') {
        column-gap: 45px;
    }

    @include media('>=large-desktop') {
        margin-top: 93px;
        column-gap: 64px;
    }

    &.four-card-row {

        @include media('>=desktop') {
            column-gap: 32px;
        }

        .benefits-item {
            max-width: 370px;
        }
    }
}