
/*packaging*/

.packaging-section {
    &.grey-bg:not([class*="indent-"]) {
        padding: 40px 0;

        @include media(">=desktop") {/*1200*/
            padding: 80px 0;
        }
    
        @include media(">=large-desktop") {/*1800*/
            padding: 171px 0;
        }
    }

    h3, .h3 {
        margin: 0 0 20px;

        @include media(">=tablet") {/*768*/
            margin: 0 0 30px;
        }

        @include media(">=desktop") {/*1200*/
            margin: 0 0 40px;
        }

        @include media(">=large-desktop") {/*1800*/
            margin: 0 0 59px;
        }
    }

    table {
        td, th {
            text-align: center;
        }

        td:first-child,
        th:first-child {
            text-align: left;
            min-width: 120px;

            @include media(">=tablet"){/*768*/
                width: 260px;
            }
        }



        td:first-child {
    
            @include media(">=tablet"){/*768*/
                display: flex;
                align-items: center;
            }

            img {
                margin: 0 0 5px;
                display: block;

                @include media(">=tablet"){/*768*/
                    margin: 0 10px 0 0;
                }
            }
        }
    }

    .close-marker {
        margin: 0 auto;
    }
}

.packaging-items {
    display: flex;
    flex-wrap: wrap;
    margin: -16px 0 0 -16px;
    width: calc(100% + 16px);

    &.slick-initialized {
        overflow: hidden;
        margin: 0 -20px;
        width: auto;
        padding: 0 20px;

        .slick-list {
            padding: 0 20px;
            margin: 0 -20px;
            min-width: 100%;
        }
    }

    @include media(">=medium-desktop") {/*1440*/
        margin: -32px 0 0 -32px;
        width: calc(100% + 32px);
    }

    .slick-slide {
        padding: 0 16px 0 0;

        .packaging-item {
            margin: 0;
        }
    }
}

.packaging-item {
    width: calc(100% - 16px);
    margin: 16px 0 0 16px;
    background: $color-white;

    @include media(">=tablet") {/*768*/
        width: calc(50% - 16px);
    }

    @include media(">=desktop") {/*1200*/
        width: calc(33.33% - 16px);
    }

    @include media(">=medium-desktop") {/*1440*/
        width: calc(33.33% - 32px);
        margin: 32px 0 0 32px;
    }

    .picture {
        height: 200px;
        display: flex;
        margin: auto;
        background: $color-light-gray;

        @include media(">=desktop"){/*1200*/
            height: 326px;
        }

        > span {
            width: 80px;
            height: 80px;
            display: flex;
            padding: 16px;
            margin: auto;
            border-radius: 50%;
            background: $color-dark;

            @include media(">=desktop"){/*1200*/
                padding: 30px;
                width: 166px;
                height: 166px;
            }
        }

        img {
            margin: auto;
        }
    }

    .text {
        padding: 10px;
        color: rgba($color-dark, .4);

        @include media(">=tablet") {/*768*/
            padding: 15px;
        }

        @include media(">=desktop") {/*1200*/
            padding: 28px 30px;
        }

        > span {
            display: block
        }
    }

    .title {
        @include title-h5;
        font-weight: 400;
        font-family: $ff-pp-fragment-sans;
    }
}

/*packaging end*/