.upcoming-reports-section {
    background-color: rgba($color-creame, .2);
    padding: $indent-section-mobile 0;
    position: relative;

    @include media('>=desktop') {
        padding: $indent-section-desktop 0;
    }

    @include media('>=medium-desktop') {
        padding: $indent-section-medium-desktop 0;
    }

    @include media('>=large-desktop') {
        padding: $indent-section-large-desktop 0;
    }

    .container {
        position: relative;
        z-index: 1;
    }
}

.upcoming-reports-decor {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;

    img {
        position: absolute;
        bottom: 0;
        right: 0;
        object-fit: contain;
        width: 24.219vw;

        &.top {
            top: 0;
            left: 0;
            width: 16.823vw;
        }
    }
}

.upcoming-reports-inner {
    @include media('>=tablet') {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        column-gap: 30px;
    }

    @include media('>=desktop') {
        column-gap: 80px;
    }

    @include media('>=medium-desktop') {
        column-gap: 130px;
    }

    @include media('>=large-desktop') {
        column-gap: 211px;
    }

    .section-info, .reports-calendar {
        @include media('>=tablet') {
            width: calc(50% - 15px);
        }

        @include media('>=tablet-landscape') {
            width: 40.3%;
        }

        @include media('>=desktop') {
            width: 40.5%;
        }
    }

    .section-info {
        text-align: left;

        @include media('>=tablet-landscape') {
            width: calc(59.7% - 30px);
        }

        @include media('>=desktop') {
            width: calc(59.5% - 80px);
        }

        @include media('>=medium-desktop') {
            width: calc(59.5% - 130px);
        }

        @include media('>=large-desktop') {
            width: calc(59.5% - 211px);
        }

        h2 {
            @include media('>=large-desktop') {
                margin-bottom: 29px;
            }
        }

        & > p {
            @include media('>=desktop') {
                font-size: 20px;
                line-height: 28px;
            }

            @include media('>=large-desktop') {
                font-size: 24px;
                line-height: 34px;
            }
        }
    }
}

.reports-calendar {
    margin-top: 40px;

    @include media('>=tablet') {
        margin-top: 0;
    }
}

.upcoming-reports-holder {
    margin-top: 33px;
}

.upcoming-report {
    margin-bottom: 15px;
    user-select: none;

    @include media('>=tablet') {
        margin-bottom: 26px;
    }

    &:last-child {
        margin-bottom: 0;
    }

    h5 {
        font-weight: 400;
        font-family: $ff-pp-fragment-sans;
        margin-bottom: 8px;
    }
}