
/*cooking-reverse*/

.cooking-section {
    .section-info {
        text-align: center;

        p {
            @include media(">=tablet") {/*768*/
                max-width: 640px;
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
}

.cooking-reverse {
    @include media(">=tablet-landscape") {/*768*/
        display: flex;
    }

    &.reverse {
        @include media(">=tablet-landscape") {/*768*/
            flex-direction: row-reverse;
        }

        .cooking-reverse-picture {
            flex-direction: row-reverse;

            @include media(">=tablet-landscape") {/*1024*/
                margin: 0 -32px 0 0;
                padding: 0 0 0 74px;
            }

            .picture {
                @include media(">=tablet-landscape") {/*1024*/
                    margin: 0 32px 0 0;
                }
            }
        }
    }
}

.cooking-reverse-picture {
    display: flex;
    align-items: flex-end;
    margin: 0 auto 32px;
    max-width: 510px;

    @include media(">=tablet-landscape") {/*1024*/
        flex: auto;
        margin: 0 0 0 -32px;
        padding: 0 74px 0 0;
        max-width: 100%;
    }

    .picture {
        margin: 0 10px;
        flex: 1;

        @include media(">=tablet-landscape") {/*1024*/
            margin: 0 0 0 32px;
            flex: auto;
        }

        &:first-child {
            flex: 0 0 50%;
            position: relative;

            @include media(">=tablet") {/*768*/
                flex: 0 0 62%;
            }

            @include media(">=medium-desktop") {/*1440*/
                flex: 0 0 72%;
            }

            @include media(">=large-desktop") {/*1800*/
                height: 948px;
            }

            &::after {
                content: '';
                display: block;
                padding-top: 122.83%;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                position: absolute;
                top: 0;
                left: 0;
            }
        }

        &:nth-child(2) {
            img {
                aspect-ratio: 1 / 1;
                height: 100%;
                object-fit: cover;
                width: 100%;
            }
        }
    }

    img {
        width: 100%;
    }
}

.cooking-reverse-text {

    @include media(">=tablet-landscape") {/*768*/
        align-self: center;
        flex: 0 0 462px;
        padding-top: 5px;
    }

    h3, .h3 {
        margin: 0 0 18px;

        @include media(">=tablet") {/*768*/
            margin: 0 0 30px;
        }
    }

    p {
        margin: 0 0 10px;
    }

    > *:last-child {
        margin-bottom: 0;
    }
}

/*cooking-reverse end*/