.three-img-section {

	.section-info {
		text-align: left;
		max-width: 100%;
		margin-left: 0;
		margin-right: 0;
		@include media('<desktop') {
			font-size: 16px;
			line-height: 1.62;
		}

		.sub-text {
			margin-bottom: 24px;
		}

		p {
			margin-left: 0;
			margin-right: 0;

			//@include media('>=large-desktop') {
			//    font-size: 24px;
			//    line-height: 34px;
			//}

		}

		h2 {
			@include media('>=large-desktop') {
				margin-bottom: 28px;
			}
		}
	}

	.buttons-holder {
		margin-top: 10px;

		@include media('>=desktop') {
			margin-top: 20px;
		}
	}

	&.reverse {

		.two-sides-row {
			@include media('>=tablet') {
				flex-direction: row-reverse;
			}
		}
	}

	&.has-statistics {
		.right-side {
			display: block;
			background-color: rgba($color-creame, .2);
			margin-top: 40px;
			height: auto !important;

			@include media('>=tablet') {
				margin-top: 0;
			}

			&::before {
				display: none;
			}
		}

		.section-info {

			p {
				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		& + .has-statistics {

			.two-sides-row {
				@include media('<tablet') {
					flex-direction: column-reverse;
					row-gap: 45px;
				}
		}
		}
	}

	&.animation {

		.statistics-item {
			transform: translateY(150px);
			opacity: 0;
			transition: transform 1s ease, opacity .4s linear;

			&:nth-child(2) {
				transition-delay: .15s;
			}

			&:nth-child(3) {
				transition-delay: .30s;
			}

			&:nth-child(4) {
				transition-delay: .45s;
			}

			&:nth-child(5) {
				transition-delay: .60s;
			}

			&:nth-child(6) {
				transition-delay: .75s;
			}
		}

		&.animated {

			.statistics-item {
				transform: translateY(0);
				opacity: 1;
			}
		}
	}

	&.has-contain-big-img {

		.two-sides-row {
			@include media('>=tablet') {
				align-items: flex-start;
			}
		}

		.right-side {

			img {

				@include media('>=tablet') {
					height: auto;
					object-fit: contain;
				}
			}
		}
	}
}

.left-side {
	@include media('>=tablet') {
		width: calc(51% - 20px);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	@include media('>=desktop') {
		width: calc(51% - 60px);
	}

	@include media('>=medium-desktop') {
		width: calc(51% - 100px);
	}

	@include media('>=large-desktop') {
		width: calc(51% - 166px);
	}
}


.right-side {
	display: none;
	position: relative;

	&::before {
		content: '';
		padding-top: 123%;
		display: block;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		position: absolute;
		top: 0;
		left: 0;

		@include media('>=mobile-landscape') {
			object-fit: contain;
		}

		@include media('>=tablet') {
			object-fit: cover;
		}
	}

	@include media('>=tablet') {
		display: block;
		width: 49%;
	}
}

.two-sides-row {
	@include media('>=tablet') {
		display: flex;
		flex-direction: row;
		column-gap: 20px;
	}

	@include media('>=desktop') {
		column-gap: 60px;
	}

	@include media('>=medium-desktop') {
		column-gap: 100px;
	}

	@include media('>=large-desktop') {
		column-gap: 166px;
	}

	&.one-picture {
		display: flex;
		flex-direction: column;

		@include media(">=tablet") { /*768*/
			flex-direction: row;
		}

		.left-side {
			align-self: center;
		}

		.right-side {
			@include media("<tablet") {
				display: block;
				order: -1;
				margin: 0 0 40px;
				height: 420px;
			}
		}

		.section-info {
			p:not(.sub-text) {
				margin: 0 0 20px;
				//@include media('>=large-desktop') { /*1800*/
				//	font-size: 18px;
				//	line-height: 1.56;
				//}

				&:last-child {
					margin-bottom: 0;
				}
			}

			p {
				letter-spacing: .05px;
			}
		}
	}

	&.home {
		position: relative;

		@include media('>=desktop') {
			column-gap: 125px;
		}

		@include media('>=medium-desktop') {
			column-gap: 162px;
		}

		@include media('>=large-desktop') {
			column-gap: 222px;
		}

		.two-sides-row-bg {
			@include media('<tablet') {
				opacity: 0;
			}
		}

		.left-side {
			flex-direction: column-reverse;
			display: flex;
			position: relative;
			z-index: 1;

			@include media('>=desktop') {
				width: calc(60% - 118px);
			}

			@include media('>=medium-desktop') {
				width: calc(60% - 160px);
			}

			@include media('>=large-desktop') {
				width: calc(54% - 136px);
			}
		}

		.images-holder {
			margin-top: 0;
			align-items: flex-start;
			flex-direction: row-reverse;
			justify-content: flex-end;
		}

		.section-info {
			margin-top: 41px;

			@include media('>=tablet') {
				margin-top: 35px;
			}

			@include media('>=desktop') {
				margin-top: 71px;
			}

			@include media('>=medium-desktop') {
				margin-top: 144px;
			}

			& > :last-child {
				@include media('>=tablet') {
					margin-bottom: -20px;
				}
			}

			h2 {
				margin-bottom: 22px;

				@include media('>=large-desktop') {
					margin-bottom: 26px;
				}
			}
		}

		p:not(.sub-text) {
			@include media('<desktop') { /*1200*/
				font-size: 18px;
				line-height: 1.56;
			}

			@include media('>=large-desktop') { /*1800*/
				font-size: 18px;
				line-height: 1.56;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		.img-wrap {
			max-width: 236px;
			width: calc(50% - 4px);

			@include media('>=tablet') {
				max-width: 160px;
				width: calc(50% - 10px);
			}

			@include media('>=medium-desktop') {
				max-width: 180px;
			}

			@include media('>=large-desktop') {
				max-width: 236px;
			}

			&::after {
				padding-top: 100%;
			}

			&.big {
				width: 48%;

				@include media('>=tablet') {
					width: 50%;
				}

				&::after {
					padding-top: 150%;
				}
			}
		}

		.right-side {
			position: relative;
			z-index: 1;

			@include media('>=desktop') {
				width: 40.5%;
			}

			&::before {
				padding-top: 139.5%;
			}
		}

		.btn {
			@include media('>=desktop') {
				min-width: 180px;
			}

			@include media('>=large-desktop') {
				min-width: 159px;
			}
		}

		.buttons-holder {
			margin-top: 20px;

			@include media('>=desktop') {
				margin-top: 11px;
			}

			@include media('>=medium-desktop') {
				margin-top: -20px;
			}

			@include media('>=large-desktop') {
				margin-top: 18px;
			}
		}
	}

	&.has-bg {

		@include media('>=tablet') {
			padding: 50px 0;
		}

		@include media('>=desktop') {
			padding: 90px 0 56px;
		}

		@include media('>=medium-desktop') {
			padding: 94px 0 105px;
		}

		@include media('>=large-desktop') {
			padding: 196px 0 205px;
		}
	}

	&.stretched-left-side {
		@include media('>=large-desktop') {
			column-gap: 116px;
		}

		.left-side {
			@include media('>=large-desktop') {
				width: calc(51% - 116px);
			}

			.section-info {
				p {
					@include media('>=large-desktop') {
						padding-right: 50px;
					}
				}
			}
		}
	}
}

.images-holder {
	display: flex;
	flex-direction: row;
	column-gap: 10px;
	margin-top: 35px;

	@include media('>=desktop') {
		column-gap: 32px;
	}

	.img-wrap {
		position: relative;
		width: calc(42% - 10px);

		@include media('>=desktop') {
			width: calc(42% - 32px);
		}

		&::after {
			content: '';
			padding-top: 60%;
			display: block;

			@include media('>=desktop') {
				padding-top: 64%;
			}
		}

		img {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 100%;
			height: 100%;
			transform: translate3d(-50%, -50%, 1px);
			object-fit: cover;
		}

		&.big {
			width: 58%;
		}
	}
}

.dark-bullet {
	margin-top: 20px;

	@include media('>=desktop') {
		margin-top: 32px;
	}

	li {
		padding-left: 46px;
		position: relative;
		margin-bottom: 15px;
		letter-spacing: .3px;

		&:last-child {
			margin-bottom: 0;
		}

		@include media('>=desktop') {
			margin-bottom: 25px;
		}

		&::before {
			content: '';
			width: 30px;
			height: 30px;
			border-radius: 50%;
			background-image: url('../images/dark-bullet.svg');
			background-size: contain;
			background-position: center;
			position: absolute;
			top: -1px;
			left: 0;
		}
	}
}

.statistics-block {
	padding: 15px;

	@include media('>=tablet') {
		padding: 30px;
	}

	@include media('>=medium-desktop') {
		padding: 50px;
	}

	@include media('>=large-desktop') {
		padding: 80px;
	}
}

.statistics-item {
	padding-bottom: 20px;
	border-bottom: 1px solid rgba($color-dark, .1);
	margin-bottom: 20px;

	@include media('>=desktop') {
		padding-bottom: 30px;
		margin-bottom: 20px;
	}

	@include media('>=medium-desktop') {
		margin-bottom: 35px;
	}

	@include media('>=large-desktop') {
		padding-bottom: 41px;
		margin-bottom: 50px;
	}

	&:last-child {
		padding-bottom: 0;
		margin-bottom: 0;
		border-bottom: none;
	}

	.h2 {
		@include media('>=tablet') {
			margin-bottom: 11px;
		}
	}
}

.health-method-img-holder {
	width: 72px;
	height: 72px;
	padding: 8px;
	border-radius: 50%;
	background-color: $color-dark;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0 auto 24px;

	@include media('>=mobile-landscape') {
		margin-left: 0;
	}

	img, svg {
		max-width: 100%;
		max-height: 100%;
		object-fit: contain;

		@include media('>=medium-desktop') {
			transform: scale(1.416);
		}
	}

	@include media('>=medium-desktop') {
		width: 102px;
		height: 102px;
	}
}

.health-methods {
	margin: 15px;
	overflow: hidden;

	@include media('>=mobile-landscape') {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center;
		column-gap: 20px;
		row-gap: 40px;
	}

	@include media('>=tablet') {
		margin: 30px;
	}

	@include media('>=tablet-landscape') {
		overflow: hidden;
	}

	@include media('>=desktop') {
		row-gap: 80px;
	}

	@include media('>=medium-desktop') {
		margin: 50px;
		justify-content: space-between;
	}

	@include media('>=large-desktop') {
		margin: 80px;
	}
}

.health-method {
	text-align: center;
	margin-bottom: 35px;

	&:nth-child(odd) {

		@include media('>=tablet-landscape') {
			position: relative;

			&::after {
				content: '';
				width: 100vw;
				bottom: -20px;
				left: 0;
				height: 1px;
				background-color: rgba($color-dark, .1);
				position: absolute;
			}
		}

		@include media('>=desktop') {

			&::after {
				bottom: -40px;
			}
		}
	}

	&:last-child {
		margin-bottom: 0;

		&::after {
			display: none;
		}
	}

	@include media('>=mobile-landscape') {
		width: 50%;
		max-width: 249px;
		text-align: left;
		margin-bottom: 0;
	}

	@include media('>=tablet') {
		width: 100%;
		max-width: 100%;
	}

	@include media('>=tablet-landscape') {
		width: 100%;
		max-width: 47%;
	}

	@include media('>=medium-desktop') {
		max-width: 249px;
	}

	p {
		@include media('>=medium-desktop') {
			font-size: 24px;
			line-height: 34px;
		}
	}
}

.two-sides-row-bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 0;
	opacity: .15;

	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
}