.cycle {
    margin-top: 50px;

    @include media('>=mobile-landscape') {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        column-gap: 20px;
        row-gap: 20px;
    }

    @include media('>=tablet') {
        column-gap: 0;
    }

    @include media('>=desktop') {
        margin-top: 80px;
        column-gap: 0;
    }

    @include media('>=large-desktop') {
        margin-top: 118px;
    }

    &:first-child {
        margin-top: 0;
    }
}

.cycle-item {
    margin-bottom: 40px;

    @include media('>=mobile-landscape') {
        margin-bottom: 0;
        width: calc(50% - 10px);
    }

    @include media('>=tablet') {
        width: 25%;
    }

    &.reversed {

        @include media('>=tablet') {
            display: flex;
            flex-direction: column-reverse;
        }

        .cycle-img-holder {

            @include media('>=tablet') {
                border-bottom-left-radius: 50%;
                border-bottom-right-radius: 50%;
                border-top-left-radius: 0;
                border-top-right-radius: 0;
            }
        }

        .cycle-item-text {

            @include media('>=tablet') {
                padding: 0 10px 15px;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
            }

            @include media('>=desktop') {
                padding: 15px 30px 40px;
            }

            @include media('>=large-desktop') {
                padding: 15px 72px 45px;
            }
        }
    }
}

.cycle-img-holder {
    position: relative;
    border-radius: 50%;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    overflow: hidden;

    &::after {
        content: '';
        display: block;
        padding-top: 99%;

        @include media('>=tablet') {
            padding-top: 114%;
        }
    }

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.cycle-item-text-inner {
    position: relative;

    &::after {

        @include media('>=tablet') {
            content: '';
            display: block;
            padding-top: 114%;
        }
    }
}

.cycle-item-text {
    padding: 15px 5px 0;
    text-align: center;

    @include media('>=tablet') {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        padding: 15px 10px 0;
    }

    @include media('>=desktop') {
        padding: 40px 30px 0;
    }

    @include media('>=large-desktop') {
        padding: 52px 72px 0;
    }

    h4 {
        margin-bottom: 10px;

        @include media('>=desktop') {
            margin-bottom: 15px;
        }
    }

    p {
        font-size: 16px;
        line-height: 26px;

        @include media('>=desktop') {
            font-size: 18px;
            line-height: 28px;
        }
    }
}