
/*map-section*/

.map-section {
    .section-info {
        margin-bottom: 40px;

        @include media(">=desktop"){/*1200*/
            margin-bottom: 78px;
        }

        h2, .h2 {
            @include media(">=large-desktop"){/*1800*/
                max-width: 700px;
                margin: 0 auto 27px;
            }
        }
    }
    
}

.map {
    margin: 0 auto 20px;
    @include media(">=tablet"){/*768*/
        max-width: 1308px;
        margin-bottom: 39px;
    }

    svg {
        width: 100%;
        height: auto;
    }

    path {
        fill: $color-creame;
        fill-opacity: 0.3;
        stroke: $color-dark;
        stroke-opacity: 0.05;
        stroke-width: 0.5;
        transition: all $transition-duration linear;
        cursor: pointer;

        .no-touch &:hover {
            fill-opacity: 1;
        }
    }
}

.map-location {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 10px;
    column-gap: 20px;

    @include media(">=desktop"){/*1200*/
        row-gap: 16px;
        column-gap: 32px;
        max-width: 1335px;
        margin: 0 auto;
    }

    button {
        border: 0;
        background: none;
        white-space: nowrap;
        margin: 0;
        padding: 0;
        cursor: pointer;
        font-size: 14px;
        line-height: 1.56;
        font-weight: 400;
        color: rgba($color-dark, .2);
        transition: all $transition-duration linear;

        @include media('>=desktop') { /*1200*/
            font-size: 20px;
            line-height: 1.4;
        }

        @include media('>=large-desktop') { /*1800*/
            font-size: 24px;
            line-height: 1.33;
        }

        &.active,
        .no-touch &:hover {
            color: $color-dark;
        }
    }
}

/*map-section end*/