/* Slider */
.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    padding: 0;
    margin: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}

.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: flex;

    &::before,
    &::after {
        content: "";
        display: table;
    }

    &::after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}

.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }

    img {
        display: block;
        object-fit: cover;
        width: 100%;
    }

    .slick-initialized & {
        display: block;
    }
}
.slick-arrow.slick-hidden {
    display: none;
}

.slick-dots {
    padding-top: 40px;
    display: flex;
    flex-wrap: wrap;
	flex-direction: row;
    justify-content: center;

    li {
        margin: 0 4px 4px;
        line-height: 0;

        @include media(">=desktop") {/*1200*/
            margin: 0 5px 6px;
        }

        button {
            cursor: pointer;
            border-radius: 50%;
            width: 10px;
            height: 10px;
            padding: 0;
            text-indent: -9999px;
            margin: 0;
            border: 0;
            background: rgba($color-white, .5);
            transition: all $transition-duration linear;
            font:400 12px/22px $ff-pp-fragment-sans;

            @include media(">=desktop") {/*1200*/
                width: 8px;
                height: 8px;
            }
        }

        &.slick-active {
            button {
                background: $color-white;
            }
        }
    }
}

.numbered {
    .slick-dots {
        align-items: center;
        justify-content: center;
        column-gap: 50px;

        li {
            position: relative;
            margin: 0;

            &::after {
                content: '';
                position: absolute;
                top: 50%;
                right: -45px;
                width: 40px;
                height: 1px;
                background-color: rgba($color-dark, .1);
            }

            &:last-child {
                &::after {
                    display: none;
                }
            }

            button {
                width: auto;
                height: auto;
                background: none;
                text-indent: 0;
            }
        }
    }

    .slick-active {

        button {
            color: $color-dark;
        }
    }

    button {
        border: none;
        background-color: rgba(0,0,0,0);
        color: rgba($color-dark, .5);
        transition: color .3s ease;
        cursor: pointer;

        .no-touch & {
            &:hover {
                color: $color-dark;
            }
        }
    }

    &.short-lines {
        .slick-dots {
            column-gap: 30px;

            li {
                &::after {
                    width: 20px;
                    right: -25px;
                }
            }
        }
    }
}