/*specification-sheet*/

.specification-sheet-tabs {
    h3, .h3 {
        position: relative;
        z-index: 2;
        margin: 0 0 30px;

        @include media(">=desktop") { /*1200*/
            margin: 0 0 50px;
        }
    }

    .tabs {
        @include media(">=desktop") { /*1200*/
            padding: 0 0 32px;
        }
    }
}

.specification-sheet-section {
    background: $color-white;
    position: relative;

    .product-description-section + & {
        @include media(">=large-desktop") { /*1800*/
            padding-top: 38px;
        }

        &:before {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            background: $color-beige;
            content: "";
            height: 50px;
            z-index: 0;

            @include media(">=desktop") {/*1200*/
                height: 163px;
            }
        }
    }

    .table-adaptive {
        margin: 0 0 16px;
        border: 1px solid #e1e8e8;

        // @include media('<tablet') {
        //     overflow-x: auto;
        // }

        @include media(">=tablet") {/*768*/
            margin-top: 33px;
        }

        &.jcf-scrollable {
            @include media('<tablet') {
                height: auto !important;
                width: 100% !important;
            }
        }

        td,
        th {
            &:first-child {
                @include media("<tablet") {
                    padding-top: 20px;
                }
                @include media(">=tablet") {/*768*/
                    width: 34%;
                }
            }

            &:last-child {
                @include media("<tablet") {
                    padding-bottom: 20px;
                }
            }

            &:not(:first-child) {
                @include media(">=tablet") {/*768*/
                    text-align: center;
                }
            }
        }

        thead {
            @include media('<tablet') {
                display: table-header-group;
            }

            th {
                @include media('<tablet') {
                    min-width: 150px;
                    display: table-cell;

                    &:first-child {
                        padding-top: 10px;
                    }

                    &:last-child {
                        padding-bottom: 10px;
                    }
                }
            }
        }

        table {
            border: none;

            tbody {

                tr {
                    @include media('<tablet') {
                        display: table-row;
                    }
                }

                td {
                    @include media('<tablet') {
                        display: table-cell;
                    }
                }
            }

            td, th {
                line-height: 1.5;

                @include media('>=desktop') {
                    line-height: 26px;
                }
            }
        }
    }

    .jcf-scrollbar-horizontal {
        @include media('<tablet') {
            height: 5px;
            display: flex;
            width: 100% !important;

            .jcf-scrollbar-dec, .jcf-scrollbar-inc {
                height: 5px;
                width: 5px;
            }

            .jcf-scrollbar-slider {
                height: 5px;
                width: 100% !important;
            }

            .jcf-scrollbar-handle {
                height: 5px;
            }
        }
    }

    .jcf-scrollbar-vertical {
        display: none;
        height: 0 !important;
        width: 0 !important;
        visibility: hidden;
        top: auto;
    }

    .buttons {
        margin-top: 30px;

        @include media(">=tablet") {/*768*/
            margin-top: 48px;
        }
    }
}

.specification-sheet-cards {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 0 -16px;
    width: calc(100% + 16px);
    @include media('>=tablet') {
        margin: -10px 0 0 -10px;
    }
    @include media('>=desktop') {
        margin: -16px 0 0 -16px;
    }
    @include media(">=medium-desktop") {/*1440*/
        margin: -32px 0 0 -32px;
        width: calc(100% + 32px);
    }
}

.specification-sheet-card {
    position: relative;
    width: calc(100% - 16px);
    cursor: pointer;
    overflow: hidden;
    margin: 0 0 0 16px;

    @include media(">=tablet") {/*768*/
        width: calc(25% - 10px);
        margin: 10px 0 0 10px;
    }

    @include media(">=desktop") {/*1200*/
        width: calc(25% - 16px);
        margin: 16px 0 0 16px;
    }

    @include media(">=medium-desktop") {/*1440*/
        width: calc(25% - 32px);
        margin: 32px 0 0 32px;
    }

    &.active,
    .no-touch &:hover {
        .mask {
            opacity: 0;
        }

        .btn {
            color: $color-dark;
            background-color: $color-white;
        }
    }

    &:before {
        display: block;
        content: "";
        height: 90px;
        @include media('>=tablet') {
            height: auto;
            padding-top: 65.45%;
        }
    }

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .mask {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba($color-dark, .5);
        transition: opacity $transition-duration linear;
    }

    .btn {
        position: absolute;
        top: 50%;
        left: 50%;
        max-width: 80%;
        color: $color-white;
        transform: translate(-50%, -50%);
        font-weight: 400;

        span {
            border-color: $color-white;
            min-width: 167px;
						@include media('>=tablet') {
								min-width: 140px;
								padding: 12px 15px;
						}
						@include media('>=tablet-landscape') {
								min-width: 167px;
						}
            @include media(">=desktop") {/*1200*/
                padding: 12px 20px 10px;
            }
        }

        .no-touch &:hover {
            color: $color-dark;
        }
    }
}

/*specification-sheet end*/