
/*diversity-section*/

.diversity-section {
    overflow: hidden;
    position: relative;

    .section-info {
        text-align: center;
        margin: 0 auto 20px;

        @include media(">=desktop"){/*1200*/
            margin-bottom: 80px;
        }

        @include media(">=large-desktop"){/*1800*/
            margin-bottom: 120px;
        }

        h2, .h2 {
            @include media(">=large-desktop"){/*1800*/
                margin-bottom: 28px;
            }
        }

        p {
            @include media(">=desktop"){/*1200*/
                margin-left: auto;
                margin-right: auto;
                max-width: 722px;
            }
        }
    }
}

.diversity-picture {
    text-align: center;
    line-height: 0;
    
    img {
        width: 100vw;
        max-height: 960px;
        object-fit: cover;
    }
}

.diversity-numbers {

    @include media(">=tablet"){/*768*/
        display: flex;
        flex-wrap: wrap;
    }

    @include media(">=medium-desktop") {/*1440*/
        margin: 0 auto;
        max-width: 1920px;
        position: absolute;
        bottom: 0;
        width: 100%;
        left: 50%;
        transform: translateX(-50%);
    }

    .col {
        position: relative;
        padding: 20px;
        display: flex;
        flex-wrap: wrap;
        background: $color-light-gray;

        @include media(">=tablet"){/*768*/
            width: 50%;
        }

        @include media(">=desktop"){/*1200*/
            padding: 40px 64px;
        }

        &:before {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: -9999px;
            background: $color-light-gray;
            content: "";
            z-index: -1;
        }

        &:nth-child(even) {
            background: $color-gray;

            &:before {
                right: -9999px;
                background: $color-gray;
                left: 0;
            }
        }
    }

    .holder {
        display: flex;
        align-self: center;
        padding: 0 10px 0 0;

        .h2 {
            padding-right: 20px;

            @include media(">=desktop"){/*1200*/
                padding: 8px 40px 0 0;
            }
        }

        .sub-text {
            align-self: flex-end;
            margin: 0 0 10px;
        }
    }

    .icon {
        margin: 0 0 0 auto;
        display: flex;
        border-radius: 50%;
        background: $color-dark;
        width: 60px;
        height: 60px;
        padding: 10px;

        @include media(">=tablet"){/*768*/
            width: 80px;
            height: 80px;
            padding: 15px;
        }

        @include media(">=desktop"){/*1200*/
            width: 124px;
            height: 124px;
            padding: 20px;
        }

        img {
            margin: auto;
            max-width: 100%;
            max-height: 100%;
        }
    }
}

/*diversity-section end*/