
/*pagination*/
.pagination {
	display: flex;
	flex-shrink: 0;
	width: 100%;
	justify-content: center;
	align-items: center;
	padding: 3px;
	margin-top: 40px;

	@include media('>=desktop') { /*1200*/
		margin-top: 64px;
	}

	.wp-pagenavi {
		display: flex;
		flex-shrink: 0;
		flex-wrap: wrap;
		position: relative;
		padding: 0 50px;
		max-width: 100%;
		justify-content: center;

		@include media('>=tablet') { /*768*/
		}

		a,
		span {
			position: relative;
			z-index: 10;
			display: flex;
			flex-shrink: 0;
			border-radius: 50%;
			min-width: 32px;
			height: 32px;
			font-weight: 400;
			color: $color-dark;
			text-align: center;
			align-items: center;
			justify-content: center;
			font-size: 14px;
			margin: 0 5px;
			padding: 5px 2px 1px;

			@include media('>=desktop') { /*1200*/
				padding-top: 5px;
				min-width: 48px;
				height: 48px;
			}

			@include media('>=medium-desktop'){ /*1440*/
				font-size: 18px;
			}

			&:after {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				background: $color-green;
				transition: opacity $transition-duration linear;
				z-index: -1;
				opacity: 0;
				border-radius: 50%;
			}

			&.current, &.active {
				color: $color-white;

				&:after {
					opacity: 1;
				}
			}
		}

		& > a {
			.no-touch &:hover {
				color: $color-white;

				&:after {
					opacity: 1;
				}

				svg path {
					stroke: $color-white;
				}
			}
		}

		.prev,
		.next {
			padding: 0;
			position: absolute;
			top: 0;
			margin: 0;
			transition: opacity $transition-duration linear;
			@include media('>=tablet') {
				position: relative;
			}

			.no-touch &:hover {
				opacity: 1;

				&:not(.disabled) {
					background: $color-white;
				}
			}

			&.disabled {
				opacity: .6;
				pointer-events: none;
			}

			svg {
				position: relative;
				top: 0;
			}
		}

		.prev {
			left: 0;
			@include media('>=tablet') {
				margin-right: 36px;
			}
		}

		.next {
			right: 0;
			@include media('>=tablet') {
				margin-left: 36px;
			}
		}
	}
}


/*pagination end*/