
/*testimonual*/

.testimonial-section {
    overflow: hidden;
    background-color: rgba($color-creame, .2);
    padding-bottom: 30px;

    @include media('>=tablet') {
        background-color: rgba(0,0,0,0);
        padding-bottom: 0;
    }

    &.about {

        .text {
            @include media('>=tablet') {
                padding-bottom: 50px;
            }

            @include media('>=desktop') {
                padding-top: 256px;
            }

            @include media('>=medium-desktop') {
                padding-bottom: 100px;
            }
        }
    }
}

.testimonial-slider {

    .slick-list {
        overflow: visible;
    }

    .slick-dots {
        align-items: center;
        column-gap: 50px;
        margin: -60px 0 60px;
        max-width: 772px;

        @include media(">=tablet"){/*768*/
            margin: -120px 0 120px -5px;
            justify-content: flex-start;
        }

        @include media(">=desktop"){/*1200*/
            margin: -220px 0 220px -5px;
        }

        li {
            position: relative;

            &::after {
                margin-top: -2px;
                content: '';
                position: absolute;
                top: 50%;
                right: -50px;
                width: 40px;
                height: 1px;
                background-color: rgba($color-dark, .1);
            }

            &:last-child {
                &::after {
                    display: none;
                }
            }

            button {
                font: 12px/22px $ff-pp-fragment-sans;
                width: auto;
                height: auto;
                background: none;
                text-indent: 0;
                color: rgba($color-dark, .5);
            }

            &.slick-active {
                button {
                    color: $color-dark;
                }
            }
        }
    }

    .testimonial {
        position: relative;
        padding: 0 0 60px;

        @include media(">=desktop"){/*1200*/
            padding-bottom: 200px;
        }
    }

}

.testimonial {
    position: relative;

    @include media(">=tablet"){/*768*/
        display: flex !important;
        align-items: flex-end;
        padding: 0 0 60px;
    }

    @include media(">=desktop"){/*1200*/
        padding-bottom: 160px;
    }

    blockquote,
    .text {
        padding: 40px 0 23px;
        max-width: 812px;
        border: 0;
        text-align: left;

        @include media(">=tablet"){/*768*/
            padding: 80px 40px 43px 0;
        }

        @include media(">=desktop"){/*1200*/
            padding-top: 160px;
        }

        &:before {
            position: absolute;
            bottom: -100px;
            top: 0;
            left: -9999px;
            right: -9999px;
            background: rgba($color-creame, .2);
            content: "";
            z-index: -1;
            display: none;

            @include media(">=tablet"){/*768*/
                display: block;
            }

            @include media(">=desktop"){/*1200*/
                padding-top: 200px;
            }
        }

        blockquote {
            padding: 0;

            q, p {
                font-size:18px;
                line-height: 1.56;

                @include media('>=desktop') { /*1200*/
                    font-size: 20px;
                    line-height: 1.4;
                }

                @include media('>=large-desktop') { /*1800*/
                    font-size: 24px;
                    line-height: 1.33;
                }
            }

            &:before {
                display: none;
            }
        }
    }

    .text {
        position: relative;
        max-width: 678px;

        @include media(">=tablet"){/*768*/
            padding-bottom: 0;
        }

        @include media(">=desktop"){/*1200*/
            padding-top: 167px;
        }

        &:before {
            bottom: -160px;
        }

        p {
            margin: 0 0 10px;

            @include media(">=desktop") {/*1200*/
                margin: 0 0 20px;
            }

            &:last-child {
                margin-bottom: 0;
            }

            &.sub-text {
                margin-bottom: 40px;

                @include media('>=desktop') {
                    margin-bottom: 80px;
                }
            }
        }

        h2 {
            margin-bottom: 15px;

            @include media('>=desktop') {
                margin-bottom: 26px;
            }
        }

        .h3, h3 {
            margin-top: 20px;

            @include media(">=desktop") {/*1200*/
                margin-top: 28px;
            }
        }

        .h4, h4 {
            margin: 0 0 20px;
            @include media(">=desktop") {/*1200*/
                margin: 0 0 40px;
            }
            @include media('<large-desktop') {
                font-size: 36px;
								line-height: 44px;
            }
        }

        .buttons {
            margin-top: 20px;

            @include media(">=desktop") {/*1200*/
                margin-top: 36px;
            }
        }
    }

    blockquote {

        &::before {
            bottom: -200px;
            // @include media(">=large-desktop"){/*1800*/
            //     bottom: -200px;
            // }
        }

        q, p {
            font-weight: 400;
        }
    }

    .picture {
        position: relative;
        z-index: 2;
        margin: 0 auto 20px;
        width: 80%;

        @include media(">=tablet"){/*768*/
            margin: 0 0 0 auto;
            flex: 0 0 40.6%;
            width: auto;
        }

        video {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        &.cover {
            max-height: 887px;
            overflow: hidden;

            &::after {
                content: '';
                display: block;
                padding-top: 138.8%;
            }

            img, video {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    .video-wrap{
        position: relative;
        z-index: 20;
				margin: 0 auto 20px;
				width: 80%;
				height: 345px;
				@include media(">=tablet"){/*768*/
					margin: 0 0 0 auto;
					flex: 0 0 40.6%;
					width: auto;
					height: 411px;
				}
				@include media('>=tablet-landscape') {
					height: 555px;
				}
				@include media('>=desktop') {
					height: 632px;
				}
				@include media('>=medium-desktop') {
					height: 700px;
				}
				@include media('>=large-desktop') {
						min-height: 887px;
				}
				video {
					position: absolute;
					left: 0;
					top: 0;
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
    }
		strong {
				font-weight: 700;
		}
    cite strong {
        font-weight: 400;
    }
}

/*testimonual end*/