.featured-news {

    .news-card {

        &.large, &.medium {

            .news-card-picture {

                @include media('>=tablet') {
                    height: 300px;
                }

                @include media('>=desktop') {
                    height: 350px;
                }

                @include media('>=large-desktop') {
                    height: 416px;
                }
            }
        }
    }

    .slick-slider {
        @include media('<tablet') {
            margin-left: -20px;
            margin-right: -20px;
        }

        .news-card {
            @include media('<tablet') {
                margin-bottom: 0;
                display: flex !important;
                flex-direction: column;
            }
        }

        .news-card-picture {
            @include media('<tablet') {
                position: relative;
                overflow: hidden;
            }
        }

        .slick-slide {
            height: inherit;

            & > div {
                height: 100%;
            }

            .news-card {
                height: 100%;
            }

            @include media('<tablet') {
                margin-right: 10px;
            }
        }
    }

    .slick-dots {
        li {
            button {
                background-color: $color-white;
            }

            &.slick-active {
                button {
                    background-color: $color-green;
                }
            }
        }
    }
}