.more-products-list {
    @include media('>=mobile-landscape') {
        display: flex;
        flex-direction: row;
        column-gap: 15px;
        row-gap: 32px;
        flex-wrap: wrap;
        justify-content: center;
    }

    @include media('>=tablet-landscape') {
        column-gap: 32px;
        row-gap: 32px;
    }

    .more-products-card {
        margin-bottom: 30px;

        @include media('>=mobile-landscape') {
            width: calc(50% - 8px);
            margin-bottom: 0;
        }

        @include media('>=tablet') {
            width: calc(100%/3 - 22px);
        }
    }

    .more-products-img {

        &::after {
            padding-top: 100%;
        }

        @include media('>=large-desktop') {
            height: 552px;
        }
    }
}

.preview-img {
    width: 50px;
    height: 50px;
    flex-shrink: 0;
    margin-left: 15px;
    border-radius: 50%;
    overflow: hidden;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    @include media('>=tablet') {
        width: 70px;
        height: 70px;
    }

    @include media('>=desktop') {
        width: 100px;
        height: 100px;
    }

    @include media('>=large-desktop') {
        width: 124px;
        height: 124px;
    }
}

.more-products-card {
    position: relative;

    & > a {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
    }

    &.single {
        .more-products-text {
            display: flex;
            align-items: center;
            flex-direction: row;
            justify-content: space-between;

            @include media('>=desktop') {
                padding: 40px 64px;
            }
        }
    }

    .no-touch &:hover {
        .more-products-img {
            img {
                transform: scale(1.05);
            }
        }
    }
}

.more-products-img {
    position: relative;
    overflow: hidden;
    max-height: 552px;

    &::after {
        content: '';
        display: block;
        padding-top: 80%;
    }

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform .5s ease;
    }
}

.more-products-text {
    background-color: $color-white;
    padding: 15px;
    text-align: center;

    @include media('>=desktop') {
        padding: 46px;
    }
}