
/*proof-numbers*/

.proof-numbers-section {
    .section-info {
        margin-bottom: 30px;

        @include media(">=desktop"){/*1200*/
            margin-bottom: 80px;
            max-width: 740px;
        }

        @include media(">=large-desktop"){/*1800*/
            margin-bottom: 120px;
        }

        p {
            @include media(">=desktop"){/*1200*/
                max-width: 640px;
                margin: 0 auto;
            }
        }
    }
}

.proof-numbers-row {
    & + & {
        margin: 49px 0 0;
        transform: scaleX(-1);

        .proof-number {
            transform: scaleX(-1);
        }
    }

    .slick-slide {
        padding: 0 25px;

        @include media(">=tablet"){/*768*/
            padding: 0 50px;
        }

        @include media(">=medium-desktop"){/*1440*/
            padding: 0 100px;
        }
    }
}

.proof-number {
    text-align: center;
    position: relative;
    min-width: 200px;

    @include media('>=tablet') {
        min-width: 400px;
    }

    .text {
        position: absolute;
        padding: 10px 5%;
        width: 80%;
        top: 42%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;

        @include media(">=desktop") {/*1200*/
            font-size: 18px;
            line-height: 20px;
        }

        @include media(">=large-desktop") {/*1800*/
            line-height: 1.42;
            font-size: 24px;
        }
    }

    .number {
        font-size: 100px;
        line-height: 100px;
        font-weight: 800;
        font-family: $ff-pp-fragment-serif;
        color: rgba($color-creame, .5);

        @include media(">=desktop"){/*1200*/
            font-size: 150px;
            line-height: 150px;
        }

        @include media(">=medium-desktop"){/*1440*/
            font-size: 200px;
            line-height: 200px;
        }
    }
}

/*proof-numbers end*/