
/*products*/
.products-filter {
    margin: 0 0 40px;

    @include media(">=tablet-landscape"){/*1024*/
        display: flex;
        margin: 0 0 62px;
    }

    .news-filters {
        margin: 0 0 20px;
        padding: 0;

        @include media(">=tablet-landscape"){/*1024*/
            padding: 0 20px 0 0;
            max-width: 600px;
            margin: 0;
        }
        @include media(">=large-desktop"){/*1800*/
            max-width: 680px;
        }

        .woocommerce & {
						@include media('>=tablet-landscape') {
								max-width: 780px;
						}
						@include media('>=desktop') {
								max-width: 820px;
						}
            @include media(">=large-desktop"){/*1800*/
                max-width: 880px;
            }
        }

        ul {
            @include media("<tablet") {
                column-gap: 5px;
                row-gap: 5px;
            }
        }

        .btn.small {
            @include media("<tablet") {
                min-height: 10px;
            }

            span {
                padding: 12px 22px 10px;

                @include media("<tablet") {
                    padding: 5px 15px;
                }
            }
        }
    }

    .woocommerce-ordering {
        @include media(">=tablet-landscape"){/*1024*/
            margin: auto 0 0 auto;
        }
    }

    .jcf-select {
        height: 48px;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 1px;
    }

    .jcf-select-text {
        font-size: 14px;
        line-height: 24px;
        text-transform: uppercase;
        min-width: 236px;
        padding-left: 21px;
        letter-spacing: 1px;
        border-color: $color-dark;
    }

    .jcf-select-opener {
        width: 39px;
    }
}

.jcf-select-orderby .jcf-option {
    padding-left: 20px;
    font-size: 14px;
    line-height: 24px;
}

.products-section {
    padding: 40px 0 0;

    @include media(">=tablet-landscape"){/*1024*/
        padding-top: 63px;
    }

    .woocommerce {
        &.columns-3 {
            ul.products li.product {
                width: 100%;

                @include media(">=tablet-landscape") {/*1024*/
                    width: calc(50% - 20px);
                    margin: 0 20px 20px 0;
                }

                @include media(">=desktop") {/*1200*/
                    width: calc(50% - 32px);
                    margin: 0 32px 40px 0;
                }

                @include media(">=medium-desktop") {/*1440*/
                    width: calc(33.33% - 32px);
                }
            }
        }
        .products {
            margin: 0 0 -20px;
            display: flex;
            flex-wrap: wrap;

            @include media(">=tablet-landscape") {/*1024*/
                margin-right: -20px;
            }

            @include media(">=desktop") {/*1200*/
                margin-right: -32px;
                margin-bottom: -40px;
            }

            .product {
                background: $color-white;
                position: relative;
                overflow: hidden;
                width: 100%;
                margin: 0 0 20px;

                @include media(">=tablet-landscape") {/*1024*/
                    width: calc(50% - 20px);
                    margin: 0 20px 20px 0;
                }

                @include media(">=desktop") {/*1200*/
                    width: calc(50% - 32px);
                    margin: 0 32px 40px 0;
                }

                @include media(">=medium-desktop") {/*1440*/
                    width: calc(33.33% - 32px);
                }

                .woocommerce-loop-product__title {
                    margin: 0;
                    padding: 0 0 10px;
                    flex: 1;
                    font: 400 18px/1.56 $ff-pp-fragment-sans;

                    @include media(">=mobile-landscape"){/*568*/
                        padding: 0 0 70px;
                        min-width: 190px;
                    }

                    @include media('>=desktop') { /*1200*/
                        font-size: 20px;
                        line-height: 1.4;
                    }

                    @include media('>=large-desktop') { /*1800*/
                        font-size: 24px;
                        line-height: 1.33;
                    }
                }

                .price {
                    margin: 0 0 16px;
                    color: $color-dark;
                    font: 400 18px/1.56 $ff-pp-fragment-sans;

                    @include media('>=desktop') { /*1200*/
                        font-size: 20px;
                        line-height: 1.4;
                    }

                    @include media('>=large-desktop') { /*1800*/
                        font-size: 24px;
                        line-height: 1.33;
                    }
                }

                .button {
                    margin: 25px 0 0;
                    padding: 0;
                    width: 100%;
                    overflow: hidden;
                    z-index: 1;

                    @include media(">=mobile-landscape"){
                        position: absolute;
                        width: auto;
                        left: 32px;
                        bottom: 35px;
                        margin: 0;
                    }
                }

                a {
                    img {
                        margin: auto;
                        width: auto;
                        height: auto;
                    }

                    > img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }

                .no-touch &:hover {
                    .product-picture {
                        a {
                            > img {
                                 transform: scale(1.1);
                             }

                            &::after {
                                //opacity: 1;
                            }
                        }
                    }

                    .button {
                        // color: $color-white;

                        &:before {
                            transform: scaleX(1);
                            transform-origin: bottom left;
                        }
                    }
                }
            }
        }

        .button {
            margin: 0;
            border: 0;
            text-transform: uppercase;
            color: $color-dark;
            background: transparent;
            cursor: pointer;
            -webkit-appearance: none;
            -webkit-border-radius: 40px;
            font-weight: 400;
            letter-spacing: 1px;
            border-radius: 40px;
            transition: color $transition-duration/2 linear, background-color $transition-duration/2 linear;
            position: relative;
            overflow: hidden;
            min-height: 48px;
            font-size: 12px;
            line-height: 1.83;

            @include media('>=desktop') { /*1200*/
                min-height: 44px;
            }

            @include media('>=large-desktop') { /*1800*/
                min-height: 48px;
                font-size: 14px;
                line-height: 1.71;
            }

            .no-touch &:hover{
                color: $color-white;
                background-color: $color-dark;
            }

            // &:before {
            //     background: $color-dark;
            // }

            &.added {

                &::after {
                    position: absolute;
                    top: 46%;
                    right: 20px;
                    transform: translateY(-50%);
                }
            }

            span {
                border-color: $color-dark;
                padding: 12px 24px 10px;
                min-width: 190px;
            }
        }
    }
}

.icon-feature {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    padding: 15px 15px 0;
    display: flex;
    flex-wrap: wrap;
    z-index: 1;

    span {
        margin: 5px;
        border-radius: 50%;
        padding: 5px;
        display: flex;
        border: 1px solid $color-white;
        width: 40px;
        height: 40px;
    }

    img,
    svg {
        margin: auto;
    }
}

.products {
    .product {
        .icon-feature {
            background: linear-gradient(180deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 100%);
            min-height: 100px;
        }

        .product-picture {
            position: relative;
            overflow: hidden;
            aspect-ratio: 4/4.33;

            @include media(">=tablet"){/*768*/
                aspect-ratio: auto;
                height: 500px;
            }

            @include media(">=tablet-landscape"){/*1024*/
                aspect-ratio: 4/4.33;
                height: auto;
            }

            a {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;

                > img {
                    transition: all $transition-duration linear;
                }

                &.woocommerce-LoopProduct-link {

                    &::after {
                        content: '';
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        left: 0;
                        z-index: 0;
                        backdrop-filter: blur(5px);
                        opacity: 0;
                        transition: opacity $transition-duration/2 ease;
                    }
                }
            }
        }

        .product-text {
            padding: 15px;
            position: relative;
            font-size: 18px;
            line-height: 1.56;
            font-weight: 400;

            @include media('>=mobile-landscape') { /*568*/
                display: flex;
                font-size: 20px;
                line-height: 1.4;
                padding: 32px 32px 27px;
                flex-wrap: wrap-reverse;
            }

            @include media('>=large-desktop') { /*1800*/
                font-size: 24px;
                line-height: 1.33;
            }

            a {
                &.added_to_cart {
                    padding-top: 0;
                    padding-bottom: 0.5em;
                    text-decoration: underline;

                    .no-touch &:hover {
                        text-decoration: none;
                    }
                }
            }

        }

        .holder {
            @include media(">=mobile-landscape"){/*568*/
                margin: 0 0 0 auto;
                width: 131px;
                padding: 0 0 0 10px;
            }
        }

        .pack-size {
            font-size: 14px;
            line-height: 24px;
            font-weight: 300;
            color: rgba($color-dark, .4);

            @include media(">=desktop"){/*1200*/
                font-size: 18px;
                line-height: 28px;
            }

            span {
                display: block;
                font-weight: 400;
                color: $color-dark;
            }
        }
    }
}
/*products end*/