.footer {
    
}

.footer-top {
    background: $color-white;
    padding: 82px 0 80px;

    @include media(">=desktop") { /*1200*/
        padding: 65px 0 44px;
    }

    @include media(">=medium-desktop") { /*1440*/
        padding: 81px 0 60px;
    }

    @include media(">=large-desktop") { /*1800*/
        padding: 101px 0 81px;
    }

    .container {
        @include media(">=tablet") {
            display: flex;
            flex-wrap: wrap;
        }
    }

}

.footer-nav {
    @include media(">=tablet") {
        flex: 0 0 100%;
    }

    @include media(">=desktop") { /*1200*/
        flex: 1;
    }
}

.footer-nav-list {
    @include media(">=tablet") { /*768*/
        display: flex;
        flex-wrap: wrap;
        margin: 0 -5% 0 0;
    }

    @include media(">=tablet-landscape") { /*1024*/
        margin: 0;
    }

    @include media(">=desktop") { /*1200*/
        flex-wrap: nowrap;
    }

    > li {
        @include media(">=tablet") { /*768*/
            width: 45%;
            margin: 0 5% 0 0;
        }

        @include media(">=desktop") {
            max-width: 236px;
            width: 20%;
            margin: 0 5% 0 0;
        }

        @include media(">=large-desktop") { /*1800*/
            margin: 0 6.5% 0 0;
        }

    }
}

.footer-nav-link {
    text-transform: uppercase;
    font-weight: 400;
    font-family: $ff-pp-fragment-sans;
    margin: 0 0 19px;
    padding: 0 0 17px;
    letter-spacing: 1px;
    background: none;
    border: 2px solid $color-beige;
    border-width: 0 0 2px;
    width: 100%;
    text-align: left;

    @include media(">=desktop") {/*1200*/
        font-size: 12px;
        line-height: 16px;
        padding: 0 0 12px;
    }

    @include media(">=medium-desktop") { /*1440*/
        font-size: 14px;
        line-height: 1.71;
    }

    @include media(">=large-desktop") { /*1800*/
        font-size: 16px;
        line-height: 1.62;
        padding: 0 0 17px;
    }


    a {
        position: relative;

        &:before {
            position: absolute;
            top: 100%;
            height: 1px;
            left: 0;
            width: 100%;
            background: currentColor;
            content: "";
            transition: transform $transition-duration/1.5 ease;
            transform-origin: bottom left;
            transform: scaleX(0);
        }

        .no-touch &:hover {
            text-decoration: none;

            &:before {
                transform: scaleX(1);
            }
        }
    }
}

.footer-contact {
    margin: 20px 0 0;

    @include media(">=tablet") { /*768*/
        margin: 0;
        width: 45%;
    }

    @include media(">=tablet-landscape") {/*1024*/
        width: 45;
    }

    @include media(">=desktop") { /*1200*/
        margin: 0 0 0 auto;
        width: 30%;
        max-width: 200px;
    }

    @include media(">=medium-desktop") { /*1440*/
        max-width: 232px;
    }

    @include media(">=large-desktop") {/*1800*/
        max-width: 268px;
    }

    li {
        margin: 10px 0 0;
        font-size: 16px;
        line-height: 1.62;
        font-weight: 400;

        @include media(">=desktop") { /*1200*/
            font-size: 12px;
            line-height: 1.83;
        }

        @include media(">=medium-desktop") { /*1440*/
            font-size: 14px;
            line-height: 1.71;
        }

        @include media(">=large-desktop") {/*1800*/
            font-size: 16px;
            line-height: 1.62;
        }
    }

    ul {
        margin: 1px 0 0;
        color: #7E8284;

        a {
            color: currentColor;

            .no-touch &:hover {
                text-decoration: underline;
            }
        }

        address {
            font-style: normal;
        }
    }
}

.footer-nav-inner {
    font-size: 16px;
    line-height: 1.62;
    font-weight: 400;
    color: #7E8284;
    margin: 0 0 36px;
    display: none;

    @include media(">=tablet") {/*768*/
        display: block !important;
        height: auto !important;
    }

    @include media(">=desktop") {/*1200*/
        font-size: 12px;
        line-height: 1.83;
    }

    @include media(">=medium-desktop") { /*1440*/
        font-size: 14px;
        line-height: 1.71;
    }

    @include media(">=large-desktop") { /*1800*/
        font-size: 16px;
        line-height: 1.62;
    }

    &.two-col {
        ul {
            display: grid;
            grid-template-columns: minmax(auto, .8fr) 2.1fr;

            @include media(">=desktop") {/*1200*/
                grid-template-columns: minmax(auto, 1fr) 1.7fr;
            }

            @include media(">=medium-desktop") { /*1440*/
                grid-template-columns: minmax(auto, 1fr) 1.35fr;
            }

            @include media(">=large-desktop") { /*1800*/
                grid-template-columns: minmax(auto, 1fr) 1.65fr;
            }
        }
    }

    ul {
        overflow: hidden;
        margin-top: -10px;
    }

    li {
        margin: 10px 0 0;
    }

    a {
        color: #7E8284;
        position: relative;
        text-decoration: none;
        
        &:before {
            position: absolute;
            top: 100%;
            height: 1px;
            left: 0;
            width: 100%;
            background: currentColor;
            content: "";
            transition: transform $transition-duration/1.5 ease;
            transform-origin: bottom left;
            transform: scaleX(0);
        }

        .no-touch &:hover {
            text-decoration: none;

            &:before {
                transform: scaleX(1);
            }
        }
    }
}

.footer-bottom {
    background: $color-dark;
    color: $color-white;

    h5, .h5 {
        color: $color-white;
        font-weight: 400;
        margin: 0 0 18px;
        font-family: $ff-pp-fragment-sans;

        @include media(">=desktop") { /*1200*/
            margin: 0 0 22px;
        }

        @include media('<desktop') {
            font-size: 24px;
            line-height: 1.42;
        }

        @include media(">=large-desktop") { /*1800*/
            margin: 0 0 19px;
        }
    }
}

.footer-bottom-top {
    padding: 0 0 48px;

    @include media(">=tablet") { /*768*/
        padding: 0 0 56px;
    }

    @include media(">=desktop") { /*1200*/
        display: flex;
    }

    @include media(">=medium-desktop") { /*1440*/
        padding: 0 0 64px;
    }

    @include media(">=large-desktop") { /*1800*/
        padding: 0 0 80px;
    }

    > *:not(.footer-logo) {
        padding-top: 42px;

        @include media(">=desktop") { /*1200*/
            padding-top: 58px;
        }

        @include media(">=medium-desktop") { /*1440*/
            padding-top: 66px;
        }

        @include media(">=large-desktop") { /*1800*/
            padding-top: 83px;
        }
    }

    .form-subscribe,
    .wpcf7 {
        @include media(">=desktop") { /*1200*/
            width: 60%;
            max-width: 392px;
            margin-right: 10%;
        }

        @include media(">=large-desktop") { /*1800*/
            max-width: 503px;
        }

        .form-subscribe {
            width: 100%;
            max-width: 100%;
            margin-right: 0;
        }

        > .screen-reader-response {
            display: none;
        }
    }
}

.footer-logo {
    text-align: center;
    margin: 0 auto;
    min-height: 124px;

    @include media(">=desktop") { /*1200*/
        margin: 0 9% 0 7.5%;
        flex: 0 0 104px;
    }

    @include media(">=medium-desktop") { /*1440*/
        margin: 0 20.6% 0 5.2%;
    }

    @include media(">=large-desktop") { /*1800*/
        margin: 0 14.7% 0 4.2%;
    }
}

.footer-follow {

    @include media(">=desktop") { /*1200*/
        margin: 0 0 0 auto;
        width: 22.8%;
    }

    @include media(">=medium-desktop") { /*1440*/
        width: 19.5%;
    }

    @include media(">=large-desktop") { /*1800*/
        width: 17.5%;
    }
}

.footer-bottom-bottom {
    padding: 50px 0 45px;
    border-top: 1px solid rgba($color-white, .1);
    font-size: 14px;
    line-height: 1.57;
    color: rgba($color-white, .55);

    @include media(">=desktop") { /*1200*/
        display: flex;
        flex-wrap: wrap;
        padding: 27px 0 14px;
        line-height: 1.71;
    }

    @include media(">=medium-desktop") { /*1440*/
        padding: 27px 0 28px;;
    }

    @include media(">=large-desktop") { /*1800*/
        font-size: 16px;
        line-height: 1.62;
        padding: 40px 0 37px;
    }
}

.footer-add-nav {
    @include media(">=tablet") {
        order: 1;
        margin: 0 0 0 auto;
    }

    ul {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -8px;

        @include media(">=tablet") {
            margin-bottom: -16px;
        }
    }

    li {
        padding: 0 8px 16px;
    }

    a {
        color: rgba($color-white, .55);

        .no-touch &:hover {
            text-decoration: underline;
        }
    }
}