
/*shelf*/

.shelf-item {

    &.without-border {

        .shelf-text {
            &:last-child {
                border-bottom: none;
            }
        }
    }
}

.shelf-picture {
    height: 195px;

    @include media(">=mobile-landscape"){/*568*/
        height: 300px;
    }

    @include media(">=tablet"){/*768*/
        height: 400px;
    }
    
    @include media(">=desktop") {/*1200*/
        height: 450px;
    }

    @include media(">=medium-desktop") {/*1440*/
        height: 552px;
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.shelf-text {
    padding: 20px 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border-bottom: 1px solid rgba($color-dark, .1);

    @include media(">=desktop"){/*1200*/
        padding: 30px 0;
    }

    @include media(">=large-desktop"){/*1800*/
        padding: 40px 0;
    }

    .icon {
        margin: 0 20px 0 0;
        background: $color-dark;
        display: flex;
        padding: 10px;
        border-radius: 50%;
        flex: 0 0 50px;
        height: 50px;
        align-self: flex-start;

        @include media(">=tablet-landscape"){/*1024*/
            padding: 20px;
            flex: 0 0 102px;
            height: 102px;
            margin: 0 32px 0 0;
        }

        img {
            max-width: 100%;
            max-height: 100%;
            margin: auto;
        }
    }

    h4, .h4 {
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;
        margin: 0 0 7px;
        flex: 1;
        min-height: 50px;
        align-items: center;
        align-self: flex-start;
        display: flex;

        &:last-child {
            @include media(">=tablet-landscape"){/*1024*/
                min-height: 102px;
                align-self: center;
            }
        }
    }

    .text {
        padding: 0 4% 0 0;
        max-width: 860px;
        flex: 1;
        margin: 0 0 auto;
        align-items: flex-start;
        
        > *:last-child {
            margin-bottom: 0;
        }
    }

    .columns {
        margin: 30px 0 0;
        flex: 0 0 100%;
        font-weight: 400;
        display: flex;
        flex-wrap: wrap;

        @include media(">=tablet-landscape"){/*1024*/
            margin: 0 0 0 auto;
            flex: 0 0 400px;
        }

        @include media(">=medium-desktop"){/*1440*/
            flex: 0 0 584px;
        }

        &.three-col {
            @include media(">=medium-desktop"){/*1440*/
                flex: 0 0 500px;
            }

            .col,
            .col:nth-child(1) {
                @include media(">=mobile-landscape"){/*568*/
                    width: 33.33%;
                }
            }
        }

        &.one-col {

            @include media('>=tablet-landscape') {/*1024*/
                justify-content: flex-end;
            }

            @include media(">=medium-desktop"){/*1440*/
                flex: 0 0 770px;
            }

            &.has-bullets {
                @include media('>=tablet-landscape') {/*1024*/
                    justify-content: flex-start;
                }

                .dark-bullet {
                    @include media('>=tablet') {
                        margin-left: 70px;
                    }

                    @include media('>=desktop') {
                        margin-left: 0;
                    }
                }
            }

            .col {
                @include media(">=mobile-landscape"){/*568*/
                    width: 100% !important;
                    padding-left: 0 !important;
                }

                @include media(">=tablet-landscape"){/*1024*/
                    width: auto !important;
                }
            }
        }

        .title {
            margin: 0 0 4px;
            color: rgba($color-dark, .4);
        }
    }

    .col {

        > span {
            display: block;
        }
    }

    .col:not(.three-col) {
        padding: 0 0 10px;
        width: 100%;

        @include media(">=mobile-landscape"){/*568*/
            width: 50.9%;
            padding: 0 0 0 4%;
        }

        &:first-child {
            @include media(">=medium-desktop"){/*1440*/
                width: 49%;
            }
        }
    }

    .dark-bullet {
        margin: 0;
    }
}

.shelf-picture-slider {
    line-height: 0;

    .slick-arrow {
        position: absolute;
        top: 50%;
        left: 10px;
        border-radius: 50%;
        width: 64px;
        height: 64px;
        transform: translateY(-50%);
        transition: all $transition-duration linear;
        text-indent: -9999px;
        background: url("../images/icon-arrow-left.svg") no-repeat 50% 50%;
        margin: 0;
        padding: 0;
        border: 1.5px solid $color-white;
        cursor: pointer;
        z-index: 2;
        display: none !important;

        @include media(">=tablet") {/*768*/
            display: block !important;
        }

        @include media(">=tablet-landscape") {/*1024*/
            left: 40px;
            width: 64px;
            height: 64px;
        }

        &.slick-next {
            left: auto;
            right: 10px;
            transform: translateY(-50%) scaleX(-1);

            @include media(">=tablet-landscape") {/*1024*/
                right: 40px;
            }
        }

        .no-touch &:hover {
            background-color: $color-dark;
            border-color: $color-dark;
        }
    }

    .slick-dots {
        position: absolute;
        bottom: 10px;
        left: 0;
        right: 0;

        @include media(">=tablet"){/*768*/
            bottom: 24px;
        }
    }
}

/*shelf end*/