.logos-section {
    background-color: $color-beige;

    &.indent-top {
        margin-top: 0 !important;
        padding-top: $indent-section-mobile;

        @include media('>=desktop') { /*1200*/
            padding-top: $indent-section-desktop;
        }

        @include media('>=medium-desktop') { /*1440*/
            padding-top: $indent-section-medium-desktop;
        }

        @include media('>=large-desktop') { /*1800*/
            padding-top: $indent-section-large-desktop;
        }
    }

    &.indent-top-medium {
        margin-top: 0 !important;
        padding-top: $indent-section-mobile/2;

        @include media('>=desktop') { /*1200*/
            padding-top: $indent-section-mobile;
        }

        @include media('>=medium-desktop') { /*1440*/
            padding-top: $indent-section-desktop;
        }

        @include media('>=large-desktop') { /*1800*/
            margin-top: $indent-section-medium-desktop;
        }
    }

    &.indent-bottom {
        margin-bottom: 0 !important;
        padding-bottom: $indent-section-mobile;

        @include media('>=desktop') { /*1200*/
            padding-bottom: $indent-section-desktop;
        }

        @include media('>=medium-desktop') { /*1440*/
            padding-bottom: $indent-section-medium-desktop;
        }

        @include media('>=large-desktop') { /*1800*/
            padding-bottom: $indent-section-large-desktop;
        }
    }

    &.indent-bottom-medium {
        margin-bottom: 0 !important;
        padding-bottom: $indent-section-mobile/2;

        @include media('>=desktop') { /*1200*/
            padding-bottom: $indent-section-mobile;
        }

        @include media('>=medium-desktop') { /*1440*/
            padding-bottom: $indent-section-desktop;
        }

        @include media('>=large-desktop') { /*1800*/
            margin-bottom: $indent-section-medium-desktop;
        }
    }

    &.with-border {

        .logos-block {
            padding: 50px 0;
            border: 1px solid rgba($color-dark, .1);
            border-width: 1px 0;

            @include media('>=medium-desktop') {
                padding: 75px 0 37px;
            }

            @include media('>=large-desktop') {
                padding: 102px 0 37px;
            }
        }
    }

    &.white-bg {
        background: $color-white;
    }
}

.logos-block {
    padding-top: 50px;

    &:not(.slick-initialized) {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        row-gap: 35px;

        @include media('>=mobile-landscape') {
            flex-direction: row;
            row-gap: 25px;
            justify-content: center;
        }

        @include media('>=desktop') {
            column-gap: 30px;
            row-gap: 15px;
            justify-content: center;
        }

        @include media('>=medium-desktop') {
            padding-top: 75px;
        }

        @include media('>=large-desktop') {
            padding-top: 102px;
            column-gap: 41px;
        }
    }

    .logo-container {
        max-width: 240px;
        margin: 0 auto;
        display: block !important;
        position: relative;

        @include media('>=desktop') {
            width: 140px;
            justify-content: center;
            margin: 0;
        }

        @include media('>=large-desktop') {
            &:first-child {
                margin-left: -16px;
            }

            &:last-child {
                margin-right: -16px;
            }
        }

        .no-touch &:hover {

            .logo-desc {
                opacity: 1;
            }
        }

        a {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
            // transform: translateZ(1px);
        }
    }

    .logo-img {
        width: 150px;
        height: 150px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;

        @include media('>=mobile-landscape') {
            width: 110px;
            height: 110px;
        }

        img {
            width: 130px;
            height: 130px;
            object-fit: contain;

            @include media('>=mobile-landscape') {
                width: 90px;
                height: 90px;
            }
        }

        &.vertical {
            img {
                height: 140px;

                @include media('>=mobile-landscape') {
                    height: 103px;
                }
            }
        }
    }

    .logo-desc {
        margin-top: 16px;
        color: rgba($color-dark, .5);
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        width: 100%;

        @include media('>=desktop') {
            // position: absolute;
            // top: 100%;
            opacity: 0;
            transition: opacity .4s ease;
        }
    }

    .slick-slide {
        margin: 0 5px;
    }

    .slick-dots {
        li {
            button {
                background-color: rgba($color-dark, .5);
            }

            &.slick-active {
                button {
                    background-color: $color-dark;
                }
            }
        }
    }
}

.green-section-inner {
    position: relative;
    padding: 50px 0;

    @include media('>=tablet') {
        min-height: 450px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    @include media('>=medium-desktop') {
        min-height: 560px;
        padding: 75px 0;
    }

    @include media('>=large-desktop') {
        padding: 103px 0px 100px;
    }

    .container {
        position: relative;
        z-index: 2;
        // transform: translateZ(1px);
    }

    .h3, p {
        color: $color-white;
    }

    .btn {
        span {
            justify-content: center;
        }
    }

    .section-info {

        p {
            max-width: 636px;
            margin: 0 auto 25px;

            @include media('>=desktop') {
                margin-bottom: 38px;
            }
        }
    }
}

.green-section-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    // transform: translateZ(1px);

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &::after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba($color-green, .9);
    }
}