.season-section {
    background-color: $color-creame-light;
    padding: 50px 0;

    @include media('>=desktop') {
        padding-top: 105px;
    }

    .section-info {
        position: relative;
        min-height: 400px;
        display: flex;
        flex-direction: column;
        justify-content: center;

        @include media('>=tablet-landscape') {
            width: 458px;
            height: 458px;
            border-radius: 50%;
            border: 1px solid $color-creame;
            justify-content: flex-end;
            padding-bottom: 46px;
        }

        @include media('>=desktop') {
            width: 558px;
            height: 558px;
        }

        @include media('>=medium-desktop') {
            width: 658px;
            height: 658px;
        }

        .buttons-holder, h2 {
            position: relative;
            z-index: 1;
        }

        h2 {
            @include media('>=tablet-landscape') {
                margin-bottom: 30px;
            }

            @include media('>=desktop') {
                margin-bottom: 50px;
            }

            @include media('>=medium-desktop') {
                margin-bottom: 101px;
            }
        }
    }
}

.almond-diagram {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center;
		column-gap: 30px;
		row-gap: 40px;

    @include media('>=tablet-landscape') {
        column-gap: 0;
        row-gap: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
        z-index: 0;
    }

    &::after {
        @include media('>=tablet-landscape') {
            content: '';
            width: calc(100% - 220px);
            height: calc(100% - 203px);
            position: absolute;
            top: 78px;
            left: 112px;
            background-image: url("data:image/svg+xml,%3Csvg width='952' height='952' viewBox='0 0 952 952' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='476' cy='476' r='475.5' stroke='%23E9BE92' stroke-dasharray='8 8'/%3E%3C/svg%3E");
            border-radius: 50%;
            background-position: center;
            background-size: 100% 100%;
            background-repeat: no-repeat;
            z-index: 0;
        }
    }
}

.section-info-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    @include media('>=tablet-landscape') {
        width: 70%;
        height: 70%;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%, -50%, 0);
    }

    @include media('>=medium-desktop') {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        transform: translate3d(0, 0, 0);
    }

    img, svg {
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;

        @include media('>=medium-desktop') {
            transform: translateY(-26px);
        }
    }
}

.season-section-inner {
    display: flex;
    flex-direction: column;
    //flex-direction: column-reverse;
    row-gap: 40px;

    @include media('>=tablet-landscape') {
        row-gap: 0;
        padding: 249px 0 254px;
        position: relative;
        max-width: 1174px;
        width: 100%;
        margin: 0 auto;
    }

    @include media('>=large-desktop') {
        padding: 219px 0 274px;
    }
}

.diagram-card {
    //margin-bottom: 40px;
		width: calc(50% - 15px);
    &:last-child {
        margin-bottom: 0;
    }

    &:nth-child(1) {
        @include media('<tablet') {
            order: 2;
        }
    }

    &:nth-child(2) {
        @include media('<tablet') {
            order: 3;
        }
    }

    @include media('>=mobile-landscape') {
        margin-bottom: 0;
    }

    @include media('>=tablet-landscape') {
        max-width: 214px;
        width: 30%;
        z-index: 1;

        &:nth-child(1) {
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%);
        }

        &:nth-child(2) {
            position: absolute;
            left: 11.8%;
            bottom: 11%;
        }

        &:nth-child(3) {
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
        }

        &:nth-child(4) {
            position: absolute;
            left: 11.8%;
            top: 13%;
        }

        &:nth-child(5) {
            position: absolute;
            left: 50%;
            top: 0;
            transform: translateX(-50%);
        }

        &:nth-child(6) {
            position: absolute;
            right: 10.8%;
            top: 13%;
        }

        &:nth-child(7) {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
        }

        &:nth-child(8) {
            position: absolute;
            right: 10.5%;
            bottom: 11%;
        }
    }
}

.diagram-media {
    position: relative;
}

.diagram-bg-holder {
    width: 160px;
    height: 160px;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto;
    position: relative;
    z-index: 0;
		@include media('<tablet') {
			width: 120px;
			height: 120px;
		}
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.transparent-decor {
    position: absolute;
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
}

.diagram-text {
    text-align: center;
    margin-top: 11px;
		@include media('>=tablet') {
			margin-top: 13px;
		}

    p {
        font-size: 14px;
        line-height: 22px;
				@include media('<tablet') {
					font-size: 9.75px;
					line-height: 16px;
				}
    }

    h6 {
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;
				@include media('<tablet') {
					font-size: 12px;
					line-height: 18px;
				}
        @include media('>=tablet-landscape') {
            font-size: 18px;
            line-height: 24px;
        }
    }
}