
/*video*/

.video-section {
    .section-info {
        text-align: center;
        margin: 0 auto 40px;

        @include media(">=desktop"){/*1200*/
            margin-bottom: 80px;
        }

        p {
            @include media(">=desktop"){/*1200*/
                margin-left: auto;
                margin-right: auto;
                max-width: 832px;
            }
        }
    }

    .video {
        video {
            object-fit: cover;
        }
    }
}

.video {
    position: relative;
    overflow: hidden;
    aspect-ratio: 2/1;
    background-position: 50% 50%;
    background-size: cover;

    @include media(">=large-desktop"){/*1800*/
        height: 800px;
        max-width: 100%;
    }

    &.active {
        background: none !important;

        &:before {
            display: none;
        }
    }

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba($color-dark, .2);
    }

    .video-play-btn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(.5);
        cursor: pointer;
        background: none;
        border: 0;
        padding: 0;
        margin: 0;

        @include media(">=tablet"){/*768*/
            transform: translate(-50%, -50%);
        }

        svg {
            pointer-events: none;
        }

        path {
            transition: all $transition-duration linear;
        }

        .no-touch &:hover {
            path {
                fill: $color-dark
            }
        }
    }

    iframe,
    video {
        width: 100%;
        height: 100%;
        margin: 0 auto;
    }
}

/*video end*/