
/*icon-list*/

.icon-list-heading {
    margin: 0 0 40px;
    
    @include media(">=desktop"){/*1200*/
        margin: 0 0 78px;
    }

    h2, .h2 {
        margin: 0 0 20px;

        @include media(">=desktop") {/*1200*/
            margin: 0 0 28px;
        }
    }

    .text {
        @include media(">=tablet-landscape"){/*768*/
            column-count: 2;
            column-gap: 20px;
        }

        @include media(">=desktop"){/*1200*/
            column-gap: 80px;
        }

        @include media(">=large-desktop"){/*1800*/
            column-gap: 128px;
        }

        p {
            margin: 0 0 17px;

            &.sub-text {
                @include media('>=tablet-landscape') {
                    margin-bottom: 74px;
                }
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.icon-list-rows {
    border-top: 1px solid rgba($color-dark, .1);
    margin: 0 0 40px;
    
    @include media(">=desktop"){/*1200*/
        margin: 0 0 80px;
    }
}

.icon-list-row {
    padding: 20px 0;
    border-bottom: 1px solid rgba($color-dark, .1);
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @include media(">=desktop"){/*1200*/
        padding: 40px 0;
    }

    .icon {
        margin: 0 20px 0 0;
        background: $color-dark;
        display: flex;
        padding: 10px;
        border-radius: 50%;
        flex: 0 0 50px;
        align-self: flex-start;
        aspect-ratio: 1/1;

        @include media(">=tablet-landscape"){/*1024*/
            align-self: center;
            padding: 20px;
            flex: 0 0 102px;
            margin: 0 32px 0 0;
        }

        img {
            max-width: 30px;
            max-height: 30px;
            margin: auto;

            @include media('>=tablet') {
                max-width: 100%;
                max-height: 100%;
            }
        }

        &.full-img {
            padding: 0;
            background: none;

            img {
                border-radius: 50%;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    h4, .h4 {
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;
        margin: 0;
        flex: 1;
        min-height: 50px;
        align-items: center;
        align-self: flex-start;
        display: flex;

        @include media(">=tablet-landscape"){/*1024*/
            min-height: 102px;
        }
    }

    .text {
        flex: 0 0 100%;
        padding: 20px 0 0;

        @include media(">=tablet"){/*768*/
            flex: 0 0 41.7%;
            padding: 0 0 0 20px;
        }

        p {
            margin: 0 0 10px;
            
            &:last-child {
                margin-bottom: 0;
            }
        }

        a {
            text-decoration: underline;

            .no-touch & {
                &:hover {
                    text-decoration: none;
                }
            }
        }
    }
}



/*icon-list end*/