
/*shelf-life*/

.shelf-life {
    h3, .h3 {
        margin: 0 0 20px;

        @include media(">=tablet") {/*768*/
            margin: 0 0 30px;
        }

        @include media(">=desktop") {/*1200*/
            margin: 0 0 48px;
        }
    }

    table {
        td, th {
            text-align: center;
        }

        td:first-child,
        th:first-child {
            text-align: left;
            min-width: 100px;

            @include media(">=tablet"){/*768*/
                width: 280px;
            }
        }
    }

    .close-marker {
        margin: 0 auto;
    }
}

/*shelf-life end*/