.hero-section {
    z-index: 1;
    position: relative;

    &:first-child {
        margin-top: -116px;

        @include media(">=desktop"){/*1200*/
            margin-top: -124px;
        }

        @include media(">=large-desktop"){/*1800*/
            margin-top: -126px;
        }
    }

    .slide-active {
        left: 0 !important;
    }

    .slick-slide {
        height: inherit;

        & > div {
            height: 100%;
        }
    }

    &.adaptive-height {

        .hero-section-inner {
            min-height: 0;
            padding-bottom: 0;
        }

        .hero-info {
            padding: $indent-section-mobile 0;

            @include media('>=desktop') {
                padding: $indent-section-desktop 0;
            }

            @include media('>=large-desktop') {
                padding: $indent-section-medium-desktop 0;
            }
        }
    }

    &.home {

        .decor {

            @include media('>=medium-desktop') {
                max-width: 447px;
            }

            @include media('>=large-desktop') {
                max-width: 463px;
            }

            &.decor-left {
                width: 36%;

                @include media('>=tablet') {
                    width: 21%;
                    bottom: 0;
                    justify-content: flex-end;
                }

                @include media('>=medium-desktop') {
                    width: 23%;
                    max-width: 320px;
                }

                // img {
                //     transform: rotate(90deg) translateY(3px);

                //     @include media('>=tablet') {
                //         transform: rotate(0) translateY(0);
                //     }
                // }
            }
        }

        .hero-info {
            max-width: 658px;

            @include media('>=large-desktop') {
                max-width: 758px;
            }
        }
    }

    &.animation {

        .decor-left {
            opacity: 0;
        }

        .decor-right {
            transform: translateY(30px);
            opacity: 0;
        }

        &.animated {
            .decor-left, .decor-right {
                transform: translateY(30px);
                opacity: 1;
            }

            .decor-right {
                transform: translateY(0);
            }
        }
    }
}

.hero-slide {
    position: relative;
    min-height: 100vh;
    height: 100%;
    display: flex !important;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 60px;
    padding-top: 120px;

    @include media('>=desktop') {
        padding-bottom: 53px;
        padding-top: 135px;
    }

    @include media('>=large-desktop') {
        padding-top: 189px;
        padding-bottom: 75px;
    }

    .container {
        position: relative;
        z-index: 2;
        // transform: translateZ(1px);
    }

    .btn {
        @include media('>=desktop') {
            min-width: 267px;
        }

        span {
            justify-content: center;
        }
    }
}

.hero-section-outer {

    .container {
        position: relative;
        z-index: 2;
        // transform: translateZ(1px);
    }

    .hero-section-inner {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 50px;
        padding-top: 120px;

        @include media('>=desktop') {
            padding-top: 135px;
        }
    }
}

.hero-section-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    // transform: translateZ(1px);

    .video-bg {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: relative;
        z-index: 2;
    }

    picture {
        width: 100%;
        height: 100%;
        display: block;
        position: relative;
        z-index: 1;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &.has-dark-overlay, &.has-white-overlay {
        &::after {
            z-index: 3;
        }
    }
}

.hero-info {
    text-align: center;
    max-width: 638px;
    margin: 0 auto;

    @include media('>=medium-desktop') {
        max-width: 1335px;
    }

    p {
        max-width: 772px;
        width: 100%;
        margin: 0 auto;
        margin-bottom: 24px;

        &:last-child {
            margin-bottom: 0;
        }

        @include media('>=desktop') {
            margin-bottom: 40px;
            font-size: 18px;
            line-height: 28px;
        }

        @include media('>=medium-desktop') {
            margin-bottom: 45px;
            font-size: 20px;
            line-height: 29px;
        }

        @include media('>=large-desktop') {
            font-size: 24px;
            line-height: 34px;
        }
    }

    h1, .h1 {
        margin-bottom: 16px;

        @include media('>=desktop') {
            margin-bottom: 18px;
        }

        @include media('<mobile-landscape'){
            font-size: 40px;
        }
    }
}

.hero-section-inner {
    min-height: 100vh;
}

.hero-slider-nav {
    position: absolute;
    bottom: 16px;
    width: 100%;
    left: 0;
    padding: 0 15px;

    @include media('>=desktop') {
        bottom: 18px;
    }

    @include media('>=medium-desktop') {
        bottom: 24px;
    }

    @include media('>=medium-desktop') {
        bottom: 35px;
    }
}

.decor {
    position: absolute;
    right: 0;
    bottom: 0;
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    transition: transform 1s ease, opacity .2s linear;

    @include media('>=desktop') {
        height: calc(100vh - 130px);
    }

    @include media('>=medium-desktop') {
        height: calc(100vh - 175px);
    }

    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
        width: auto;
        display: inline-block;
    }

    &.decor-left {
        left: 0;
        align-items: flex-start;
        width: 50%;
        max-width: 45%;


        @include media('>=medium-desktop') {
            // width: 13.7%;
            // max-width: 263px;
            justify-content: flex-end;
            // bottom: 16px;
        }
    }

    &.decor-right {
        width: 50%;
        max-width: 45%;
        align-items: flex-end;
    }
}