
/*gallery-section*/

.gallery-section {

    .section-info {
        margin: 0 auto 20px;

        @include media(">=desktop"){/*1200*/
            margin-bottom: 40px;
        }

        @include media(">=medium-desktop"){/*1440*/
            margin-bottom: 96px;
        }

        p {
            margin-left: auto;
            margin-right: auto;
            @include media(">=desktop"){/*1440*/
                max-width: 722px;
            }
        }

        h2, .h2 {
            @include media(">=desktop"){/*1200*/
                margin-bottom: 28px;
            }
        }
    }

    .gallery-picture {
        height: 340px;
        padding: 0 8px;
        max-width: calc(100vw - 16px);
        cursor: pointer;

        @include media(">=tablet"){/*768*/
            height: 440px;
        }

        @include media(">=tablet-landscape"){/*1024*/
            height: 540px;
        }

        @include media(">=desktop"){/*1200*/
            padding: 0 16px;
            max-width: calc(100vw - 32px);
        }

        @include media(">=medium-desktop"){/*1440*/
            height: 640px;
        }

        @include media(">=large-desktop"){/*1440*/
            height: 740px;
        }

    }

    img {
        width: auto;
        max-width: 100%;
        height: 100%;

        @include media('<tablet') {
            object-fit: cover;
            width: 100%;
        }
    }

    .slick-list.draggable {
        @include media (">=tablet-landscape") {/*1024*/
            padding-bottom: 100px !important;
        }

        .slick-track {
            @include media (">=tablet-landscape") {/*1024*/
                overflow: hidden;
            }
        }

        &:after {
            @include media (">=tablet-landscape") {/*1024*/
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                content: "";
                height: 64px;
                width: 200px;
                background: url("../images/drag-indicator.svg") no-repeat 50% /cover;
            }
        }
    }
}

/*gallery-section end*/