.news-section {

    .news-card {

        &.big, &.medium, &.small {

            .news-card-picture {

                @include media('>=tablet') {
                    height: 250px;
                }

                @include media('>=desktop') {
                    height: 330px;
                }

                @include media('>=large-desktop') {
                    height: 380px;
                }
            }
        }

        &.medium {
            @include media('>=tablet') {
                width: calc(50% - 64px/2);
            }

            @include media('>=tablet-landscape') {
                width: calc(33.3% - 64px/3);
            }
        }
    }

    .news-holder {
        @include media('>=tablet') {
            row-gap: 40px;
        }
    }
}

.news-filters {
    margin: 50px 0;

    @include media('>=desktop') {
        margin: 80px 0;
    }

    @include media('>=large-desktop') {
        padding: 46px 0;
        margin: 120px 0;
    }

    ul {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        column-gap: 13px;
        row-gap: 15px;
    }

    .btn {
        &.small {
            span {
                @include media('>=large-desktop') {
                    padding: 12px 40px 10px;
                }
            }
        }

        &.active {
            background-color: $color-green;
            color: $color-white;

            &::before {
                display: none;
            }

            span {
                border-color: $color-green;
            }
        }
    }

    & + .woocommerce-ordering {

        .jcf-select-text {
            padding: 12px 50px 10px 20px;
        }
    }
}

.line-wrap {
    border-top: 1px solid rgba($color-dark, .1);
    border-bottom: 1px solid rgba($color-dark, .1);
    padding: 20px 0;
    margin-top: 50px;

    @include media('>=desktop') {
        padding: 29px 0;
        margin-top: 80px;
    }

    .pagination {
        margin-top: 0;
    }
}