.contact-us-section {

    form {
        margin-top: 30px;

        @include media('>=desktop') {
            margin-top: 36px;
        }

        .btn {
            width: 100%;
            margin-top: 20px;
        }

        input {
            line-height: 3.6 !important; // for fixed letter cut in windows
        }

        &.unaccepted {
            .wpcf7-response-output {
                color: red;
            }
        }
    }

    h4 {
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;
        text-transform: none;
    }

    .faq-inner {
        max-width: 100%;

        @include media('>=tablet') {
            align-items: center;
        }

        @include media('>=tablet-landscape') {
            align-items: inherit;
        }

        .right-side {
            @include media('>=tablet-landscape') {
                width: 63%;
            }

            @include media('>=desktop') {
                width: 49%;
            }
        }
    }

    .left-side {
        @include media('>=tablet') {
            width: 75.5%;
        }

        @include media('>=tablet-landscape') {
            justify-content: space-between;
            width: calc(51% - 20px);
        }
    }

    .right-side {

        .screen-reader-response {
            display: none;
        }

        .wpcf7-response-output {
            padding-top: 10px;
        }
    }

    .two-sides-row {
        @include media('<tablet-landscape') {
            display: flex;
            flex-direction: column-reverse;
            row-gap: 50px;
        }
    }

    .marker-label {
        font-weight: bold;
        font-size: 14px;
        top: 55%;
        position: absolute;
        left: calc(100% + 15px);
        transform: translateY(-50%);
        width: 150px;
        overflow: hidden;
        display: flex;
        white-space: normal;
        text-align: left;
        color: #E94235 !important;
        text-shadow: 1px 0 1px white, 0 1px 1px #fff, -1px 0 1px #fff, 0 -1px 1px #fff;
    }
}

.contact-info {
    margin-bottom: 35px;

    @include media('>=tablet') {
        max-width: 278px;
        padding-top: 20px;
    }

    @include media('>=desktop') {
        padding-top: 40px;
    }

    @include media('>=large-desktop') {
        padding-top: 61px;
    }

    h4 {
        margin-bottom: 21px;
    }
}

.contact-item {
    margin-bottom: 23px;

    &:last-child {
        margin-bottom: 0;
    }

    span {
        font-size: 14px;
        line-height: 24px;
        color: rgba($color-dark, .4);
        text-transform: uppercase;
        display: block;
        letter-spacing: 1px;
        margin-bottom: 5px;
    }

    a {
        color: $color-dark;
        font-size: 18px;
        line-height: 28px;
        cursor: pointer;
        font-weight: 400;

        .no-touch &:hover {
            text-decoration: underline;
        }
    }
}

.fieldset {
    position: relative;
    margin-bottom: 18px;

    @include media('>=desktop') {
        margin-bottom: 24px;
    }

    &:last-child {
        margin-bottom: 0;
    }

    input {
        width: 100%;
    }

    a {
        text-decoration: underline;
    }

    .error-msg {
        color: $color-error;
        font-size: 16px;
        display: none;

        @include media('>=desktop') {
            font-size: 18px;
        }
    }

    &.has-error {
        .error-msg {
            display: block;
        }

        input {
            border-color: $color-error;
        }
    }

    &.has-two-containers {

        @include media('>=medium-desktop') {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
        }
    }
}

.checkbox-holder {
    margin-bottom: 20px;

    @include media('>=desktop') {
        width: 100%;
        // max-width: 48%;
        margin-bottom: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }

    .wpcf7-list-item {
        display: inline-block;
    }

    a {
        margin: 0 4px;
        display: inline-block;
    }

    .wpcf7-not-valid-tip {
        display: block;
    }

    .wpcf7-list-item-label {
        font-weight: 400;
    }
}

.capcha-holder {
    @include media('>=desktop') {
        max-width: 0;
        // width: 100%;
    }
}

.map-container {
    width: 100%;
    height: 400px;
    position: relative;
    overflow: hidden;

    @include media('>=tablet') {
        height: 500px;
        flex-grow: 1;
        margin-top: 100px;
    }

    @include media('>=desktop') {
        height: 600px;
    }

    @include media('>=medium-desktop') {
        height: 700px;
				margin-top: 135px;
    }

    @include media('>=large-desktop') {
        height: 956px;
				margin-top: 175px;
    }

    & > iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}