.our-partners {
    background-color: rgba($color-creame, .2);
    padding: $indent-section-mobile 0;

    @include media('>=desktop') {
        padding: $indent-section-desktop 0;
    }

    @include media('>=large-desktop') {
        padding: $indent-section-medium-desktop 0;
    }

    .section-info {
        max-width: 672px;

        h3 {
            @include media('>=large-desktop') {
                margin-bottom: 27px;
            }
        }
    }
}

.partners-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 20px;
    margin: 40px auto 0;
    justify-content: center;

    @include media('>=tablet') {
        max-width: 772px;
    }

    @include media('>=desktop') {
        column-gap: 32px;
        row-gap: 32px;
        margin-top: 80px;
    }

    li {
        border: 1px solid $color-creame;
        width: 100%;
        padding: 16px;
        height: 132px;
        position: relative;

        @include media('>=mobile-landscape') {
            max-width: 236px;
        }
    }

    a {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;

        .no-touch &:hover {
            cursor: pointer;
        }
    }
}

.partners-img-holder {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
    }
}