/*table-section*/

.table-section {
    .legend-color {
        padding: 10px 5px;
        width: 100%;
        margin: 0 auto;
        display: block;
        max-width: 83px;
    }

    .big-table {
        th, td {
            &:not(:first-child) {
                padding-left: 20px;
                padding-right: 20px;
                text-align: center;
            }

            &:first-child {
                padding-left: 20px;
                width: 150px;

                @include media(">=desktop"){/*1200*/
                    padding-left: 40px;
                    width: 195px;
                }
            }
        }

        td {
            height: 70px;
            @include media(">=desktop"){/*1200*/
                height: 108px;
            }
        }
    }

    .bottom-text {
        padding: 24px 0 0;
        font-size: 12px;
        line-height: 20px;
        font-weight: 400;

        @include media(">=desktop"){/*1200*/
            font-size: 14px;
            line-height: 24px;
        }
    }

    .jcf-scrollable-wrapper {
        height: 100% !important;
    }

    .holder {

        table {
            @include media('>=desktop') {
                width: 100% !important;
            }

            th, td {
                @include media('>=desktop') {
                    padding: 9px;
                }

                @include media('>=medium-desktop') {
                    padding: 10px 13px;
                }

                @include media('>=large-desktop') {
                    padding: 15px 22px;
                }
            }
        }
    }

    .table-scrolling {
        .jcf-scrollable {
            width: 100% !important;
            height: 100% !important;

            table {
                td, th {
                    line-height: 1.5;

                    @include media('>=desktop') {
                        line-height: 26px;
                    }
                }
            }
        }

        .jcf-scrollbar-horizontal {
            height: 5px;
            display: flex;
            width: 100% !important;

            .jcf-scrollbar-dec, .jcf-scrollbar-inc {
                height: 5px;
                width: 5px;
            }

            .jcf-scrollbar-slider {
                height: 5px;
                width: 100% !important;
            }

            .jcf-scrollbar-handle {
                height: 5px;
            }
        }

        .jcf-scrollbar-vertical {
            display: none;
            height: 0 !important;
            width: 0 !important;
            visibility: hidden;
            top: auto;
        }
    }

}

.table-section-heading {
    margin: 0 0 40px;
    padding: 4px 0 0;

    @include media(">=tablet"){/*768*/
        display: flex;
        justify-content: space-between;
    }

    @include media(">=desktop"){/*1200*/
        margin: 0 0 77px;
    }

    .col {
        @include media(">=tablet"){/*768*/
            flex: 0 0 45%;
            display: flex;
            flex-direction: column;
        }

        @include media(">=large-desktop"){/*1800*/
            flex: 0 0 49%;
        }

        &:nth-child(2){
            padding: 20px 0 0;

            @include media(">=tablet"){/*768*/
                padding: 0;
            }
        }
    }

    h3, .h3 {
        margin: 0 0 22px;
    }
}

.table-section-legend {
    display: flex;
    flex-wrap: wrap;

    @include media(">=tablet"){/*768*/
        margin: auto 0 -30px auto;
        max-width: 575px;
    }
    
    .row {
        display: flex;
        font-size: 12px;
        line-height: 20px;
        font-weight: 400;
        margin: 0 0 9px;
        width: 100%;

        @include media(">=mobile-landscape"){/*568*/
            width: 50%;
        }

        @include media(">=desktop"){/*1200*/
            font-size: 14px;
            line-height: 24px;
        }

        &:nth-child(1) {
            @include media("<mobile-landscape"){/*568*/
                order: -2;
            }
        }
        
        &:nth-child(3) {
            @include media("<mobile-landscape"){/*568*/
                order: -1;
            }
        }

        .legend-color {
            flex: 0 0 32px;
            height: 18px;
            // margin: 0 12px 0 0;
            margin: 2px 12px 0 0;
            padding: 0;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &:nth-child(odd) {
            .text {
                padding: 0 10px 0 0;
            }
        }
    }
}

.legend-color {
    &.white {
        background: $color-white;
        border: 1px solid $color-gray;
    }

    &.grey {
        background: #90A1A7;
    }

    &.light-grey {
        background: $color-gray;
    }

    &.dark-grey {
        background: #728C93;
    }
}

/*table-section end*/