
/*social*/
.social-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -16px -16px;

    li {
        margin: 0 16px 16px;
    }
}

.social-btn {
    transition: background $transition-duration linear;
    border: 1px solid $color-white;
    width: 40px;
    height: 40px;
    display: flex;
    border-radius: 50%;

    @include media(">=desktop") {/*1200*/
        width: 32px;
        height: 32px;
        padding: 0 3.4%;
    }

    @include media(">=medium-desktop") { /*1440*/
        width: 40px;
        height: 40px;
    }

    .no-touch &:hover {
        background: $color-white;

        path {
            fill: $color-dark;
        }
    }

    svg {
        margin: auto;
        path {
            transition: fill $transition-duration linear;
            fill: $color-white;
        }
    }
}
/*social end*/