
/*bulleted-column-section*/

.bulleted-column-section {

    h2, .h2 {
        margin: 0 0 20px;

        @include media(">=tablet") {/*768*/
            margin: 0 0 28px;
        }

        @include media(">=large-desktop"){/*1800*/
            max-width: 775px;
            width: 44%;
        }
    }

    &.two-columns {
        .h2, .h2 {
            @include media(">=large-desktop"){/*1800*/
                max-width: 665px;
            }
        }

        .bulleted-column-list {
            @include media(">=desktop") {/*1200*/
                flex: 0 0 48%;
                max-width: 743px;
            }

            ul {
                @include media(">=desktop") {/*1200*/
                    // column-count: 2;
                    column-gap: 30px;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                }

                @include media(">=large-desktop"){/*1800*/
                    column-gap: 56px;
                }

                li {
                    @include media(">=desktop") {/*1200*/
                        width: calc(50% - 15px);
                    }

                    @include media(">=large-desktop"){/*1800*/
                        width: calc(50% - 28px);
                    }
                }

                li:before {
                    @include media(">=desktop") {/*1200*/
                        top: 0;
                    }
                }
            }
        }
    }

    &.animation {

        .bulleted-column-list {
            transform: translateY(150px);
            opacity: 0;
            transition: transform 1s ease, opacity .4s linear;
            transition-delay: .15s;
        }

        &.animated {

            .bulleted-column-list {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }
}

.bulleted-column-section-row {
    display: flex;
    flex-wrap: wrap;

    .sub-text {
        margin: 0 0 15px;

        @include media(">=tablet") {/*768*/
            margin: 0 0 25px;
        }
    }


    .section-info {
        margin: 0 0 30px;
        text-align: left;

        @include media(">=tablet") {/*768*/
            margin: 0 20px 0 0;
            max-width: 640px;
            flex: 0 0 44%;
        }
    }

    &.reverse {
        @include media(">=tablet") {/*768*/
            flex-direction: row-reverse;
        }

        .section-info {
            @include media(">=tablet") {/*768*/
                margin: 0 0 0 20px;
            }
        }

        .bulleted-list-col {
            @include media(">=tablet") {/*768*/
                margin: 0 auto 0 0;
            }
        }
    }

}

.bulleted-column-list {
    
    @include media(">=tablet") {/*768*/
        max-width: 670px;
        flex: 0 0 45%;
        margin: 0 0 0 auto;
    }

    @include media(">=desktop") {/*1200*/
    }

    ul {
        margin: 0;
        font-weight: 400;
    }

    .dark-bullet {
        li:before {
            top: 3px;
        }
    }
}



/*bulleted-column-section end*/