.collaboration-section {
    background-color: $color-light-blue;
    padding: $indent-section-mobile 0;
    position: relative;
    min-height: 250px;

    &::before {
        content: '';
        width: 0;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
    }

    @include media('>=tablet') {
        padding: 100px 0;
        min-height: 400px;
    }

    @include media('>=desktop') {
        padding: $indent-section-desktop 0;
        min-height: 500px;
    }   

    @include media('>=large-desktop') {
        padding: $indent-section-medium-desktop 0;
    }

    .section-info {
        text-align: left;
        margin-left: 0;
        max-width: 734px;
        position: relative;
        z-index: 2;
        // transform: translateZ(1px);

        @include media('>=tablet') {
            width: 50%;
        }

        p {
            text-align: left;
            margin-left: 0;
            margin-right: 0;

            @include media('>=large-desktop') {
                font-size: 24px;
                line-height: 34px;
            }
        }

        h2 {
            @include media('>=medium-desktop') {
                margin-bottom: 28px;
            }
        }
    }

    .buttons-holder {
        margin-top: 17px;
    }

    .btn {
        span {
            justify-content: center;
        }
    }

    .container {
        position: relative;
        z-index: 3;
        // transform: translateZ(1px);
    }

    &.has-video {
        &::before {
            width: 100%;
            background: rgba($color-light-blue-1, .5);
        }
    }

    &.large-padding {

        @include media('>=medium-desktop') {
            padding: $indent-section-medium-desktop 0;
        }

        @include media('>=large-desktop') {
            padding: $indent-section-large-desktop 0;
        }
    }

    &.small-text-width {

        .section-info {
            max-width: 598px;
        }
    }
}

.collaboration-bg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    overflow: hidden;

    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        right: 0;
    }

    video {
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        height: 100%;
        width: 100%;
    }
}

.collaboration-section-inner {

    .buttons-hoder {
        text-align: left;
    }
}