/*news*/

.news-grid-slider {
    margin: 0 -10px 0 0;

    @include media(">=mobile-landscape"){/*568*/
        margin: 0;
    }
    @include media(">=desktop"){/*1200*/
        position: relative;
        // padding: 0 0 0 calc(57.2% + 32px);
        // margin: 0 0 -32px;
        display: grid;
        gap: 32px;
        grid-template-columns: 57.2% auto;

        @include media(">=large-desktop"){/*1800*/
            // padding: 0 0 0 calc(57.5% + 32px);
            grid-template-columns: 57.5% auto;
            grid-template-rows: repeat(2, 1fr);
        }
    }

    .slick-slide {
        padding: 0 10px 0 0;

        @include media("<desktop"){/*1200*/
            height: inherit;
        }

        @include media(">=mobile-landscape"){/*568*/
            padding: 0 16px 0 0;
        }

        @include media(">=desktop"){/*1200*/
            padding: 0 32px 0 0;
        }

        & > div {
            @include media("<desktop"){/*1200*/
                height: 100%;
            }
        }

        .news-card {
            @include media("<desktop"){/*1200*/
                height: 100%;
            }
        }

        .news-card-text {
            @include media("<desktop"){/*1200*/
                height: calc(100% - 300px);
            }

            @include media("<tablet"){/*768*/
                height: calc(100% - 257px);
            }
        }
    }

    .news-card-picture {
        height: 257px;

        @include media(">=tablet"){/*768*/
            height: 300px;
        }

        @include media(">=desktop"){/*1200*/
            height: 100%;
        }
    }

    .news-card {
        margin: 0;

        &:first-child {
            @include media(">=desktop"){/*1200*/
                grid-column: 1/2;
                grid-row: 1/3;
                display: flex;
                flex-direction: column;
            }

            .news-card-picture {
                @include media(">=desktop"){/*1200*/
                    height: 342px;
                    flex-grow: 1;
                    max-height: 342px;
                }

                @include media(">=medium-desktop"){/*1440*/
                    height: 379px;
                    max-height: 379px;
                }

                @include media(">=large-desktop"){/*1800*/
                    height: 416px;
                    max-height: 416px;
                }
            }

            h5, .h5 {
                font-size: 24px;
                line-height: 32px;

                @include media(">=desktop"){/*1200*/
                    font-size: 28px;
                    line-height: 1.29;
                }

                @include media(">=large-desktop"){/*1800*/
                    font-size: 40px;
                    line-height: 1.20;
                }
            }
        }

        &:not(:first-child) {
            @include media(">=desktop"){/*1200*/
                display: flex;
                grid-column: 2/3;
            }

            .news-card-picture {
                @include media(">=desktop"){/*1200*/
                    flex: 0 0 57.5%;
                    min-height: 222px;
                    position: relative;
                }

                @include media(">=medium-desktop"){/*1440*/
                    flex: 0 0 57.5%;
                    min-height: 242px;
                }

                @include media(">=large-desktop"){/*1800*/
                    flex: 0 0 58.1%;
                    min-height: 287px;
                }

                img {
                    @include media(">=desktop"){/*1200*/
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                }
            }
        }

    }
}

.news-card {
    position: relative;

    &.horizontal-view {
        @include media(">=tablet-landscape") { /*1200*/
            display: flex;
        }
    }

    &.full {
        @include media('>=tablet') {
            width: 100%;
        }

        .news-card-text {

            @include media('>=desktop') {
                display: flex;
                align-items: flex-start;
                flex-direction: row;
                column-gap: 32px;
            }
        }

        h4, p, h5 {
            @include media('>=desktop') {
                width: calc(50% - 16px);
                margin-bottom: 0;
            }
        }

        p {
            @include media('>=desktop') {
                padding-left: 8px;
            }
        }
    }

    &.large, &.big, &.medium, &.small {

        h4, h5 {
            margin-bottom: 0
        }
    }

    &.large {
        @include media('>=tablet') {
            width: calc(67% - 16px);
        }
    }

    &.big {
        @include media('>=tablet') {
            width: calc(50% - 64px/2);
        }

        @include media('>=tablet-landscape') {
            width: calc(41.7% - 64px/3);
        }
    }

    &.medium {
        @include media('>=tablet') {
            width: calc(33% - 16px);
        }
    }

    &.small {
        @include media('>=tablet') {
            width: calc(50% - 64px/2);
        }

        @include media('>=tablet-landscape') {
            width: calc(25% - 64px/3);
        }
    }

    &.has-video {
        .video-play-btn {
            display: block;
        }
    }

    .no-touch &:hover {
        .news-card-text {
            color: $color-white;
            background-color: $color-green;

            // &:before {
            //     transform: scaleX(1);
            //     transform-origin: bottom left;
            // }
        }
    }

    .video-play-btn {
        display: none;
    }
}

.news-card-link {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    text-transform: none;
}

.news-card-picture {
    @include media(">=tablet-landscape") { /*1200*/
        flex: 0 0 59.6%;
    }

    img, video {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.news-card-text {
    background: $color-white;
    padding: 20px 24px;
    display: flex;
    position: relative;
    flex-direction: column;
    color: $color-dark;
    transition: color $transition-duration linear, background-color $transition-duration ease;
    flex: 1;

    @include media('>=desktop') { /*1200*/
        padding: 20px 18px 17px;
	}

    @include media('>=medium-desktop') { /*1440*/
        padding: 24px 22px;
	}

	@include media('>=large-desktop') { /*1800*/
        padding: 32px;
	}

    &:before {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        content: "";
        background: $color-green;
		transition: transform $transition-duration/1.5 ease;
		transform: scaleX(0);
		transform-origin: bottom left;
		z-index: 0;
    }

    > * {
        position: relative;
    }

    h5, .h5, .h4, h4 {
        margin: 0 0 20px;
        color: currentColor;
        font-weight: 400;
        font-family: $ff-pp-fragment-sans;
    }

    .category {
        text-transform: uppercase;
        margin: 0 0 8px;
        letter-spacing: 1px;
        font-size: 14px;
        line-height: 1.71;
        color: currentColor;
        opacity: .4;
        display: block;

        @include media('>=desktop') { /*1200*/
            font-size: 12px;
            line-height: 1.83;
            margin: 0 0 4px;
        }

        @include media('>=large-desktop') { /*1800*/
            margin: 0 0 8px;
            font-size: 14px;
            line-height: 1.71;
        }
    }

    > *:last-child {
        margin-bottom: 0;
    }
}
