.sustainability-section {

    .right-side {
        display: block;

        @include media('>=desktop') {
            padding-left: 95px;
        }

        @include media('>=medium-desktop') {
            padding-left: 104px;
        }

        @include media('>=large-desktop') {
            padding-left: 128px;
        }

        &::before {
            display: none;
        }
    }

    .section-info {
        text-align: left;

        @include media('>=desktop') {
            margin-left: 0;
        }

        h2 {
            margin-bottom: 23px;

            @include media('>=desktop') {
                margin-bottom: 20px;
            }
        }
    }

    .left-side {
        padding-top: 44px;

        @include media('>=tablet') {
            padding-top: 0;
            justify-content: center;
        }

        @include media('>=desktop') {
            width: calc(51% - 35px);
        }

        @include media('>=large-desktop') {
            width: calc(51% - 140px);
            transform: translateY(-12px);
        }
    }

    .two-sides-row {

        @include media('<tablet') {
            display: flex;
            flex-direction: column-reverse;
        }

        @include media('>=desktop') {
            column-gap: 35px;
        }

        @include media('>=large-desktop') {
            column-gap: 140px;
        }
    }

    .buttons-holder {
        margin-top: 19px;

        @include media('>=desktop') {
            margin-top: 2px;
        }

        @include media('>=medium-desktop') {
            margin-top: 0;
        }

        @include media('>=large-desktop') {
            margin-top: 15px;
        }
    }

    &.animation {

        .sustainability-item {
            &::before {
                transform: scale(.9);
                transition: transform 2.1s ease .2s;
            }
        }

        .sustainability-media {

            img, video {
                transform: scale(.9);
                transition: transform 2.1s ease .2s;
            }
        }

        &.animated {

            .sustainability-media {

                img, video {
                    transform: scale(1);
                }
            }

            .sustainability-item {
                &::before {
                    transform: scale(1);
                }
            }
        }
    }
}

.sustainability-divider {
    text-align: center;
    position: relative;
    height: 26px;
    margin: 5px 0;

    @include media('>=desktop') {
        margin: 7px 0 15px;
    }

    &::after, &::before {
        content: '';
        width: calc(50% - 20px);
        height: 1px;
        background-color: $color-white;
        position: absolute;
        top: 50%;
        right: 0;
    }

    &::before {
        left: 0;
    }

    svg, img {
        max-width: 26px;
        max-height: 100%;
        object-fit: contain;
    }
}

.sustainability-item {
    min-height: 440px;
    display: flex !important;
    flex-direction: column;
    justify-content: flex-end;
    padding: 20px;
    transition: transform .5s ease, min-height .5s ease;
    position: relative;
    cursor: pointer;
    overflow: hidden;

    @include media('>=desktop') {
        min-height: 621px;
        padding: 21px;
        transform: scale(.81);
    }

    @include media('>=medium-desktop') {
        min-height: 760px;
        padding: 36px;
        transform: scale(.847);
    }

    @include media('>=large-desktop') {
        min-height: 644px;
        padding: 48px;
        // transform: scale(.75);
        transform: scale(1);
    }

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($color-dark, .2);
        z-index: 1;
    }
}

.sustainability-info {
    max-width: 370px;
    margin: 0 auto;
    text-align: center;
    width: 100%;
    position: relative;
    z-index: 1;
    
    @include media('<=tablet') {
        opacity: 0;
    }

    @include media('>=large-desktop') {
        max-width: 530px;
    }

    h2, p, div {
        color: $color-white;
    }

    h2, .h2 {
        @include media('>=desktop') {
            font-size: 120px;
            line-height: 120px;
        }

        @include media('>=medium-desktop') {
            font-size: 140px;
            line-height: 136px;
        }

        @include media('>=large-desktop') {
            font-size: 200px;
            line-height: 200px;
            margin-bottom: -24px;
            letter-spacing: -8px;
        }
    }

    p {
        @include media('>=desktop') {
            font-size: 20px;
            line-height: 30px;
            max-width: 256px;
            margin: 0 auto;
        }

        @include media('>=large-desktop') {
            font-size: 24px;
            line-height: 34px;
            max-width: 300px;
        }
    }
}

.nav-item {
    text-align: center;
    padding: 0 4px;

    @include media('>=desktop') {
        padding: 0;
        display: flex !important;
        flex-direction: row;
        align-items: center;
        transition: background-color .3s ease;
        border-radius: 8px;
    }

    @include media('>=large-desktop') {
        padding: 5px 5px 5px 0;
    }
}

.nav-item-icon-holder {
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 4px;
    flex-shrink: 0;


    img, svg {
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;

        path {
            transition: fill .4s ease, stroke .4s ease;;
        }
    }

    @include media('>=desktop') {
        width: 50px;
        height: 50px;
        margin: 0 8px 0 0;
    }
}

.nav-item-text {
    letter-spacing: .3px;
    transition: color .4s ease;

    @include media('<desktop') {
        font-size: 14px;
        line-height: 22px;
        padding: 5px 0;
    }

    @include media('>=desktop') {
        text-align: left;
    }
}

.nav-list {
    margin-top: 25px;

    @include media('>=desktop') {
        margin-top: 30px;
    }

    @include media('>=medium-desktop') {
        margin-top: 38px;
    }

    @include media('>=large-desktop') {
        margin-top: 42px;
    }

    .slick-slide {
        width: calc(100% / 3) !important;

        @include media('>=desktop') {
            width: calc(50% - 16px) !important;
            float: none;
            margin-bottom: 10px;

            &:nth-child(even) {
                margin-right: 32px;
            }

            &:nth-child(odd) {
                padding-left: 33px;
            }
        }

        @include media('>=medium-desktop') {
            width: calc(50% - 47px) !important;
            margin-bottom: 12px;

            &:nth-child(even) {
                margin-right: 66px;
            }

            &:nth-child(odd) {
                padding-left: 0;
            }
        }

        @include media('>=large-desktop') {
            width: calc(50% - 65px) !important;
            margin-bottom: 6px;

            &:nth-child(even) {
                margin-right: 46px;
            }
        }

        &:not(.slick-current) {

            .no-touch &:hover {
                cursor: pointer;

                .nav-item-icon-holder {
                    svg {
                        path {
                            stroke: $color-green;
                        }
                    }
                }

                .nav-item-text {
                    color: $color-green;
                }
            }
        }

        &.slick-current {

            .nav-item-text {
                color: $color-green;

                @include media('>=desktop') {
                    color: $color-white;
                }
            }

            .nav-item-icon-holder {
                svg {

                    path {
                        stroke: $color-green;

                        @include media('>=desktop') {
                            stroke: $color-white;
                        }
                    }
                }
            }

            .nav-item {
                @include media('>=desktop') {
                    background-color: $color-green;
                }
            }
        }

        &.slick-cloned {
            opacity: 0;
            height: 0 !important;
            width: 0 !important;
            overflow: hidden;
            min-height: 0;
            min-width: 0;
            max-width: 0;
            max-height: 0;
            padding: 0 !important;
            margin: 0 !important;
        }
    }

    .slick-track {
        width: auto !important;
        transform: translate3d(0, 0, 0) !important;
        flex-direction: row;
        flex-wrap: wrap;
        row-gap: 20px;

        @include media('>=desktop') {
            row-gap: 0;
        }
    }
}

.sustainability-slider {
    width: calc(100% + 20px);
    margin-right: -20px;

    @include media('>=desktop') {
        width: calc(100% + 80px);
        margin-right: -80px;
    }

    @include media('>=medium-desktop') {
        width: calc(100% + 100px);
        margin-right: -100px;
    }

    @include media('>=large-desktop') {
        width: calc(100% + 172px);
        margin-right: -172px;
    }

    .slick-list.draggable {
        @include media('>=desktop') {
            padding-left: 6px;
        }

        @include media (">=large-desktop") {/*1800*/
            padding-bottom: 100px !important;
        }
    }

    .slick-track {
        min-height: 440px;

        @include media('>=desktop') {
            min-height: 621px;
        }

        @include media('>=medium-desktop') {
            min-height: 760px;
        }

        @include media('>=large-desktop') {
            min-height: 860px;
        }
    }

    .slick-slide {
        margin-right: 10px;

        @include media('>=desktop') {
            margin-right: -5px;
        }

        @include media('>=medium-desktop') {
            margin-right: -8px;
        }

        @include media('>=large-desktop') {
            margin-right: 32px;
            height: auto;
        }

        & > div {
            @include media('>=large-desktop') {
                height: 100%;
                display: flex;
                align-items: center;
            }
        }

        &:last-child {
            margin-right: 15px;

            @include media('>=desktop') {
                margin-right: 0;
            }
        }

        &.slick-current {

            .sustainability-item {
                @include media('>=desktop') {
                    transform: scale(1);
                }

                @include media('>=large-desktop') {
                    min-height: 860px;
                }
            }
        }
    }

    &.show-draggable-icon {

        .slick-list.draggable {
            @include media('>=desktop') {
                padding-bottom: 100px!important;

                &:after {
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    content: "";
                    height: 64px;
                    width: 200px;
                    background: url("../images/drag-indicator.svg") no-repeat 50% /cover;

                    @include media('>=desktop') {
                        left: 43%;
                    }

                    @include media('>=medium-desktop') {
                        left: 42%;
                    }

                    @include media('>=large-desktop') {
                        left: 40%;
                    }
                }
            }
        }
    }
}

.sustainability-media {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    pointer-events: none;

    img, svg, video {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}