.our-story {

    .section-info {
        max-width: 781px;
    }

    .story-slider {
        margin-top: 50px;

        @include media('>=desktop') {
            margin-top: 100px;
        }
    }

    .slick-list {
        overflow: visible;
    }

    .slick-slide {
        margin-right: 16px;
        height: inherit;

        & > div {
            height: 100%;
        }

        &:last-child {
            margin-right: 0;
        }

        @include media('>=desktop') {
            margin-right: 32px;
        }
    }

    .slick-arrow {
        width: 40px;
        height: 40px;
        background-color: rgba(0,0,0,0);
        border: 1px solid $color-dark;
        border-radius: 50%;
        transition: opacity .3s ease;
        cursor: pointer;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        z-index: 2;

        @include media('>=desktop') {
            right: -61px;
        }

        @include media('>=medium-desktop') {
            width: 64px;
            height: 64px;
            right: -83px;
        }

        @include media('>=large-desktop') {
            right: -118px;
        }

        &.slick-prev {
            left: 0;

            @include media('>=desktop') {
                left: -61px;
            }

            @include media('>=medium-desktop') {
                left: -83px;
            }

            @include media('>=large-desktop') {
                left: -118px;
            }
        }

        &.slick-disabled {
            opacity: .5;
        }
    }

    .slick-next {

        svg {
            transform: rotate(-180deg);
        }
    }

    .story-slider-nav {

        .slick-slide {
            margin-right: 0;
        }
    }
}

.story-slide-text {
    min-height: 194px;
}

.story-slide-text-holder {
    padding: 15px;

    @include media('>=tablet') {
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }

    @include media('>=tablet-landscape') {
        width: 57%;
    }

    @include media('>=desktop') {
        padding: 37px 44px;
    }

    @include media('>=large-desktop') {
        padding: 57px 64px;
    }

    .h2 {
        margin-bottom: 11px;
    }
}

.story-slider {
    position: relative;

    &::after {
        @include media('>=desktop') {
            content: '';
            width: 30%;
            height: 100%;
            position: absolute;
            top: 0;
            right: -80px;
            background: linear-gradient(90deg, rgba(251, 230, 209, 0) 0%, $color-creame-light 93.27%);
            transition: opacity .2s linear;
        }

        @include media('>=medium-desktop') {
            right: -100px;
        }

        @include media('>=large-desktop') {
            left: 100vw;
            width: 30vw;
            transform: translateX(-125%);
        }
    }

    &.show-last-slide {
        &::after {
            opacity: 0;
        }
    }
}

.story-slider-nav {
    margin-top: 30px;

    @include media('>=desktop') {
        margin-top: 51px;
    }

    .slick-slide {

        &:not(.before-current-year) {

            .story-nav-item {
                transition: color .3s ease .5s;

                span {
                    &::after, &::before {
                        transition: background-color .3s ease .5s, transform .3s ease .5s;
                    }
                }
            }
        }
    }
}

.story-slide-img {
    position: relative;
    background-color: rgba($color-creame, .2);

    @include media('>=tablet') {
        width: 50%;
    }

    @include media('>=tablet-landscape') {
        width: 43%;
    }

    &::after {
        content: '';
        display: block;
        padding-top: 93%;
    }

    img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}

.story-slide {
    height: 100%;
    background-color: rgba($color-creame, .2);

    @include media('>=tablet') {
        display: flex !important;
        flex-direction: row;
        width: 89.09vw !important;
    }

    @include media('>=tablet-landscape') {
        width: 69.09vw !important;
    }

    @include media('>=medium-desktop') {
        width: 63.09vw !important;
    }

    &.no-image {
        @include media('>=tablet') {
            width: calc(89.09vw / 2)!important;
        }

        @include media('>=tablet-landscape') {
            width: calc(69.09vw / 2)!important;
        }

        @include media('>=medium-desktop') {
            width: calc(63.09vw / 2)!important;
        }

        .story-slide-text-holder {
            @include media('<tablet') {
                height: inherit;
                display: flex;
                flex-direction: column;
                justify-content: center;
            }

            @include media('>=tablet') {
                width: 100%;
            }
        }

        .story-slide-text {
            @include media('<tablet') {
                min-height: auto;
            }
        }
    }
}

.story-nav-item {
    color: rgba($color-dark, .3);
    transition: color .3s ease;
    cursor: pointer;

    span {
        margin-top: 6px;
        display: flex;
        flex-direction: row;
        align-items: center;
        column-gap: 10px;
        transform: translateX(15px);

        @include media('>=desktop') {
            transform: translateX(25px);
        }

        &::after, &::before {
            content: '';
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: rgba($color-dark, .3);
            transform: scale(.5);
            transition: background-color .3s ease, transform .3s ease;
            display: inline-block;
            flex-shrink: 0;
        }

        &::after {
            width: calc(100% - 29px);
            height: 1px;
            transform: scale(1);
            border-radius: 0;
        }
    }

    .slick-current &, .before-current-year & {
        color: $color-dark;

        span {
            &::before {
                transform: scale(1);
            }

            &::after, &::before {
                background-color: $color-dark;
            }
        }
    }

    @include media('>=desktop') {
        font-size: 20px;
        line-height: 28px;
    }

    @include media('>=large-desktop') {
        font-size: 24px;
        line-height: 34px;
    }
}