.get-in-touch {
    background-color: $color-light-gray;
    min-height: 300px;
    padding: 50px 0;
    position: relative;

    @include media('>=desktop') {
        padding: 85px 0;
    }

    @include media('>=medium-desktop') {
        padding: 130px 0;
    }

    @include media('>=large-desktop') {
        padding: 163px 0 142px;
    }

    .section-info {
        position: relative;
        z-index: 1;
        p {
            max-width: 500px;
            margin: 0 auto 31px;

            &.full-width {
                max-width: 100%;
            }

            &:last-child {
                margin-bottom: 0;
            }

        }

        .btn {
            @include media('>=large-desktop') {
                min-width: 208px;
            }
        }
    }

    form {

        @include media('>=mobile-landscape') {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            align-items: flex-start;
        }

        @include media('>=large-desktop') {
            margin-top: 38px;
        }

        input {
            width: 100%;
            margin-bottom: 20px;

            @include media('>=mobile-landscape') {
                margin-bottom: 0;
            }
        }

        .btn {
            width: 100%;

            @include media('>=mobile-landscape') {
                margin-left: 16px;
                flex-shrink: 0;
                width: auto;
            }

            span {
                @include media('>=desktop') {
                    padding: 13px 82px;
                }

                @include media('>=large-desktop') {
                    padding: 19px 82px;
                }
            }
        }

        &.invalid {

            input {
                border-color: $color-red;

                @include media('<mobile-landscape') {
                    margin-bottom: 0;
                }
            }
        }
    }

    .wpcf7-form-control-wrap {
        text-align: left;
        display: block;

        @include media('>=mobile-landscape') {
            width: 56.35%;
        }

        .wpcf7-not-valid-tip {
            font-size: 14px;
            color: $color-red;
            display: block;

            @include media('<mobile-landscape') {
                margin-bottom: 10px;
            }
        }
    }

    .wpcf7-response-output {
        margin-top: 10px;

        @include media('>=desktop') {
            margin-top: 25px;
        }
    }

    .screen-reader-response {
        display: none;
    }

    &.animation {

        form {
            transform: translateY(150px);
            opacity: 0;
            transition: transform 1s ease, opacity .4s linear;
            transition-delay: .15s;
        }

        &.animated {

            form {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }
    .section-info {
        max-width: 772px;
    }
}

.get-in-touch-bg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}