.woocommerce {
    .coupon {
        @include media("<tablet"){
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            row-gap: 10px;
        }

        input {
            min-width: 100%;
            margin-bottom: 0.5em;

            @include media('>=mobile-landscape') {
                min-width: 200px;
                margin-bottom: 0;
            }
        }

        .button {
            @include media('<mobile-landscape') {
                min-width: 100%;
            }
        }
    }

    table {

        &.shop_table {

            th {
                font-weight: 400;
            }
        }

        &.cart {

            td {

                &.actions {
                    @include media('<mobile-landscape') {
                        display: flex;
                        flex-direction: column;
                    }
                }
            }
        }
    }

    .cart-collaterals {

        .cart_totals {
            margin: 50px auto 0;
            text-align: center;
            float: none;

            @include media('>=desktop') {
                margin-top: 50px;
            }

            h4 {
                margin-bottom: 10px;
            }
        }
    }

    .woocommerce-cart-form {
        margin-top: 50px;

        @include media('>=desktop') {
            margin-top: 100px;
        }
    }

    .cart-collaterals {
        .cart_totals {
            width: 100%;
        }
    }

    .woocommerce-shipping-destination {
        margin: 10px 0;
    }
}

.woocommerce ul#shipping_method li label {
    display: inline-block;
}

.cart-collaterals {
    h2, .h2 {
        margin: 0 0 20px;

        @include media(">=desktop"){/*1200*/
            margin: 0 0 40px;
        }
    }

    .woocommerce-shipping-calculator {
        @include media(">=tablet"){/*768*/
            max-width: 500px;
        }
    }
}

.woocommerce table.cart td.actions .coupon {
    @include media("<tablet"){
        padding-bottom: 40px;
    }
    
    .input-text+.button {
        @include media("<mobile-landscape"){
            width: 100%;
        }
    }
}
