/*product-description*/

.product-description-section {
    padding: $indent-section-mobile/2 0;

    @include media('>=desktop') { /*1200*/
        padding: $indent-section-mobile 0;
    }

    @include media('>=medium-desktop') { /*1440*/
        padding: $indent-section-desktop 0;
    }

    @include media('>=large-desktop') { /*1800*/
        padding: $indent-section-medium-desktop + 11px 0;
    }


    .container {
        @include media(">=tablet") {/*768*/
            display: flex;
            align-items: center;
        }
    }
}

.product-description-picture {
    position: relative;
    display: flex;
    margin: 0 0 20px;

    @include media(">=tablet") {/*768*/
        width: 49%;
        margin: 7px 0 -7px;
    }

    &:before {
        position: absolute;
        top: 50%;
        left: 50%;
        aspect-ratio: 1 / 1;
        width: 85.2%;
        transform: translate(-50%, -50%);
        content: "";
        border-radius: 50%;
        background: $color-creame;
        z-index: -1;
    }

    &::after {
        content: '';
        display: block;
        padding-top: 112%;
    }

    img {
        margin: auto;
    }

    & > img {
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        z-index: 0;
        object-fit: contain;
        transition: opacity .65s ease, transform .9s ease-out;
        transform: translate3d(-50%, -35%, 1px);
    }
}

.product-description-text {
    position: relative;
    z-index: 10;

    @include media(">=tablet") {/*768*/
        width: 50%;
        align-self: center;
        padding-left: 2%;
        margin: 23px 0 0 auto;
    }

    @include media(">=desktop"){ /*1200*/
        padding-left: 9.5%;
    }

    h2, .h2 {
        margin: 0 0 18px;

        @include media(">=desktop") {/*1200*/
            margin: 0 0 28px;
        }
    }

    p {
        margin: 0 0 10px;
        @include media(">=desktop") {/*1200*/
            margin: 0 0 20px;
        }
    }

    .sub-text {
        margin: 0 0 14px;
        @include media(">=desktop") {/*1200*/
            margin: 0 0 24px;
        }
    }

    .buttons {
        margin-top: 20px;

        @include media(">=desktop") {/*1200*/
            margin: 38px 0 0;
        }
    }

    .btn span {
        @include media(">=large-desktop") {/*1800*/
            padding-left: 30px;
            padding-right: 30px;
        }
    }
}


/*product-description end*/