
/*four-card-section*/
.four-card-section {
    position: relative;
    overflow: hidden;
    padding-top: $indent-section-mobile;

    @include media('>=desktop') { /*1200*/
        padding-top: $indent-section-desktop;
    }

    @include media('>=medium-desktop') { /*1440*/
        padding-top: $indent-section-medium-desktop;
    }

    @include media('>=large-desktop') { /*1800*/
        padding-top: $indent-section-large-desktop;
    }

    .container {
        position: relative;
        z-index: 2;
    }

    .section-info {
        max-width: 602px;
        margin-bottom: 46px;
        padding-top: 33px;

        @include media(">=desktop"){/*1200*/
            margin-bottom: 82px;
            padding-top: 146px;
        }

        @include media(">=medium-desktop"){/*1440*/
            padding-top: 105px;
            margin-bottom: 119px;
        }

        @include media(">=large-desktop"){/*1800*/
            padding-top: 6px;
            margin-bottom: 159px;
            max-width: 983px;
        }

        p {
            margin-left: auto;
            margin-right: auto;
            max-width: 722px;

            @include media('>=medium-desktop') {
                margin-bottom: 19px;
            }

            @include media('>=large-desktop') {
                margin-bottom: 17px;
                max-width: 654px;
            }
        }
    }

    .slick-list {
        padding: 0 0 0 20px;

        @include media(">=tablet"){/*768*/
            padding: 0;
        }
    }

    .slick-track {
        display: flex;
        align-items: flex-end;

        .four-card-item {
            padding: 0 10px 0 0;

            @include media(">=tablet"){/*768*/
                padding: 0 16px 0 0;
            }
        }
    }

    .slick-dots {
        padding: 40px 0;
        width: 100%;

        li {
            button {
                background: rgba($color-dark, .5);
            }

            &.slick-active {
                button {
                    background: $color-dark;
                }
            }
        }
    }

    .buttons-holder {
        margin-top: 22px;

        @include media('>=desktop') {
            margin-top: 11px;
        }

        @include media('>=large-desktop') {
            margin-top: 17px;
        }
    }

    &.animation {

        .four-card-bg {
            img {
                transform: translateY(-50px);
                transition: transform 1s ease;

                @include media('>=desktop') {
                    transform: translateY(-75px);
                }

                @include media('>=large-desktop') {
                    transform: translateY(-110px);
                }
            }
        }

        &.animated {

            .four-card-bg {
                img {
                    transform: translateY(0);
                }
            }
        }
    }
}

.four-card-info-holder {
    width: 100%;

    & + .icon-holder {
        flex-shrink: 0;
        margin-left: 15px;
    }

    h5 {
        @include media('<mobile-landscape') {
            line-height: 26px;
        }
    }
}

.four-card-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    line-height: 0;
    background-color: $color-light-gray;

    img, video {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.four-card-items {
    position: relative;
    z-index: 2;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;

    @include media(">=tablet"){/*1200*/
        padding: 0;
    }

    @include media(">=desktop"){/*1200*/
        margin: 0 -16px -32px;
    }


    &.animation {
        .four-card-item {
            @include media('>=desktop') {
                transition: transform .5s ease, opacity .5s linear;
                opacity: 0;
                transform: translateY(80px);
            }

            &.small {
                @include media('>=desktop') {
                    transition: transform .5s ease .15s, opacity .5s linear;
                }
            }
        }
    }

    &.animated {

        .four-card-item {
            @include media('>=desktop') {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }

    .slick-list {
        @include media('>=mobile-landscape') {
            min-width: 100%;
        }
    }
}

.four-card-item {
    @include media(">=desktop"){/*1200*/
        margin: 0 16px 32px;
        width: calc(25% - 32px);
    }

    .picture {
        position: relative;

        &::after {
            content: '';
            display: block;
            padding-top: 115%;

            @include media('>=desktop') {
                padding-top: 108.3%;
            }
        }
    }

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &.small {

        .picture {
            @include media('>=desktop') {
                &::after {
                    padding-top: 78.4%;
                }
            }

            @include media('>=medium-desktop') {
                &::after {
                    padding-top: 77%;
                }
            }

            @include media('>=large-desktop') {
                &::after {
                    padding-top: 78%;
                }
            }
        }
    }
}

.four-card-text {
    background-color: $color-white;
    padding: 25px 26px 21px 20px;
    display: flex;
    flex-direction: row;
    align-items: center;

    @include media('>=desktop') {
        padding: 20px;
        min-height: 96px;
    }

    @include media('>=medium-desktop') {
        padding: 27px 20px 21px;
    }

    @include media('>=large-desktop') {
        padding: 32px 33px 30px;
    }


    h5 {
        font-family: $ff-pp-fragment-sans;
        font-weight: 400;
        max-width: 160px;

        @include media('>=desktop') {
            max-width: 188px;
        }
    }
}

.icon-holder {
    height: 52px;
    width: 46px;
    margin-left: auto;
    display: flex;
    justify-content: center;
    align-items: center;

    @include media('>=medium-desktop') {
        width: 54px;
    }

    &:first-child {
        margin-left: 0;
    }

    svg, img {
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
    }
}

/*four-card-section end*/