
/*product-detail*/

.product-detail {
    overflow: hidden;

    @include media(">=tablet"){/*768*/
        display: flex;
    }

    .label-price {
        margin: 0 0 24px;
        font-weight: 400;
        font-size: 18px;
        line-height: 1.56;

        @include media('>=desktop') { /*1200*/
            font-size: 20px;
            line-height: 1.4;
        }

        @include media('>=large-desktop') { /*1800*/
            font-size: 24px;
            line-height: 1.33;
        }

        > span {
            padding: 0 0 0 2px;
            position: relative;
            top: -9px;
            font-size: 10px;
            line-height: 22px;
            color: rgba($color-dark, .4);
            
            @include media('>=desktop') { /*1200*/
                font-size: 12px;
            }         
        }
    }

    .grey-text {
        font-size: 10px;
        line-height: 22px;
        color: rgba($color-dark, .4);

        @include media(">=desktop"){/*768*/
            font-size: 12px;
        }
    }

    .woocommerce-product-details__short-description {
        border-bottom: 1px solid rgba($color-dark, .1);
        padding: 0 0 17px;

        p {
            margin: 0 0 13px;
        }
    }
}

.woocommerce {
    .product-detail-section {
        .product-detail.product {

            [data-modal-open="nutrition"] {

                &.button {
                    width: auto;
                    margin-left: auto;
                    margin-right: auto;
                    display: block;
                    height: auto;
                    min-height: auto;
                    margin-top: 30px;

                    span {
                        padding: 0;
                    }

                    &:hover {
                        background-color: rgba(0,0,0,0);
                        text-decoration: underline;
                        color: $color-dark;
                    }
                }
            }
        }
    }
}

.addition-choice {
    width: 100%;
    padding: 0 0 20px;

    @include media(">=mobile-landscape"){/*568*/
        width: 40%;
        padding: 0 20px 20px 0;
    }

    @include media(">=medium-desktop"){/*1440*/
        width: 43%;
    }
}

.addition-choice__title,
.additional-quantity__title,
.price-title {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    margin: 0 0 12px;
    color: rgba($color-dark, .4);

    @include media(">=desktop"){/*1200*/
        font-size: 18px;
        line-height: 28px;
    }
}

.product-gallery {
    margin: 0 0 40px;
    line-height: 0;

    @include media(">=tablet"){/*768*/
        width: 49%;
        margin: 0 auto 0 0;
    }
}

.product-gallery-img {

    .picture img {
        aspect-ratio: 7/4.945;
    }
}

.product-gallery-thumb {
    padding: 10px 12px 10px 0;
    background: $color-white;
    overflow: hidden;

    @include media(">=tablet"){/*768*/
        padding: 20px 22px 20px 0;
    }

    @include media(">=desktop"){/*1200*/
        padding: 30px 32px 30px 0;
    }

    .slick-list {
        overflow: visible;
    }

    .picture {
        padding-left: 12px;
        display: flex;
        position: relative;

        @include media(">=tablet"){/*768*/
            padding-left: 22px;
        }

        @include media(">=desktop"){/*1200*/
            padding-left: 32px;
        }

        span {
            position: relative;
            display: flex;
            
            &:before,
            &:after {
                position: absolute;
                top: -1px;
                left: -1px;
                bottom: -1px;
                right: -1px;
                border: 1px solid $color-white;
                content: "";
                opacity: 0;
                transition: opacity $transition-duration linear;
            }

            &:after {
                top: -2px;
                bottom: -2px;
                left: -2px;
                right: -2px;
                border-color: $color-green;
            }
        }

        

        img {
            margin: auto;
            aspect-ratio: 7/4.945;
        }
    }

    .slick-current {
        .picture {
            span {
                &:before,
                &:after {
                    opacity: 1;
                }
            }
        }
    }
}

.woocommerce .product-detail.product {
    .product_title {
        margin: 0 0 13px;
    }

    div.summary {
        position: relative;

        @include media(">=tablet"){/*768*/
            width: 44.5%;
            margin: 5px 0 0;
        }

        @include media(">=large-desktop"){/*1800*/
            width: 40.5%;
        }

        .icon-feature {
            left: auto;
            width: 200px;
            padding: 0;
            border-color: $color-dark;

            > span {
                border-color: $color-dark;


                @include media(">=tablet"){/*768*/
                    margin: -5px 10px 10px 0;
                }

                @include media(">=desktop"){/*1200*/
                    margin: -5px 0 10px 10px;
                }
            }
        }
    }

    .summary-title {
        position: relative;

        @include media(">=medium-desktop"){/*1440*/
            padding: 0 220px 0 0;
        }

        svg {
            path {
                fill: $color-dark;
            }

            &.stroke {
                path {
                    stroke: $color-dark;
                }
            }
        }

        .icon-feature {
            @include media("<medium-desktop"){
                position: relative;
                top: auto;
                right: auto;
                width: auto;
                margin: 0 0 10px;
            }
        }
    }

    .additional-quantity {
        width: 100%;
        padding: 0 0 20px;
        @include media(">=mobile-landscape"){/*768*/
            padding: 0 20px 20px 0;
            min-width: 120px;
            width: 30%;
        }
        @include media(">=medium-desktop"){/*1440*/
            padding: 0 12.5% 20px 0;
            width: auto;
        }
    }

    form.cart {
        padding: 34px 0 0;

        @include media(">=desktop"){/*1200*/
            padding: 50px 0 0;
        }

        input[type="radio"] ~ label {
            font-weight: 400;
        }

        .cart-holder {
            display: flex;
            flex-wrap: wrap;
            border-bottom: 1px solid rgba($color-dark, .1);
            margin: 0 0 48px;
        }

        .quantity {
            margin: -2px 0 0;
        }
    }

    .price {
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        margin: 0 0 4px;
        color: $color-dark;
        flex: auto;
        padding: 0 0 20px;

        @include media('>=mobile-landscape') {
            width: 30%;
        }

        @include media(">=desktop"){/*1200*/
            font-size: 18px;
            line-height: 28px;
        }
    }

    .price-title {
        display: block;
        color: rgba($color-dark, .4);
    }

    .price-row {
        display: flex;
        align-items: flex-start;
        flex-direction: row-reverse;
        justify-content: flex-end;

        .grey-text {
            text-decoration: line-through;
            font-weight: 400;
            position: relative;
            top: -5px;
            padding-left: 2px;
        }

        ins {
            text-decoration: none;

            bdi {
                font-weight: 400;
            }
        }

        del {
            text-decoration: none;

            bdi {
                font-size: 12px;
                line-height: 22px;
                font-weight: 400;
                position: relative;
                top: -4px;
                padding-left: 2px;
                text-decoration: line-through;
            }
        }
    }

    .price-old {
        position: relative;
        top: -9px;
    }

    .button {
        width: 100%;
        float: none !important;
        max-width: 100%;

        &.single_add_to_cart_button {
            &:hover {
                background-color: $color-green;
            }
        }
    }

    .price-discount {
        display: block;
        font-size: 12px;
        line-height: 22px;
        color: $color-dark;
    }
}

.addition-choice__wrapper {
    font-weight: 400;
    position: relative;
    padding: 0 0 0 30px;

    &:before {
        content: '';
        width: 20px;
        height: 20px;
        border: 1px solid rgba($color-dark, .4);
        outline: 2px dashed transparent;
        background: $color-dark;
        position: absolute;
        top: 2px;
        left: 0;
        border-radius: 50%;
    }

    &:after {
        content: '';
        position: absolute;
        transition: opacity $transition-duration linear;
        width: 9px;
        height: 6px;
        border: 1px solid $color-white;
        border-width: 0 0 1.5px 1.5px;
        transform: rotate(-45deg);
        top: 7px;
        left: 6px;
    }
}

/*product-detail end*/