/* scrollbars */
.jcf-scrollable-wrapper {
    box-sizing: content-box;
    position: relative;
}

.jcf-scrollbar-vertical {
    position: absolute;
    cursor: default;
    width: 8px;
    bottom: 0;
    right: 0;
    top: 0;
    border-radius: 2px;
}

.jcf-scrollbar-vertical .jcf-scrollbar-slider {
    width: 2px;
    margin: 0 auto;
    background: rgba(0, 58, 112, .1);
}

.jcf-scrollbar-vertical .jcf-scrollbar-dec,
.jcf-scrollbar-vertical .jcf-scrollbar-inc {
    height: 0;
    width: 14px;
    left: 0;
    top: 0;
}

.jcf-scrollbar-vertical .jcf-scrollbar-inc {
    top: auto;
    bottom: 0;
}

.jcf-scrollbar-vertical .jcf-scrollbar-handle {
    height: 41px;
    width: 4px;
    border-radius: 4px;
    background-color: $color-dark;
}

.jcf-scrollbar-horizontal {
    position: absolute;
    background: #e3e3e3;
    right: auto;
    top: auto;
    left: 0;
    bottom: 0;
    width: 1px;
    height: 14px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-dec,
.jcf-scrollbar-horizontal .jcf-scrollbar-inc {
    display: inline-block;
    vertical-align: top;
    overflow: hidden;
    background: #bbb;
    height: 14px;
    width: 14px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-inc {
    left: auto;
    right: 0;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-slider {
    display: inline-block;
    position: relative;
    height: 14px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-handle {
    position: absolute;
    background: #888;
    height: 14px;
}

.jcf-scrollbar.jcf-inactive .jcf-scrollbar-handle {
    visibility: hidden;
}

.jcf-scrollbar.jcf-inactive .jcf-scrollbar-dec,
.jcf-scrollbar.jcf-inactive .jcf-scrollbar-inc {
    background: #e3e3e3;
}

/* select */
.jcf-select {
    display: inline-block;
    vertical-align: top;
    position: relative;
    width: 100%;
    min-width: 150px;
    height: 54px;
    cursor: pointer;
    text-align: left;
    z-index: 20;
    max-width: inherit;
    max-width: 100%;
    font: 400 16px/24px $ff-pp-fragment-sans;

    @include media('>=medium-desktop') {
        font-size: 18px;
        line-height: 28px;
        height: 64px;
    }

    &.jcf-drop-active {
        .jcf-select-opener {
            &:before {
                transform: translateY(-50%);
            }
        }

        .jcf-select-text {
            border-color: $color-dark;
            background-color: $color-white;
            border-radius: 28px 28px 0 0;

            @include media('>=medium-desktop') {
                border-radius: 34px 34px 0 0;
            }
        }
    }

    &.jcf-drop-flipped {

        .jcf-select-text {
            border-radius: 0 0 28px 28px;

            @include media('>=medium-desktop') {
                border-radius: 0 0 34px 34px;
            }
        }
    }

    select {
        z-index: 1;
        left: 0;
        top: 0;
    }

    .jcf-option-hideme {
        height: 0;
        pointer-events: none;
        margin: 0;
        padding: 0 !important;
        visibility: hidden;
    }

    .jcf-select-text {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        display: flex;
        align-items: center;
        cursor: pointer;
        color: $color-dark;
        border: 1px solid rgba($color-dark, .4);
        padding: 12px 50px 12px 20px;
        border-radius: 35px;
        height: inherit;

        @include media('>=medium-desktop') {
            border-radius: 40px;
            padding-left: 29px;
        }

        .jcf-option-hideme {
            visibility: visible;
            height: auto;
            opacity: .4;
        }

        .no-touch & {
            transition: background-color .3s, border-color .3s;

            &:hover {
                border-color: $color-dark;
            }
        }

        & > span {
            display: block;
            text-overflow: ellipsis;
            overflow: hidden;
            padding-right: 20px;
        }
    }

    .jcf-select-opener {
        position: absolute;
        text-align: center;
        width: 50px;
        bottom: 0;
        right: 0;
        top: 0;

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 3px;
            width: 19px;
            height: 12px;
            background: url("#{$images-path}/select-arrow.svg") 50% 50% no-repeat;
            background-size: contain;
            transition: transform .3s;
            transform: translateY(-50%) rotate(180deg);
        }
    }

    .jcf-select-drop {
        position: absolute;
        top: 100%;
        border-radius: 4px;
        border-color: $color-dark;
        margin-top: 5px;
        padding-top: 9px;
        display: block;
    }

    .jcf-drop-flipped {
        bottom: calc(100% - 27px);
        top: auto;
        border-width: 1px 1px 0 1px;

        .jcf-list {
            padding: 0 0 27px;

            ul li {
                &:first-child {

                    .jcf-option {
                        border-radius: 10px 10px 0 0;
                    }
                }

                &:last-child {
                    .jcf-option {
                        border-radius: 0;
                    }
                }
            }
        }
    }
}

body > .jcf-select-drop {
    position: absolute;
    margin: -1px 0 0;
    // z-index: 9999;
    z-index: 100;
}

.jcf-select-drop {
    background: $color-white;
    border: 1px solid $color-dark;
    overflow: hidden;
    z-index: 0;
    border-radius: 0 0 28px 28px;

    @include media('>=medium-desktop') {
        border-bottom-left-radius: 34px;
        border-bottom-right-radius: 34px;
    }

    .jcf-select-drop-content {
        .jcf-scrollbar-horizontal {
            display: none;
        }
    }

    &.jcf-drop-flipped {
        border-radius: 28px 28px 0 0;
        border-width: 1px 1px 0 1px;
        margin: 1px 0 0;

        @include media('>=medium-desktop') {
            border-radius: 34px 34px 0 0;
        }
    }
}

/* select options styles */
.jcf-list {
    display: block;
    position: relative;
    border-radius: 4px;

    .jcf-list-content {
        vertical-align: top;
        display: inline-block;
        overflow: auto;
        width: 100%;
    }

    ul {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    li {
        overflow: hidden;
        display: block;
        padding-left: 0;
        margin-bottom: 0;

        &::before {
            display: none;
        }
    }

    .jcf-overflow {
        overflow: auto;
    }

    .jcf-option {
        overflow: hidden;
        display: block;
        color: rgba($color-dark, .4);
        cursor: pointer;
        font-size: 16px;
        line-height: 24px;
        padding: 10px 8px 10px 20px;
        transition: color .3s ease;

        @include media('>=medium-desktop') {
            font-size: 18px;
            line-height: 28px;
            padding-left: 29px;
        }

        &.jcf-hover,
        &.jcf-selected {
            color: $color-dark;
        }

        &.first-disabled {
            height: 0;
            opacity: 0;
            visibility: hidden;
            padding: 0;
            margin: 0;
            line-height: 0;
            font-size: 0;
        }

    }

    .jcf-disabled {
        display: none !important;
    }

    .jcf-optgroup-caption {
        display: block;
        cursor: default;
        padding: 0 17px;
        font-weight: bold;
        margin-bottom: 28px;
        text-transform: uppercase;

        @include media('>=desktop') {
            line-height: 20px;
        }
    }

    .jcf-optgroup {
        display: block;
        padding-bottom: 4px;

        .jcf-option {
            padding-left: 49px;
            position: relative;

            &:before {
                content: '';
                position: absolute;
                left: 17px;
                top: 50%;
                width: 20px;
                height: 20px;
                border-radius: 2px;
                border: 1px solid #050505;
            }
        }
    }
}
