.round-card {
    max-width: 430px;
    margin: 0 auto 30px;
    position: relative;

    @include media('>=mobile-landscape') {
        width: calc(50% - 15px);
        margin: 0 0 30px;
    }

    @include media('>=tablet') {
        width: calc(100% / 3 - 20px);
    }

    @include media('>=large-desktop') {
        max-width: 467px;
    }

    & > a {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 4;
        width: 100%;
        height: 100%;
    }
}

.round-card-info {
    text-align: center;

    @include media('>=desktop') {
        padding: 0 15px;
    }

    @include media('>=large-desktop') {
        padding: 0 35px;
    }

    h4, .h4 {
        margin-bottom: 13px;
    }
}

.round-card-media {
    position: relative;
    margin-bottom: 10px;

    @include media('>=desktop') {
        margin-bottom: 35px;
    }

    @include media('>=large-desktop') {
        margin-bottom: 50px;
    }

    &::before {
        content: '';
        display: block;
        padding-top: 100%;
    }

    &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: calc(100% - 37px);
        height: calc(100% - 37px);
        border-radius: 50%;
        background-color: $color-creame;
        z-index: 1;
        transform: translate3d(-50%, -50%, 1px);
        transition: transform .4s ease;
    }

    img {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        object-fit: contain;
        z-index: 2;
        transform: translate3d(-50%, -50%, 1px);
        transition: transform .4s ease;

        // &.has-shadow {
        //     height: 122%;
        //     width: 107%;
        //     max-width: none;
        // }
    }
}