// variables

// colors
$color-white: #fff;
$color-beige: #fff0e1;
$color-creame: #e9be92;
$color-creame-light: #FBE6D1;
$color-green: #72A434;
$color-dark: #151b20;
$color-light-gray:#f4f5f5;
$color-gray: #e1e8e8;
$color-dark-gray: #7e8284;
$color-red: #f00;
$color-error: #EF0C0C;
$color-light-blue: #DADDE2;
$color-light-blue-1: #c2cbd4;

$ff-pp-fragment-sans: 'PP_FragmentSans', sans-serif;
$ff-pp-fragment-serif: 'PP_FragmentSerif', sans-serif;

$transition-duration: .45s;

$indent-section-mobile: 80px;
$indent-section-desktop: 160px;
$indent-section-medium-desktop: 200px;
$indent-section-large-desktop: 300px;

// grid settings
$gl-colCount: 24;
$gl-gridName: grid;
$gl-colName: col;
$gl-attributeName: class;
$gl-gutter: 30px; // Total left + right
$gl-gutter-vertical: 0;
$gl-mq-width: 'min-width';
$gl-mq-list: (
	xs: 320px,
	sm: 768px,
	md: 1200px,
	lg: 1800px
);

// media settings
$breakpoints: (
	mobile-landscape: 568px,
	tablet: 768px,
	tablet-landscape: 1024px,
	desktop: 1200px,
	medium-desktop: 1440px,
	large-desktop: 1800px,
	ultra-desktop: 2400px
);

$images-path: '../images/';
