/*page-404*/

.error-page {
    position: relative;
    text-align: center;
    padding-bottom: 120px;

    @include media(">=large-desktop"){/*1800*/
        padding: 19px 0 0;
    }


    &:before {
        position: absolute;
        bottom: -80px;
        right: 0;
        content: "";
        background: url("../images/bg-404.jpg") no-repeat 50% 50% / cover;
        width: 294px;
        height: 178px;
        z-index: -1;

        @include media(">=desktop"){/*1200*/
            width: 463px;
            height: 344px;
            bottom: -139px;
        }

        @include media(">=medium-desktop"){/*1440*/
            bottom: -178px;
        }

        @include media(">=large-desktop"){/*1800*/
            bottom: -282px;
        }
    }

    .sub-title {
        text-transform: uppercase;
        margin: 0 0 12px;
        font-size: 14px;
        line-height: 22px;
        font-weight: 400;
        letter-spacing: 1px;
        color: rgba($color-dark, .4);

        @include media(">=desktop"){/*1200*/
            margin: 0 0 31px;
            font-size: 16px;
            line-height: 26px;
        }
    }

    h1, .h1 {
        margin: 0 0 28px;

        @include media(">=desktop"){/*1200*/
            margin: 0 auto 39px;
            max-width: 1214px;
        }
    }

    .btn span {
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 1px;

        @include media(">=large-desktop") {/*1800*/
            padding: 19px 42px;
        }
    }
}